<template>
  <div class="ins-modal-wrap" v-bind:class="{'show' : showModal}" v-on:openModal="openModalHandler">
    <ins-backdrop></ins-backdrop>
    <ins-card steady>
      <div class="ins-modal-header-wrap">
        <div class="grid-x">
          <div class="small-10 item">
            <h5 v-if="headerLabel">{{ this.headerLabel}}</h5>
          </div>
          <div class="small-2 item text-right">
            <button class="close-modal" data-close type="button">
              <span aria-hidden="true" v-on:click="closeModalHandler" v-show="!isDisabledCancel">&times;</span>
            </button>
          </div>
        </div>
      </div>
      <div class="ins-modal-body-wrap">
        <slot></slot>
      </div>
      <div class="ins-modal-button-wrap">
        <div class="grid-x">
          <div class="footer-btn-container large-12 medium-12 small-12 cell text-right">
            <ins-button
                label="CANCEL"
                v-on:onClickInsButton="closeModalHandler"
                v-bind:disabled="isDisabledCancel"
                class="small-order-1"
                outlined="true"
            ></ins-button>
            <ins-button
              class="small-order-2"
              v-if="!enableSubmit"
              v-bind:label="successButton"
              v-on:onClickInsButton="successButtonHandler"
              v-bind:color="successColor"
              solid
              v-bind:disabled="isDisabledSuccess"
              v-bind:loading="isLoadingSuccess"
            ></ins-button>
          </div>
        </div>
      </div>
    </ins-card>
  </div>
</template>

<script>
export default {
  name: "InsModal",
  props: {
    showModal: Boolean,
    enableSubmit: Boolean,
    headerLabel: String,
    successButton: String,
    successColor: String,
    isLoadingSuccess: Boolean,
    isDisabledSuccess: Boolean,
    isDisabledCancel: Boolean,
    data: {}
  },
  data() {
    return {
      state_showModal: this.showModal
    };
  },
  methods: {
    openModalHandler(event, data) {
      this.state_showModal = true;
      this.data = data;
    },
    closeModalHandler(event) {
      this.state_showModal = false;
      this.$emit("closeModal");
    },
    successButtonHandler(event) {
      this.$emit("successModal");
    }
  }
};
</script>

<style lang="scss" src="./InsModal.scss">
</style>
