<template>
  <div class="grid-container-full page-addnew" v-if="pageFormDataChecked && vclInitialized">
    <div class="grid-x grid-margin-x">
      <div class="large-12 medium-12 small-12 cell">

        <ins-card steady>
          <div class="page-addnew-top">
            <h2 class="h2desc">Page Details</h2>

            <span class="copy-template-btn-holder">
              <span v-bind:class="`status-wrap ${pageFormData.metadata.status}`">
                {{ pageFormData.metadata.status ? pageFormData.metadata.status : 'Draft' }}
              </span>
            </span>
          </div>

          <InsInputWrap label="Page Title*"
            placeholder="Please enter page title"
            required="true" maxlength="60"
            v-model="pageFormData.metadata.page_title"
            v-bind:readonly="pageFormType === 'View'"
            v-bind:has-error="errors.page_title ? true:false"
            v-bind:error-message="errors.page_title"
            v-bind:validate="true"            
            v-bind:disabled="pageFormData.metadata.status === 'archived' || disableForm"
            v-on:fieldError="fieldErrorHandler">
          </InsInputWrap>

          <div class="with-preview-wrap">
            <InsInputWrap label="Page URL*"
              placeholder="Please enter page url"
              required="true" maxlength="60"
              field="url" secondary="text"
              v-model="pageFormData.slug"
              v-bind:readonly="pageFormType === 'View'"
              v-bind:has-error="errors.slug ? true:false"
              v-bind:error-message="errors.slug"
              v-bind:validate="true"
              v-bind:disabled="pageFormData.metadata.status === 'archived' || disableForm"
              v-on:fieldError="fieldErrorHandler">
            </InsInputWrap>
            <a class="url-preview"
              v-bind:href="`${siteDomain}${pageFormData.slug}`" target="_blank">
              {{ siteDomain + pageFormData.slug }}
            </a>
          </div>

          <div class="with-preview-wrap">
            <InsInputWrap label="Physical File Path*"
              placeholder="Please enter physical file path"
              required="true" maxlength="60"
              field="text" secondary="text"
              v-model="tempFilePath"
              v-bind:readonly="pageFormType === 'View'"
              v-bind:has-error="errors.slug ? true:false"
              v-bind:error-message="errors.slug"
              v-bind:disabled="disableForm"
              v-bind:validate="true"
              v-on:fieldError="fieldErrorHandler">
            </InsInputWrap>
            <span class="path-preview" v-if="!disableForm">
              {{ 'modules/ins_site_manager/public/views/pages/' + tempFilePath + '.html.liquid' }}
            </span>
            <span class="path-preview"
              v-else>
              {{ pageFormData.physical_file_path }}
            </span>
          </div>

          <InsSelectWrap label="Template" v-model="pageFormData.layout"
            v-if="siteTemplateHasData"
            v-bind:searchable="siteTemplateOptions.length ? true:false"
            v-bind:disabled="!siteTemplateOptions.length && pageFormType !== 'View' || pageFormData.metadata.status === 'archived' || disableForm"
            v-bind:readonly="pageFormType === 'View'">

            <ins-select-option v-if="!siteTemplateOptions.length"
              label="No available templates" value="" default disabled
              placeholder="- Select -">
            </ins-select-option>

            <ins-select-option
              v-for="(option, key, index) in siteTemplateOptions"
              v-bind:key="index"
              v-bind:label="option.path.replace('layouts/','')"
              v-bind:value="option.path.replace('layouts/','')"
              v-model="pageFormData.layout">
            </ins-select-option>
          </InsSelectWrap>

          <!-- Add multiple attribute for multiple selections as auth policies can be more than 1 -->
          <InsSelectWrap label="Secure Zone"
            placeholder="- Select -"
            v-bind:disabled="pageFormType !== 'View' || disableForm"
            v-bind:readonly="pageFormType === 'View'">

            <ins-select-option
              label="Please subscribe to enable Secure Zone"
              value="" default disabled>
            </ins-select-option>
          </InsSelectWrap>

          <Redactor label="Content" ref="myRedactor"
            v-bind:source-code="pageFormData.content"
            v-bind:readonly="pageFormType === 'View' || pageFormData.metadata.status === 'archived' || disableForm"
            v-on:getRedactorData="getRedactorData" />
        </ins-card>
        <div class="spacer"></div>
      </div>

<!-- Published State -->
      <div class="large-4 medium-4 small-12 cell small-order-2 medium-order-1 footer-btn-container"
        v-if="pageFormData.metadata.status !== 'archived'">

        <ins-button label="ARCHIVE"
          color="information-1" size="normal"
          v-if="pageFormType === 'Edit'"
          v-on:onClickInsButton="archivePageHandler"
          v-bind:disabled="publishingPage"
          v-bind:loading="archivingPage">
        </ins-button>
      </div>

      <div class="large-8 medium-8 small-12 cell small-order-1 medium-order-2 text-right footer-btn-container"
        v-if="pageFormData.metadata.status !== 'archived'">

        <ins-button label="CANCEL" outlined
          v-bind:disabled="publishingPage || archivingPage"
          v-on:onClickInsButton="cancelManagePageHandler">
        </ins-button>

        <!-- <ins-button outlined
          label="COPY PAGE" icon="icon-content_copy"
          v-bind:disabled="publishingPage"
          v-if="pageFormType === 'Edit'"
          v-on:onClickInsButton="copyPageHandler">
        </ins-button> -->

        <ins-button label="PUBLISH" solid
          v-bind:options="this.vclMasterData.info.is_enabled ? 'Publish with commit message, Copy Page':'Copy Page'"
          v-if="pageFormType !== 'View'"
          v-bind:disabled="publishingPage || archivingPage"
          v-bind:loading="publishingPage"
          v-on:onClickInsButton="publishHandler"
          v-on:onClickInsButtonOption="runPrompt">
        </ins-button>
      </div>

<!-- Archived State -->
      <div class="large-6 medium-6 small-6 cell small-order-2 medium-order-1 footer-btn-container"
        v-if="pageFormData.metadata.status === 'archived'">

        <ins-button label="This page is archived"
          color="information-1" size="normal" disabled>
        </ins-button>
      </div>

      <div class="large-6 medium-6 small-6 cell small-order-2 text-right medium-order-1 footer-btn-container"
        v-if="pageFormData.metadata.status === 'archived'">

        <ins-button label="CANCEL" outlined
          v-on:onClickInsButton="cancelManagePageHandler">
        </ins-button>
      </div>

    </div>
  </div>
</template>

<script>
// general resources
import { mapState } from 'vuex';

import InsitesMixins from '../../mixins/InsitesMixins';
import VclMixins from '../../mixins/VclMixins';

import InsInputWrap from '../../components/InsInputWrap/InsInputWrap.vue';
import InsSelectWrap from '../../components/InsSelectWrap/InsSelectWrap.vue';
import InsTextareaWrap from '../../components/InsTextareaWrap/InsTextareaWrap.vue';
import Redactor from '../../components/Redactor/Redactor.vue';
import { 
  createVclFile,
  updateVclFile,
  renameVclFile
} from '../../services/VclService';

import { getPageByIDJSON, getAllTemplatesJSON } from '../../services/SiteManagerServices';

import { sidebarMenuSiteManagerRoute, listPagesRoute, managePageRoute } from "../../models/InsSiteManagerRoutes.js";

export default {
  name: "ManagePages",
  mixins: [InsitesMixins, VclMixins],
  components: {
    Redactor,
    InsInputWrap,
    InsSelectWrap,
    InsTextareaWrap
  },
  computed: mapState({
    pageFormType: state => state.PagesStore.pageFormType,
    pageFormData: state => state.PagesStore.pageFormData,
    siteTemplateOptions: state => state.PagesStore.siteTemplateOptions
  }),
  data() {
    return {
      rdefault: "default content",
      rContent: "",
      siteDomain: '',
      tempFilePath: '',
      tempWholePath: '',
      seoToggleLabel: 'This page is visible to search engines.',
      disableForm: false,
      publishingPage: false,
      archivingPage: false,
      pageFormDataChecked: false,
      siteTemplateHasData: false,
      formHasError: false,
      errors: {
        page_title: '',
        slug: ''
      },
      headers: {},
    }
  },
  methods: {
    addNewPageHandler(){
      InsitesUtil.renderPage('New Page', '#page-addnew');
      this.$router.push('/page-addnew');
    },
    archivePageHandler(){
      let copyMsg = 'Are you sure you want to archive this page?';
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="${copyMsg}">
          </ins-loader>`,
        type: "",
        showCancelButton: true,
        confirmButtonClass: "negative",
        confirmButtonText: "Archive"
      }).then(result => {
        if (result.value) {
          this.archivingPage = true;
          this.archivePage();
        }
      });
    },
    validateTemplateExist(){
      let url = `/insites/api/get-all-layouts-data.json?page=1&size=1000&path=${this.pageFormData.layout}`;
      let self = this;
      this.$http.get(url)
        .then(res => {
          if (res.data.admin_liquid_layouts.results.length <= 0) {
              self.pageFormData.layout = "";
          }
        });
    },
    archivePage(){
      let timestamp = + new Date();
      let pageFormDataCopy = JSON.parse(JSON.stringify(this.pageFormData));
      pageFormDataCopy.redirect_to = "404";
      pageFormDataCopy.slug = this.constructSlug(pageFormDataCopy.slug, 'archived', timestamp);
      pageFormDataCopy.physical_file_path = this.constructPath('archived', timestamp);
      pageFormDataCopy.metadata.search_engine_visibility = false;
      pageFormDataCopy.metadata.status = 'archived';

      let parsedData = JSON.stringify(pageFormDataCopy);
      this.submitForm(parsedData, 'archived');
    },
    copyPageHandler(){
      if (!this.validateForm()){
        let copyMsg = 'Are you sure you want to copy this page? This will copy the contents to a New Page.';
        swal({
          title: "",
          html: `
            <ins-loader
              state-icon="warning"
              icon-color="warning"
              state-message="${copyMsg}">
            </ins-loader>`,
          type: "",
          showCancelButton: true,
          confirmButtonText: "Copy"
        }).then(result => {
          if (result.value) {
            this.duplicatePage();
          }
        });
      }
    },
    duplicatePage(){
      this.pageFormData.content = this.$refs.myRedactor.getRedactorValue();
      this.pageFormData.slug = this.constructSlug(this.pageFormData.slug, 'copy');
      this.pageFormData.physical_file_path = this.constructPath('copy');
      this.pageFormData.metadata.status = '';
      this.pageFormData.metadata.page_title = `${this.pageFormData.metadata.page_title} Copy`;
      this.$store.commit('setPageFormType', 'New');

      let message = `Page has been copied.`;
      toastIt('success', message, InsitesUtil.ops());
    },
    constructSlug(slug, type, timestamp){
      let ext = slug.lastIndexOf('.');
      let suffix = type === 'archived' ? `${type}_${timestamp}` : type;

      if (slug.substring(ext) === '.html'){
        return `${slug.slice(0, ext)}_${suffix}${slug.slice(ext)}`;
      }
      return `${slug}_${suffix}`;
    },
    constructPath(type, timestamp){
      let path = 'modules/ins_site_manager/public/views/pages/';
      let suffix = type === 'archived' ? `${type}_${timestamp}` : type;
      return `${path}${this.tempFilePath}${type ? `_${suffix}` : '' }.html.liquid`;
    },
    constructPageContent(file){
      let fileCopy = JSON.parse(JSON.stringify(file));
      let arr = ['---'];
      arr.push(`slug: ${fileCopy.slug}`);
      arr.push(`layout_name: ${fileCopy.layout}`);
      arr.push(`searchable: ${fileCopy.searchable}`);

      arr.push(`metadata: {`);
      for (var key in fileCopy.metadata) {
        arr.push(`    ${key}: "${fileCopy.metadata[key]}",`);
      }
      arr.push(`}`);

      arr.push(`--- `);
      arr.push('');

      let contentArr = fileCopy.content.split('\n');
      fileCopy.content = arr.concat(contentArr).join('\n')
      return fileCopy;
    },
    fieldErrorHandler(event){

      let label = event.target.$el.label;
      let lastIndex = label.length - 1;
      let lastLetter = label[lastIndex];

      if (lastLetter === "*"){
        label = label.substring(0, lastIndex);
      }

      event.target.$el.hasError = event.error;
      event.target.$el.errorMessage = (`${event.target.$el.label} is required.`).replace('*','');
      // event.target.$el.errorMessage = `${label} is required.`;
    },
    checkWhiteSpace(event){
      this.pageFormData.slug = InsitesUtil.checkURLinput(event)
    },
    publishHandler(event){
        this.publishingPage = true;
        if (!this.validateForm()){
          this.pageFormData.content = this.$refs.myRedactor.getRedactorValue();
          this.pageFormData.physical_file_path = this.constructPath();
          let parsedData = JSON.stringify(this.pageFormData);
          let word = this.pageFormType === 'Edit' ? 'updated' : 'added';
          this.submitForm(parsedData, word);
        } else {
          this.publishingPage = false;
        }
    },
    submitForm(parsedData, word){
      let pageData = { payload: this.checkData(parsedData) }
      if (this.vclMasterData.info.is_enabled){
        this.runVcl(pageData, word);
      } else {
        this.runMutation(pageData, word);
      }
    },
    async runVcl(pageData, word){
      let vclFile = this.constructPageContent(pageData.payload);
      let vclPayload = this.constructVclPayload(vclFile);
      let vclRes;
      
      if (word === "archived") {
        vclPayload.git.rename_file = this.tempWholePath;
        vclRes = await renameVclFile(vclPayload);

      } else if (this.pageFormType === "Edit"){

        if (this.tempWholePath !== vclPayload.file.physical_file_path){
          vclPayload.git.rename_file = this.tempWholePath;
        }

        vclRes = await updateVclFile(vclPayload);
      } else {
        vclRes = await createVclFile(vclPayload);
      }

      if (vclRes.state){

        if (vclRes.results.message.includes("File already exist")) {
          this.overwriteHandler(pageData, word);
          return;
        } else if (vclRes.results.message.includes("Successfully")){
          this.runMutation(pageData, word);
          return;
        }
      }
      this.errorHandler(vclRes.results.response.data.message);
    },
    async overwriteHandler(pageData, word){
      let prompt = await this.vclPrompt(
        "File already exist. would you like to overwrite?",
        "Overwrite");
        
      if (prompt.value){
        this.gitOverwrite = true;
        this.runVcl(pageData, word);
      } else {
        this.publishingPage = false;
        this.resetVclData();
      }
    },
    async runPrompt(){
      let prompt = await this.vclPromptCommitMessage();
      if (prompt.value){
        this.gitMessage = prompt.value;
        this.publishHandler();
      }
    },
    runMutation(pageData, word){
      let type = this.pageFormType === 'New' ? 'create':'update';
      let url = `/insites/api/${type}-page.json`

      this.$http.post(url, pageData, {headers: this.headers})
        .then(res => this.resHandler(res, word, pageData.payload))
        .catch(this.errorHandler);
    },
    resHandler(res, word, pageData){
      if (res.data.includes) {
        if (res.data.includes('500 - Server Error') ||
            res.data.includes('parse_json failed')){
          this.errorHandler('500 POS Server Error');
        }
      } else {

        let message = `Page has been ${word} and published.`
        if (word === 'archived'){
          InsitesUtil.goBack();
          message = `Page has been ${word}.`;
        } else if (word === 'added') {
          this.pageFormData.id = res.data.admin_page_create.id;
          this.pageFormData.metadata.status = 'published';
          this.$store.commit('setPageFormType', 'Edit');
        }
        this.tempWholePath = this.pageFormData.physical_file_path;
        toastIt('success', message, InsitesUtil.ops());
      }
      this.resetVclData();
      this.archivingPage = false;
      this.publishingPage = false;
    },
    errorHandler(error){
      console.error(error);
      if (this.pageFormType === "New"){
        this.pageFormData.metadata.status = "";
      }

      this.resetVclData();
      this.archivingPage = false;
      this.publishingPage = false;
      toastIt("error", error, InsitesUtil.ops());
    },
    checkData(parsedData){
      let data = JSON.parse(parsedData);
      data.content = data.content.replace(/&nbsp;/g, ' ');

      if (data.metadata.status !== "archived"){
        data.metadata.status = "published";
      }

      return data;
    },
    validateForm(){
      let errorsCount = 0;

      if (this.pageFormData.metadata.page_title === ''){
        this.errors.page_title = 'Page Title is required.';
        errorsCount++;
      } else {
        this.errors.page_title = '';
      }

      if (this.pageFormData.slug === ''){
        this.errors.slug = 'Page URL is required.';
        errorsCount++
      } else {
        this.errors.slug = '';
      }

      return errorsCount;
    },
    cancelManagePageHandler(){
      InsitesUtil.goBack();
    },
    getRedactorData(code) {
      this.pageFormData.content = code;
    },
    getSiteDomain(){
      let protocol = window.location.protocol;
      let hostname = window.location.hostname;
      this.siteDomain = `${protocol}//${hostname}/`;
    },
    seoToggleHandler(event){
      this.pageFormData.metadata.search_engine_visibility = event.detail.checked;
      let state = event.detail.checked ? 'visible to' : 'hidden from';
      this.seoToggleLabel = `This page is ${state} search engines.`;
    },
    checkMetadata(){
      if (!this.pageFormData.metadata) {
        this.pageFormData.metadata = {};
      }
      if (this.pageFormData.metadata.page_title === undefined) {
        this.pageFormData.metadata.page_title = ''
      }
      if (this.pageFormData.metadata.meta_title === undefined) {
        this.pageFormData.metadata.meta_title = ''
      }
      if (this.pageFormData.metadata.description === undefined) {
        this.pageFormData.metadata.description = ''
      }
      if (this.pageFormData.metadata.canonical_url === undefined) {
        this.pageFormData.metadata.canonical_url = ''
      }
      if (this.pageFormData.metadata.status === undefined) {
        this.pageFormData.metadata.status = 'published'
      }
      if (this.pageFormData.metadata.search_engine_visibility === undefined) {
        this.pageFormData.metadata.search_engine_visibility = true;
      } else if (!this.pageFormData.metadata.search_engine_visibility) {
        this.seoToggleLabel = 'This page is hidden from search engines.'
      }

      this.pageFormDataChecked = true;
    },
    errorPages() {
      let self = this;
      setTimeout(() => {
        self.$router.push({ name: 'ListPages' });
      }, 1300);
      self.toastInvalidIDNotification('page');
    },
    deconstructPath(path){
      let noModule = path.replace('modules/ins_site_manager/public/views/pages/', '');
      let noExt = noModule.replace('.html.liquid','');
      return noExt;
    },
    setEditPageFormData(data) {
      let result = {
        data: {
          rawData: data
        },
        action: "Edit"
      }
      
      this.tempFilePath = this.deconstructPath(data.physical_file_path);
      this.$store.commit('setPageFormData', result);
    },
    setBreadCrumbs() {
      let crumbs = [];
      crumbs = [sidebarMenuSiteManagerRoute, listPagesRoute];

      if (this.$route.name === "EditPage") {
        crumbs.push({
          link: `#/edit-page/${this.checkURLParamsID()}`,
          app: false,
          withSubmenu: false,
          label: 'Edit Page'
        });
      } else if (this.$route.name === "ManagePage") {
        crumbs.push(managePageRoute);
      }
      InsitesUtil.setBreadCrumbs(crumbs);
    },
    async editPage() {
      let self = this;
      let response = await getPageByIDJSON(self.checkURLParamsID());
      
      return new Promise(resolve => {
        if (response.results.admin_pages.total_entries) {
          self.setEditPageFormData(response.results.admin_pages.results[0]);
          resolve({ state: true });
        } else {
          resolve({ state: false });
        }
      });
    },
    async setSiteTemplates() {
      let self = this;
      let response = await getAllTemplatesJSON();

      if (response.state) {
        return new Promise(resolve => {
          self.$store.commit("setSiteTemplatesOptions", response.results.admin_liquid_layouts.results);
          self.validateTemplateExist();
          resolve();
        });
      }
    }
  },
  async mounted() {
    let self = this;
    let response = {};

    self.siteTemplateHasData = false;
    await self.setSiteTemplates();
    self.siteTemplateHasData = true;

    if (self.$route.name === "EditPage") {
      if (self.pageFormData.metadata) {
        response.state = true;
      } else if (self.checkURLParamsID()) {
        response = await self.editPage();
      }
      if (self.pageFormData.physical_file_path.indexOf("modules/") !== 0) {
        this.disableForm = true;
      }
    } else if (self.$route.name === "ManagePage") {
      this.$store.commit("setPageFormData", { data: {}, action: "New" });
      response.state = true;
    }
  
    if (response.state) {
      self.tempWholePath = self.pageFormData.physical_file_path;
      self.tempFilePath = self.deconstructPath(self.pageFormData.physical_file_path);
      self.getSiteDomain();
      self.checkMetadata();
      self.setBreadCrumbs();
      self.headers = self.apiHeader();
    } else {
      self.errorPages();
    }
  }
}
</script>

<style lang="scss" src="./ManagePages.scss">
</style>
