<template>
  <div>
    <ins-base-table
    :id="prTableId"
    heading="tableHeading1"
    searchbar-placeholder="ins_table_searchbar_placeholder">
  </ins-base-table>
</div>
</template>
<script>
import InsitesMixins from '../../mixins/InsitesMixins';

export default {
  name: "TableComponent",
  mixins: [InsitesMixins],
  props: {
    prTableId: "",
    prTableData: "",
  },
  data(){
    return {
      // tableDataSrc: ""
    }
  },
  mounted() {
    this.initializeTable();
  },
  methods: {
    initializeTable() {
      var tableDataSrc = this.prTableData;
      var insBaseTable = document.getElementById(this.prTableId);
        var rawData = [];
        var filteredData = [];
        var displayedData = [];
        var tableHeaders = [];
        var totalCount = 0;
        var pageNumber = 1;
        var pageSize = 10;
        var searchKeyword = '';
        var sortedKeyword = '';

        insBaseTable.loadingScreen = true;
        insBaseTable.bulkActions = ['Archive'];
        insBaseTable.rowActions = ['Archive'];

        insBaseTable.addEventListener('onPaginationChange', (event) => {
          tablePaginationHandler(event)
        });

        insBaseTable.addEventListener('onTableSearch', (event) => {
          tableSearchHandler(event)
        });

        insBaseTable.addEventListener('onTableSort', (event) => {
          tableSortHandler(event)
        });

        insBaseTable.addEventListener('onTableBulkAction', (event) => {
          tableBulkActionHandler(event)
        });

        insBaseTable.addEventListener('onTableRowAction', (event) => {
          tableRowActionHandler(event)
        });

        function tablePaginationHandler(event){
          insBaseTable.loadingScreen = true;
          pageSize = event.detail.pageSize;
          pageNumber = event.detail.pageNumber;
          processTableData()
        }

        function tableSearchHandler(event){
          insBaseTable.loadingScreen = true;
          searchKeyword = event.detail.value;

          if (!searchKeyword) {
            processTableData();
            return;
          }

          filteredData = [];
          rawData.forEach((value) => {
            Object.keys(value).forEach((key) => {
              if (key === 'Default Column'){
                var match = value[key];
                if (match.toLowerCase().includes(searchKeyword.toLowerCase())) {
                  filteredData.push(value);
                }
              }
            });
          });

          if (sortedKeyword && filteredData.length > 1) {
            filteredData.sort(dynamicKeySort(sortedKeyword));
          }
          insBaseTable.pageNumber = 1;
          processTableData();

          if (!filteredData.length) {
            insBaseTable.loadingScreen = true;
            insBaseTable.loaderTitle = '';
            insBaseTable.loaderMessage = 'No result found for "' + searchKeyword + '"';
            insBaseTable.loaderIcon = '';
          }
        }

        function tableSortHandler(event){
          sortedKeyword = event.detail.order === 'asc' ? event.detail.keyword : `-${event.detail.keyword}`;
          if (searchKeyword){
            filteredData.sort(dynamicKeySort(sortedKeyword));
          } else {
            rawData.sort(dynamicKeySort(sortedKeyword));
          }
          processTableData();
        }

        function tableBulkActionHandler(event){
          if (event.detail.action === 'Archive'){
            archiveSelection(event.detail.selections);
          }
        }

        function tableRowActionHandler(event){
          if (event.detail.action === 'Archive'){
            archiveItem(event.detail.data);
          }
        }

        // process table data
        function processTableData(){
          var last_index = pageNumber * pageSize;
          var first_index = (last_index - pageSize) + 1;

          displayedData = [];

          if (searchKeyword) {

            filteredData.forEach((value, index) => {
              if (index >= first_index && index <= last_index) {
                displayedData.push(value);
              } else if (filteredData.length < pageSize){
                displayedData.push(value);
              }
            });
            insBaseTable.totalCount = filteredData.length;

          } else {

            rawData.forEach((value, index) => {
              if (index >= first_index && index <= last_index) {
                displayedData.push(value);
              }
            });
            insBaseTable.totalCount = rawData.length;

          }

          insBaseTable.tableData = displayedData;
          insBaseTable.loadingScreen = false;
        }
        // end process table data

        // fetch data
        function fetchDataFromSrc () {

          insBaseTable.loadingScreen = true;

          insBaseTable.loaderTitle = "Processing Your Data";
          insBaseTable.loaderMessage = "Just a moment, we're fetching and processing your data.";
          insBaseTable.loaderIcon = "processing";

          var xhttp = new XMLHttpRequest();
          xhttp.onreadystatechange = function () {
            if (this.readyState == 4 && this.status == 200) {

              var tableData = JSON.parse(this.responseText);
              rawData = tableData.list.map(function(data){
                data["Default Column_Img"] = '';
                return data;
              });
              insBaseTable.totalCount = tableData.count;
              insBaseTable.tableHeaders = [
{ label: "Default Column", type: 'string', sortable: true, hasLink: true, hasImage: true}, //
{ label: "Date Column", type: 'string', sortable: true, hasColumnAction: true },
{ label: "Number Column", type: 'number', sortable: true },
{ label: "Currency Column", type: 'currency', sortable: true }
];

processTableData();
}
};
xhttp.open("GET", tableDataSrc, true);
xhttp.send();
}
        // end fetch data

        // sort
        function dynamicKeySort(key) {
          var OrderSortIndex = 1;

          if (key[0] === "-") {
            OrderSortIndex = -1;
            key = key.substr(1);
          }

          return function (a, b) {
            var num = (a[key] < b[key]) ? -1 : (a[key] > b[key]) ? 1 : 0;
            return num * OrderSortIndex;
          }
        }
        // end sort

        fetchDataFromSrc();
    },
  }
};
</script>

