import moment from "moment";
import InsitesMixins from "./InsitesMixins";

export default {
    name: "SelectedFilesManager",
    mixins: [InsitesMixins],
    methods: {
        getFileType(fileName) {
            if(fileName){
                const img = ['png','jpg', 'jpeg', 'gif', 'svg', 'tiff', 'bmp'];
                let fileExtension  = fileName.split('.').slice(-1)[0].toLowerCase();
                if(fileExtension.length === 1) {
                    return 'folder'
                } else {
                return img.indexOf(fileExtension) >= 0
                    ? 'image'
                    : 'notepad';
                }
            } else {
                return false;
            }
        },
        downloadOrPreview(fileName) {
            if(fileName){
                const img = ['png','jpg', 'jpeg', 'gif', 'svg', 'tiff', 'bmp', 'js', 'css', 'scss', 'sass', 'liquid', 'json', 'html', 'docs', 'doc', 'pdf', 'map'];
                let fileExtension  = fileName.split('.').slice(-1)[0].toLowerCase();
                return img.indexOf(fileExtension) >= 0
                    ? 'Preview'
                    : 'Download';
            } else {
                return 'Download';
            }
        },
        transformMKPSelectedFilesResponse(collections, selected_folder) {
            let max_length = selected_folder.split('/').length + 1,
                files = [];

            for (let index in collections) {
                if (!selected_folder) {
                    if (index.split('/').length === 1 && index.indexOf(selected_folder) === 0 && collections[index]) {
                        files.push(this.setFileProperties(selected_folder, collections, index, 1));
                    }
                } else if (index.split('/').length === max_length && index.indexOf(selected_folder) === 0 && collections[index]) {
                    files.push(this.setFileProperties(selected_folder, collections, index, max_length));
                }
            }

            return files;
        },
        setFileProperties(physical_path_folder, collections, index, max_length) {
            let selected_folder = physical_path_folder ? `${this.root_folder}/${physical_path_folder}` : `${this.root_folder}`;

            return {
                file_name: index.split('/')[max_length - 1],
                folder: this.convertToFolder(selected_folder),
                direct_link: collections[index],
                asset_url_date: collections[index].split("?updated=")[1],
                physical_path: this.convertToPhysicalPath(`${selected_folder}/${index.split('/')[max_length - 1]}`),
                date_added: InsitesUtil.formatLocalisation(moment.unix(collections[index].split("?updated=")[1]).toISOString())
            }
        },
        transformLocalSelectedFilesToResult(collections, selected_folder) {
            // for localhost only
            let local_files = {
                "data": {
                    "items": {
                        "total_entries": Number,
                        "total_pages": 0,
                        "current_page": 0,
                        "per_page": 1000,
                        "page": 0,
                        "results": []
                    }
                }
            };

            selected_folder = this.convertToFolder(selected_folder);
            // selected_folder = this.convertToPhysicalPath(selected_folder)

            for (let index in collections) {
                if (collections[index].folder === selected_folder) {
                    local_files.data.items.results.push(collections[index]);
                }
            }

            local_files.data.items.total_entries = local_files.data.items.results.length;
            local_files.data.items.total_pages = !local_files.data.items.results.length ? 0 : 1;
            local_files.data.items.current_page = !local_files.data.items.results.length ? 0 : 1;
            local_files.data.items.page = !local_files.data.items.results.length ? 0 : 1;
            return local_files;
        }
    }
}
