<template>
  <div class="grid-x grid-padding-x gss-hours-field-wrap">

    <div class="cell large-4 medium-4 small-12">
      <ins-toggle-switch
        v-bind:label="label"
        v-bind:checked="value[`${this.liquidTag}_${this.keyDay}_enable`]"
        v-on:onCheckInsToggleSwitch="toggleHandler">
      </ins-toggle-switch>
    </div>

    <div class="cell small-12 show-for-small-only">
      <div class="header">Open Time</div>
    </div>

    <div class="cell large-4 medium-4 small-12">
      <GssSelectField
        v-bind:options="timeOptions"
        v-bind:liquid-tag="`${liquidTag}_${keyDay}_open`"
        v-bind:disabled="disabled"
        v-bind:full="true"
        v-model="value[`${liquidTag}_${keyDay}_open`]" />
    </div>

    <div class="cell small-12 show-for-small-only">
      <div class="header">Close Time</div>
    </div>

    <div class="cell large-4 medium-4 small-12">
      <GssSelectField
        v-bind:options="timeOptions"
        v-bind:liquid-tag="`${liquidTag}_${keyDay}_close`"
        v-bind:disabled="disabled"
        v-bind:full="true"
        v-model="value[`${liquidTag}_${keyDay}_close`]" />
    </div>
  </div>
</template>

<script>
import GssSelectField from '../GssSelectField/GssSelectField.vue';
import timeOptions from '../../../models/timeOptions'

export default {
  name: "GssInputField",
  props: {
    label: String,
    liquidTag: String,
    value: Object,
    keyDay: String
  },
  components: {
    GssSelectField,
  },
  data() {
    return {
      timeOptions: timeOptions,
      disabled: true,
    }
  },
  methods: {
    toggleHandler(e){
      this.disabled = !e.detail.checked;
      this.value[`${this.liquidTag}_${this.keyDay}_enable`] = e.detail.checked;
    }
  },
  created() {
    let checked = this.value[`${this.liquidTag}_${this.keyDay}_enable`];
    this.value[`${this.liquidTag}_${this.keyDay}_enable`] = checked || checked === 'true' ? true : false;
    this.disabled = !this.value[`${this.liquidTag}_${this.keyDay}_enable`];
  },
}
</script>

<style lang="scss" src="./GssHoursField.scss">
</style>