// general resources
import { keys, filter } from 'lodash';
import InsitesMixins from './InsitesMixins';

// file manager module resources
import SelectedFilesManager from './SelectedFilesManager';

export default {
  name: "SelectedFolderManager",
  data() {
      return {
          root_folder: 'assets'
      }
  },
  mixins: [SelectedFilesManager, InsitesMixins],
  methods: {
     transformMKPAssetFolderResponse(collections, parent_folder) {
        let self = this,
            valid_asset_folders = [];
        parent_folder = parent_folder && parent_folder.split('/').length === 1 ? '' : parent_folder;
        
        (keys(collections).sort()).forEach(collection => {
            let folders = collection.replace(parent_folder ? parent_folder : '', '').split('/');

            if (folders.length > 1) {
                let asset_folder = self.transformParentFoldersToDataStructure(folders, parent_folder);
                if (asset_folder) {
                    valid_asset_folders.push(asset_folder);
                }
            }
        });
        return self.transformDataStructure(valid_asset_folders);
    },
    transformParentFoldersToDataStructure(folders, temp_parent_folder) {
        let self = this,
            parent_folder = [],
            asset_folder = { name: String, has_children: folders.length > 2 };

        if (typeof temp_parent_folder !== 'undefined') {
            for (let folder in folders) {
                if (parseInt(folder) < temp_parent_folder.split('/').length) {
                    parent_folder.push(folders[folder]);
                }
            }
            asset_folder.name = parent_folder.join('/');
            if (folders.length > parent_folder.length) {
                asset_folder.has_children = folders.length > (1 + temp_parent_folder.split('/').length);
                return asset_folder;
            }
        } else {
            asset_folder.name = folders[0];
            return asset_folder;
        }
    },
    // updateMKPChildrenOfSelectedAssetFolder(selected_collection, collections, raw_data) {
    //     // collections.selected_folder = selected_collection.details.id;
    //     selected_collection.children.push(this.updateMKPSelectedAssetFolder(selected_collection, collections, raw_data));
    // },
    initializeUniqueDataStructure(collections) {
        let self = this,
            unique_collections = [];

        for (var counter = 0; counter < collections.length; counter++) {
            if (filter(unique_collections, function(data) { return data.name === collections[counter].name}).length) {
                if (collections[counter].has_children) {
                    unique_collections = self.processUniqueDataStructure(collections[counter], unique_collections);
                }
            } else {
                unique_collections.push(collections[counter]);
            }
        }
        return unique_collections;
    },
    processUniqueDataStructure(collection, unique_collections) {
        for (var counter = 0; counter < unique_collections.length; counter++) {
          if (unique_collections[counter].name === collection.name) {
            unique_collections[counter].has_children = collection.has_children;
          }
        }
        return unique_collections;
    },
    transformDataStructure(collections) {
        let temp_data_structure = this.initialDataStructureSetup();

        this.initializeUniqueDataStructure(collections).forEach((result, item) => {
            let child_path = result.name;

            temp_data_structure.children.push({
                details: {
                    id: `${this.root_folder}/${result.name}`,
                    is_open: false,
                    is_loading: false,
                    counter: item + 1,
                    has_children: result.has_children,
                    total_entries: Number,
                    file_name: child_path.split('/')[child_path.split('/').length - 1],
                    file_type: 'folder',
                    file_content: '',
                    actions: {},
                    meta_data: result
                },
                children: []
            });
        });
        return temp_data_structure;
    },
    updateMKPSelectedAssetFolder(selected_collection, collections, raw_data) {
        selected_collection.details.is_open = selected_collection.details.is_open ? false : true;
        let children_of_selected_collection = this.getChildenOfSelectedAssetFolderFromRawData(selected_collection, raw_data);

        selected_collection.children = [];
        for (let child in children_of_selected_collection.children) {
            selected_collection.children.push(children_of_selected_collection.children[child]);
        }

        return selected_collection.children;
    },
    getChildenOfSelectedAssetFolderFromRawData(selected_collection, raw_data) {
        let collections = [];
        for (let item in raw_data) {
            if (raw_data[item].indexOf(selected_collection.details.id) !== -1) {
                collections[item] = raw_data[item];
            }

            // blank folders
            if (!raw_data[item]) {
                let index = this.convertToPhysicalPath(selected_collection.details.id);

                if (item.indexOf(index) === 0) {
                    collections[item] = "";
                }
            }
        }

        return this.transformMKPAssetFolderResponse(collections, selected_collection.details.id);
    },
    initialDataStructureSetup() {
        let temp_data_structure = {
            details: {
                id: this.root_folder,
                is_open: true,
                is_loading: false,
                counter: 1,
                has_children: true,
                file_name: this.root_folder,
                file_type: 'folder',
                file_content: '',
                actions: {},
                meta_data: {}
            },
            children: []
        };
        return temp_data_structure;
    },
    setFolders(collections) {
        let values = [];
        for (let item in collections) {
            item = item.split('/');
            item.pop();
            item = `${this.root_folder}/${item.join('/')}`;
            if (!values.includes(item)) {
                values.push(item);
            }
        }
        return values.sort();
    }

  }
}
