<template>
  <div class="grid-x grid-margin-x">    
    <div class="cell large-7 medium-12 small-12">
      <InsInputWrap
        v-bind:label="label"
        v-bind:value="value"
        v-bind:field="field"
        v-on:input="onValueChange"
        v-bind:has-error="hasError"
        v-bind:readonly="readonly"
        v-bind:errorMessage="errorMessage"
        v-bind:required="required" />
    </div>

    <div class="cell large-5 medium-12 small-12">
      <label class="liquid-label">Liquid Tag</label>
      <pre class="liquid-wrap"><span v-pre>{%- include '</span>{{ liquidTag ? liquidTag : setModuleName(`modules/ins_site_manager/${label}`) }}<span v-pre>' -%}</span></pre>
    </div>

  </div>
</template>

<script>
  // general resources
  import InsInputWrap from '../../InsInputWrap/InsInputWrap.vue';
  import Validators from '../../../mixins/Validators';
  // file manager resources
  import SiteManagerService from '../../../mixins/SiteManagerService';

  export default {
    name: "FMInputWrap",
    props: {
      label: String,
      liquidTag: {
          default: "modules/ins_site_manager/component_name",
          type: String
      },
      value: String,
      field: String,
      required: String,
      readonly: Boolean,
      errorMessage: String
    },
    mixins: [Validators, SiteManagerService],
    components: {
      InsInputWrap,
    },
    data() {
      return {
        hasError: false
      }
    },
    methods: {
      onValueChange(value) {
        this.$emit("input", value);
        this.checkInput(value);
      },
      checkInput(value){
        if (this.required && !value) {
          return this.toggleError(true, `${this.label} is required.`);
        } else if (value) {

          let error;
          let message;

          switch (this.field) {
            case 'email':
              error = this.validateEmail(value);
              message = error ? 'Invalid email address.' : '';
              break;

            case 'url':
              error = this.validateURL(value);
              message = error ? 'Invalid url.' : '';
              break;
          }

          this.toggleError(error, message);

        } else {
          this.toggleError(false, '');
        }
      },
      toggleError(error){
        this.hasError = error ? true : false;
        // this.errorMessage = message;
        this.$emit("fieldError", this.hasError);
      }
    }
  }
</script>

<style lang="scss" src="./FMInputWrap.scss">
</style>