<template>
  <div class="grid-container-full sm-content-block">
    <div class="grid-x">
      <div class="large-12 cell content-blocks-container">
      <!-- no data -->
      <div v-if="!hasData">
        <ins-card steady>
          <h2 class="title-page">Welcome to Content Blocks</h2>
          <p>Content Blocks help you save time by allowing you to re-use common snippets of code or content across multiple pages. One content blocks can be inserted on multiple pages and any change will be reflected across all pages when you make them.</p>
        </ins-card>

        <div class="pages-bottom">
          <h2 class="pages-title-secondary">You don't have any content blocks yet.</h2>
          <p>Would you like to create your first content block?</p>
          <ins-button size="normal"
            label="ADD CONTENT BLOCK"
            solid color="blue"
            v-on:onClickInsButton="addContentBlockHandler">
          </ins-button>
        </div>
      </div>

      <!-- /no data -->
      <!-- has data -->

      <div class="ins-table-container" v-show="hasData">
      <!-- page view filter -->
      <!-- <div class="page-view-filter">
          <span class="filter-label">Filter:</span>
          <ins-select label="Status" small button>
            <ins-select-option label="Enabled" value="Enabled" default></ins-select-option>
            <ins-select-option label="Disabled" value="Disabled"></ins-select-option>
          </ins-select>
          <ins-select label="Last Modified" small button>
            <ins-select-option label="This Week" value="This Week" default></ins-select-option>
            <ins-select-option label="Date2" value="Date2"></ins-select-option>
          </ins-select>
        </div>  -->

        <!-- /end page view filter -->
      <div class="header-buttons">
        <ins-button id="addContentBlockBtn"
          label="ADD CONTENT BLOCK" solid color="primary" size="normal"
          v-on:onClickInsButton="addContentBlockHandler">
        </ins-button>
      </div>

        <!-- table -->
        <div class="ins-table-container">
          <ins-base-table
            id="insBaseTable"
            ref="insBaseTable"
            searchbar-placeholder="Search by Name..."
            v-on:onPaginationChange="tablePaginationHandler"
            v-on:onTableSearch="tableSearchHandler"
            v-on:onTableSort="tableSortHandler"
            v-on:onTableBulkAction="tableBulkActionHandler"
            v-on:onTableRowAction="tableRowActionHandler">
          </ins-base-table>
          <div class="spacer large"></div>
        </div>
        <!-- /end table -->
      </div>
      <!-- /has data -->
      </div>
    </div>
  </div>
</template>

<script>
// general resources
import InsitesMixins from '../../mixins/InsitesMixins';
import VclMixins from '../../mixins/VclMixins';
import InsTable from '../../components/InsTable/InsTable.vue';

// site manager module resources
import SiteManagerService from '../../mixins/SiteManagerService';
import { getAllContentBlocksJSON } from '../../services/SiteManagerServices';
import { deleteVclFile } from '../../services/VclService';
export default {
  name: "ContentBlocks",
  mixins: [InsitesMixins, SiteManagerService, VclMixins],
  components: {
    InsTable
  },
  data() {
    return {
      hasData: true,
      source: "/insites/api/get-all-partials-data.json",
      deleteURL: "/insites/api/delete-partial.json?id=",
      updateURL: "/insites/api/update-partial.json?id=",
      location: "",
      rawData: [],
      filteredData: [],
      displayedData: [],
      tableHeaders: [],
      totalCount: 0,
      pageNumber: 1,
      pageSize: 10,
      searchKeyword: "",
      sortedKeyword: "",
      sortedBy: "asc",
      continueDeletion: false
    }
  },
  methods: {
    addContentBlockHandler(){
      InsitesUtil.renderPage('Add Content Block', '#manage-content-block');
      this.$store.commit("pushFormType", "add");
      this.$router.push({ "name": "ManageContentBlock" });
    },
    extractFileName(url) {
      return url.substring(url.lastIndexOf('/')+1);
    },
    tablePaginationHandler(event) {
      this.$refs.insBaseTable.loadingScreen = true;
      this.pageSize = event.detail.pageSize;
      this.pageNumber = event.detail.pageNumber;
      this.fetchDataFromSrc();
    },
    tableSearchHandler(event) {

      if ((event.detail.keyCode === 13 && event.detail.value !==  "") ||
        (event.detail.value === "" && this.searchKeyword !== "")
        ){

      this.searchKeyword = event.detail.value;
      this.pageNumber = 1;
      this.$refs.insBaseTable.pageNumber = 1;
      this.fetchDataFromSrc();
      }
    },
    tableSortHandler(event) {
      this.sortedKeyword = event.detail.keyword;
      this.sortedBy = event.detail.order;
      this.fetchDataFromSrc();
    },
    tableBulkActionHandler(event) {
      if (event.detail.action === "Delete") {
        this.archiveSelection(event.detail.selections);
      }
    },
    tableRowActionHandler(event) {
      if (event.detail.action === "Delete") {
        this.archiveItem(event.detail.data);
      } else if (event.detail.action === "Edit") {
        this.$store.commit("pushPartialData", event.detail.data);
        this.$store.commit("pushFormType", "edit");
        InsitesUtil.renderPage('Edit Content Block', `#/edit-content-block/${event.detail.data.id}`);
        this.$router.push(`/edit-content-block/${event.detail.data.id}`);

      } else if (event.detail.action === "View") {
        this.$store.commit("pushPartialData", event.detail.data);
        this.$store.commit("pushFormType", "view");
        this.$router.push(`/view-content-block/${event.detail.data.id}`);
        InsitesUtil.renderPage('View Content Block', `#/edit-content-block/${event.detail.data.id}`);
        // this.viewItem();
      }
    },
    viewItem() {
      router.push("/manage-admin-user");
    },
    archiveItem(value) {
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="Are you sure you want to delete this content block?">
          </ins-loader>`,
        type: "",
        width: 330,
        showCancelButton: true,
        confirmButtonClass: "negative",
        confirmButtonText: "Delete"
      }).then(result => {
        if (result.value){
          this.deleteHandler(value, "Content Block has");
        }
      });
    },
    async deleteHandler(pageData, text) {
      let vclPayload = this.constructVclPayload(pageData.Data);
      
      let vclRes;
      vclRes = await deleteVclFile(vclPayload);
      if (vclRes.state){
        if (vclRes.results.message.includes("Successfully")){
          this.deleteItem(pageData, text);
          return;
        }
      }
      toastIt("error", "Failed to delete content block. File may already have been deleted.", InsitesUtil.ops());
    },
    archiveSelection(selections) {
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="Are you sure you want to delete these content blocks?">
          </ins-loader>`,
        type: "",
        width: 330,
        showCancelButton: true,
        confirmButtonClass: "negative",
        confirmButtonText: "Delete"
      }).then(result => {
        if (result.value) {
          let counter = 0;
          this.continueDeletion = true;
          selections.forEach(value => {

            if (this.continueDeletion){

              this.deleteHandler(value, "The selected content blocks have");
              counter++;

              if (selections.length === counter) {
                this.continueDeletion = false;
                this.$refs.insBaseTable.resetSelections();
              }
            }
          });
        }
      });
    },
    deleteItem(value, message) {
      // this.formModel.form.deleted_at = moment().format("DD-MM-YYYY HH:mm:ss");
      let url = `${this.deleteURL}${value.id}`;

      this.$http
        .get(url)
        .then(response => {
          if (!this.continueDeletion){
            toastIt("success", `${message} been deleted.`, InsitesUtil.ops());
            this.fetchDataFromSrc();
          }
        }).catch(error => {
          this.continueDeletion = false;
          this.fetchDataFromSrc();
          toastIt("error", "Failed to delete content block. Please try again.", InsitesUtil.ops());
        });
    },
    processTableData() {
      this.$refs.insBaseTable.tableData = this.rawData;
      this.$refs.insBaseTable.loadingScreen = false;
      // setTimeout(() => {
      //   this.$refs.insBaseTable.totalCount = this.totalCount;
      // }, 300);
    },
    dynamicKeySort(key) {
      // Partials - GraphQA has no sort yet
      let OrderSortIndex = 1;

      if (key[0] === "-") {
        OrderSortIndex = -1;
        key = key.substr(1);
      }

      return function(a, b) {
        let num = a[key] < b[key] ? -1 : a[key] > b[key] ? 1 : 0;
        return num * OrderSortIndex;
      };
    },
    async fetchDataFromSrc() {
      let self = this;

      self.$refs.insBaseTable.loadingScreen = true;
      self.$refs.insBaseTable.loaderTitle = "Processing Your Data";
      self.$refs.insBaseTable.loaderMessage =
        "Just a moment, we're fetching and processing your data.";
      self.$refs.insBaseTable.loaderIcon = "processing";

      if (self.searchKeyword){
        let search_path = self.setModuleName(self.searchKeyword)
      }
      let response = await getAllContentBlocksJSON(self.searchKeyword, self.pageNumber, self.pageSize);

      if (response.state) {
        let tableData = response.results ? response.results.admin_liquid_partials.results : '';

        if (response.results.admin_liquid_partials.results.length) {
          self.setModuleName(self.searchKeyword);
          self.$refs.insBaseTable.totalCount = response.results.admin_liquid_partials.total_entries;
          self.hasData = true;
          self.bindTableData(tableData);
        } else {
          self.$refs.insBaseTable.loaderTitle = "";
          self.hasData = false;
          if (self.searchKeyword) {
            self.$refs.insBaseTable.loaderMessage = `No results for "${self.searchKeyword}"`;
          } else {
            self.$refs.insBaseTable.loaderMessage = "There are no Content Blocks yet.";
          }
          self.$refs.insBaseTable.loaderIcon = "no-data";
        }
      } else {
        console.error("Error: " + error);
        self.errorHandler();
      }
    },
    errorHandler(){
      this.$refs.insBaseTable.loaderTitle = undefined;
      this.$refs.insBaseTable.loaderMessage = "Failed to load Data. Please try again.";
      this.$refs.insBaseTable.loaderIcon = "error";
    },
    bindTableData(tableData) {
      let tempArr = [];
      tableData.forEach(item => {
        let tempObj = {
          id: item.id,
          "Path": item.path,
          "Last Modified": "-",
          "Status": "enabled",
          "Data" : item
        };

        tempArr.push(tempObj);
        this.rawData = tempArr;

        if (tableData.length === tempArr.length) {
          this.processTableData();
        }
      });
    },
    setInsRendererLabel(){
      let insRendererEl = document.querySelector('ins-renderer');
      if (insRendererEl.updateRouteLabel){
        insRendererEl.updateRouteLabel('Content Blocks');
      } else {
        setTimeout(() => {
          this.setInsRendererLabel();
        }, 300);
      }
    },
    setupInsBaseTable(){
      this.$refs.insBaseTable.bulkActions = ["Delete"];
      this.$refs.insBaseTable.rowActions = ["Edit", "Delete"];
      this.$refs.insBaseTable.tableHeaders = [
        this.constructTableHeader("Path"),
        // this.constructTableHeader("Last Modified"),
        this.constructTableHeader("Status", "string", false, true)
      ];
    },
    constructTableHeader(label, type = "string", sortable, hasTag, hasLink, hasColumnAction, hasImage){
      return { label, type, sortable, hasTag, hasLink, hasColumnAction, hasImage }
    },
  },
  mounted() {
    this.setInsRendererLabel();
    this.setupInsBaseTable();
    this.searchKeyword = "";
    this.sortedKeyword = "";
    this.$store.commit("pushPartialData", null);
    this.fetchDataFromSrc();
  }
}
</script>
