<template>
  <div class="grid-container-full fm-toolbar">
    <div class="grid-x">
      <div class="medium-12 cell content-blocks-container">
        <!-- toolbar buttons -->
        <div class="button-holder">
          <span class="tooltip--bottom1 spacer"
          tooltip-content="Up One Folder"
          v-on:click="parentFolderHandler"
          ><i class="icon-arrow-up"></i></span>

          <span class="tooltip--bottom1 spacer"
          tooltip-content="Refresh"
          v-on:click="refreshFMContainerHandler"
          ><i class="icon-ongoing-check"></i></span>

          <span class="tooltip--bottom1"
          tooltip-content="Upload"
          v-on:click="openUploadModal"><i class="icon-file-upload"></i></span>

          <span class="tooltip--bottom1 spacer"
          tooltip-content="Download"
          v-if="selectedFile.file_name"
          v-on:click="downloadFileHandler"><i class="icon-file-download"></i></span>

          <span class="tooltip--bottom1"
          tooltip-content="New Folder"
          v-on:click="createFolderHandler"><i class="icon-create-new-folder"></i></span>

          <span class="tooltip--bottom1"
          tooltip-content="Edit Tags"
          v-on:click="editTagHandler"
          v-if="selectedFile.file_name"><i class="icon-label"></i></span>

          <span class="tooltip--bottom1 spacer"
          tooltip-content="Archive"
          v-on:click="deleteFileHandler"
          v-if="selectedFile.file_name"><i class="icon-delete"></i></span>

          <span class="tooltip--bottom1"
          tooltip-content="List View"
          v-on:click="toggleListViewHandler"
          v-bind:class="{'active' : toggleView === 'list'}"><i class="icon-view-headline"></i></span>

          <span class="tooltip--bottom1"
          tooltip-content="Grid View"
          v-on:click="toggleGridViewHandler"
          v-bind:class="{'active' : toggleView === 'grid'}"><i class="icon-view-module"></i></span>
        </div>
        <!-- /end toolbar buttons -->
      </div>
    </div>
  </div>
</template>

<script>
// general resources
import { mapState } from 'vuex';
import InsitesMixins from '../../../mixins/InsitesMixins';

// file manager resources
import ComponentManager from '../../../mixins/ComponentManager';
import SelectedFolderManager from '../../../mixins/SelectedFolderManager';
import DeleteFileCustomizationData from '../../../mixins/ArchiveFileManager';
// import { getAssetsFolder } from '../../../services/SiteManagerServices';

export default {
  name: "FMToolbar",
  mixins: [InsitesMixins, SelectedFolderManager, ComponentManager, DeleteFileCustomizationData],
  data() {
    return {
      myData: "test"
    }
  },
  computed: mapState({
    toggleView: state => state.FileManagerStore.toggleView,
    selectedFile: state => state.FileManagerStore.selected_file,
    selectedFolder: state => state.FileManagerStore.selected_folder
  }),
  methods: {
    downloadFileHandler() {
      window.open(this.selectedFile.direct_link, '_blank');
    },
    deleteFileHandler() {
      this.$emit('deleteFile');
    },
    openUploadModal() {
      this.$emit('openModal');
    },
    createFolderHandler() {
      this.$emit('createFolder');
    },
    editTagHandler() {
      this.$emit('editTag');
    },
    refreshFMContainerHandler() {
      this.refreshNodeTree();
    },
    parentFolderHandler() {
      this.goToParentFolder(this.selectedFolder);
    },
    toggleGridViewHandler() {
      this.$store.commit("updateToggleView", "grid");
    },
    toggleListViewHandler() {
      this.$store.commit("updateToggleView", "list");
    }
  },
}
</script>

<style lang="scss" src="./FMToolbar.scss"></style>
