import Vue from "vue";
import Router from "vue-router";

// Site Manager Modules
import GlobalSiteSettings from "./views/GlobalSiteSettings/GlobalSiteSettings.vue";
import ListPages from "./views/ListPages/ListPages.vue";
import ManagePages from "./views/ManagePages/ManagePages.vue";
