<template>
  <div v-bind:class="`grid-x grid-padding-x ${full ? 'full':''}`">
    
    <div v-bind:class="`cell medium-12 small-12 ${full ? 'large-12':'large-7'}`">
      <InsSelectWrap searchable
        placeholder="- Select -"
        v-bind:label="label"
        v-bind:value="value"
        v-bind:disabled="disabled"
        v-on:input="onValueChange">
        
        <ins-select-option
          v-for="(option, key, index) in options"
          v-bind:key="index"
          v-bind:label="option.label"
          v-bind:value="option.value">
        </ins-select-option>
      </InsSelectWrap>
    </div>

    <div v-bind:class="`cell medium-12 small-12 ${full ? 'large-12':'large-5'}`">
      <pre class="liquid-wrap"><span v-pre>{{</span> {{ liquidTag }} <span v-pre>}}</span></pre>
    </div>

  </div>
</template>

<script>
// general resources
import InsSelectWrap from '../../InsSelectWrap/InsSelectWrap.vue';

export default {
  name: "GssSelectField",
  props: {
    label: String,
    value: String,
    options: Array,
    liquidTag: String,
    disabled: Boolean,
    full: Boolean,
  },
  components: {
    InsSelectWrap,
  },
  data() {
    return {}
  },
  methods: {
    onValueChange(value) {
      this.$emit("input", value);
    }
  },
  created () {}
}
</script>

<style lang="scss" src="./GssSelectField.scss">
</style>