// general resources
import { map, uniq } from 'lodash';
import { mapState } from "vuex";
import InsitesMixins from './InsitesMixins';

// file manager module resources
import SelectedFolderManager from './SelectedFolderManager';
import SelectedFilesManager from './SelectedFilesManager';
import { getAssetsFolderJSON, getDeletedAssetsByFolderDataJSON } from '../services/SiteManagerServices';

export default {
    data() {
        return {
            filter_by: "physical_path",
            direct_link: ""
        }
    },
    name: "ComponentManager",
    computed: mapState({
        selectedFolderData: state => state.FileManagerStore.selected_folder_data,
        archivedFiles: state => state.FileManagerStore.archived_files
    }),
    mixins: [InsitesMixins, SelectedFolderManager, SelectedFilesManager],
    methods: {
        async refreshNodeTree() {
            let response = await getAssetsFolderJSON();
            this.$store.dispatch('resetFMToolbar');

            if (response.state) {
                let data = {
                    assets_url: response,
                    data_structure: this.transformMKPAssetFolderResponse(response.results.items.results),
                    folders: this.setFolders(response.results.items.results),
                    files: this.updateFilesOfSelectedFolder(response.results.items.results, { details : { id: this.root_folder } })
                };
                this.$store.dispatch('updateRefreshNodeTree', data);
            } else {
                console.log('error', response.results);
            }
        },
        async updateNodeTreeSelectedFolder(data, response) {
            let keyword = this.convertToPhysicalPath(data.details.id);

            if (data.details.id === this.root_folder) {
                this.refreshNodeTree();
            } else {
                if (typeof response === "undefined") {
                    response = await getAssetsFolderJSON(keyword);
                }

                return new Promise(resolve => {
                    if (response.state) {
                        this.updateFilesOfSelectedFolder(response.results.items.results, data);
                        this.updateChildrenOfSelectedAssetFolder(response.results.items.results, data);
                        this.$store.commit('updateFileManagerAssetURL', response);
                        data.details.is_loading = false;
                        data.details.is_open = true;
                        resolve({state: true});
                    } else {
                        data.details.is_loading = false;
                        console.log('error', response.results);
                        resolve({state: false});
                    }
                })
            }
        },
        compareFilesOfSelectedFolderToCustomization(files, customizations) {
            let file_ids = map(files, 'physical_path');
            let customization_ids = map(customizations, 'physical_path');
            let enabled_files = [];

            let concat_ids = file_ids.concat(customization_ids);
            let duplicate_ids = concat_ids.filter((value, index, self) => {
                return self.indexOf(value) !== index;
            });

            files.forEach(value => {
                if (!duplicate_ids.includes(value.physical_path)) {
                    enabled_files.push(value);
                }
            });

            return enabled_files;
        },
        async updateFilesOfSelectedFolder(assets_url, data) {
            let files = this.transformMKPSelectedFilesResponse(assets_url, this.convertToPhysicalPath(data.details.id));
            let folder = this.convertToFolder(this.convertToPhysicalPath(data.details.id));
            let archived_files = await getDeletedAssetsByFolderDataJSON(this.root_folder + (folder ? "|" : "") + folder);

            if (archived_files.state) {
                files = this.compareFilesOfSelectedFolderToCustomization(files, archived_files.results.items.results);
                this.$store.commit('updateFileManagerFiles', files);
                this.$store.commit('updateFileManagerArchivedFiles', archived_files.results.items.results);
            } else {
                data.details.is_loading = false;
                console.log('error', archived_files.results);
            }
        },
        updateChildrenOfSelectedAssetFolder(assets_url, data) {
            let folders = this.updateMKPSelectedAssetFolder(data, this.folders, assets_url);
            data.children = folders;
            this.$store.commit('updateFileManagerSelectedFolderData', data);
        },
        goToParentFolder() {
            let id = this.selectedFolderData.details.id;
            if (id.split('/').length > 1) {
                id = this.removeArrLastIndex(id).replace(/\//g, '-');
            }
            document.querySelector(`#${id}`).click();
            this.$store.commit('resetFileManagerSelectedFile');
        },
        updateImagePreview(file) {
            let imgContainer = document.getElementById('imagePreview');
            this.direct_link = file.direct_link;
            this.getFileType(file.file_name) === 'image'
                ? imgContainer.innerHTML = `<img src="${file.direct_link}}" />`
                : '';
            // onload="displayImageProperties('${file.direct_link}')"
        }
    }
}