<template>
<div class="grid-container-full template-new" v-if="templateFormDataChecked && vclInitialized">
  <div class="grid-x">
    <div class="large-12 medium-12 small-12 cell content-blocks-container">
      <ins-card steady>

        <h2 class="">Template Details</h2>

        <div class="grid-x grid-margin-x">
          <div class="large-7 medium-12 small 12 cell">

            <div class="with-preview-wrap">
              <InsInputWrap label="Template Path*"
                placeholder="Please enter template path"
                maxlength="50" 
                required="true"
                v-model="displayName"
                v-bind:validate="true"
                v-bind:disabled="disableForm || templateFormType === 'Edit'"
                v-bind:error-message="`Template path is required.`"
                v-on:keyup.native="validateInput"
                v-on:fieldError="fieldErrorHandler">
              </InsInputWrap>
              <span class="path-preview"
                v-if="templateFormType === 'New'">
                {{ 'modules/ins_site_manager/public/views/layouts/' + displayName + '.html.liquid' }}
              </span>
              <span class="path-preview"
                v-else-if="templateFormType === 'Edit'">
                {{ templateFormData.physical_file_path }}
              </span>
            </div>

          </div>
          <div class="cell large-5 medium-12 small-12 content-blocks-container">
            <label class="liquid-label">Body Content:</label>
            <pre class="liquid-wrap"><span v-pre>{{ </span>content_for_layout<span v-pre> }}</span></pre>
            <small><i>NOTE: Use liquid tag to append body content to template.</i></small>
          </div>
        </div>

        <Redactor label="Content" ref="myRedactor"
          v-bind:source-code="templateFormData.content"
          v-bind:readonly="disableForm"
          v-on:getRedactorData="getRedactorData"/>
      </ins-card>
      <div class="spacer"></div>

      <div class="grid-x grid-margin-x">
          <div class="large-4 medium-3 small-12 cell">
            <ins-button label="Delete"
              color="information-1"
              size="normal"
              v-if="templateFormType !== 'New' && !disableForm"
              v-on:onClickInsButton="removeTemplateHandler">
            </ins-button>
            <div class="spacer"></div>
          </div>

          <div class="large-8 medium-9 small-12 cell text-right">
            <ins-button label="CANCEL" outlined
              v-on:onClickInsButton="cancelManageTemplateHandler">
            </ins-button>

            <ins-button label="COPY TEMPLATE" outlined
              v-if="templateFormType === 'Edit'"
              v-on:onClickInsButton="copyTemplateHandler">
            </ins-button>

            <ins-button 
              solid
              color="primary" size="normal"
              v-if="templateFormType !== 'View' && !disableForm"
              v-bind:label="templateFormType === 'Edit' ? 'UPDATE' : 'SAVE'"
              v-bind:options="this.vclMasterData.info.is_enabled ? 'Save with commit message':''"
              v-bind:disabled="savingTemplate"
              v-bind:loading="savingTemplate"
              v-on:onClickInsButton="publishHandler"
              v-on:onClickInsButtonOption="runPrompt">
            </ins-button>
            <!-- <ins-button solid
              v-if="templateFormType !== 'View'"
              v-bind:label="templateFormType === 'Edit' ? 'UPDATE' : 'SAVE'"
              v-bind:disabled="savingTemplate"
              v-bind:loading="savingTemplate"
              v-on:onClickInsButton="saveHandler">
            </ins-button> -->
            <div class="spacer"></div>
          </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
// general resources 
import { mapState } from 'vuex';
import InsitesMixins from '../../mixins/InsitesMixins';
import VclMixins from '../../mixins/VclMixins';
import InsInputWrap from '../../components/InsInputWrap/InsInputWrap.vue';
import Redactor from '../../components/Redactor/Redactor.vue';
import { getTemplateByIDJSON } from '../../services/SiteManagerServices';

import { sidebarMenuSiteManagerRoute, listTemplatesRoute, manageTemplateRoute } from "../../models/InsSiteManagerRoutes.js";
import { 
  createVclFile,
  updateVclFile,
  deleteVclFile
} from '../../services/VclService';

export default {
  name: "ManageTemplates",
  mixins: [InsitesMixins, VclMixins],
  components: { Redactor, InsInputWrap },
  data() {
    return {
      savingTemplate: false,
      filePath: 'modules/ins_site_manager/public/views/',
      errors: {
        path: ''
      },
      displayName: '',
      tempWholePath: '',
      disableForm: false,
      copyTemplate: false,
      submittingForm: false,
      hasInvalidField: false,
      templateFormDataChecked: false,
      headers: {},
      missing_fields: 0
    }
  },
  computed: mapState({
    templateFormType: state => state.TemplatesStore.templateFormType,
    templateFormData: state => state.TemplatesStore.templateFormData
  }),
  methods: {
    cancelManageTemplateHandler(){
      InsitesUtil.goBack();
    },
    removeTemplateHandler(){
      let copyMsg = 'Are you sure you want to permanently delete this template? Pages that use this template will be affected.';
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="${copyMsg}">
          </ins-loader>`,
        type: "",
        showCancelButton: true,
        confirmButtonClass: "negative",
        confirmButtonText: "Delete"
      }).then(result => {
        if (result.value) {
          this.deleteHandler();
        }
      });
    },
    async deleteHandler() {
      let vclPayload = this.constructVclPayload(this.templateFormData);
      
      let vclRes;
      vclRes = await deleteVclFile(vclPayload);
      if (vclRes.state){
        if (vclRes.results.message.includes("Successfully")){
          this.removeTemplate();
          return;
        }
      }
      toastIt("error", "Failed to delete template. File may already have been deleted.", InsitesUtil.ops());
    },
    removeTemplate(){
      let url = `/insites/api/delete-layout.json`;
      // url += `?id=${this.templateFormData.id}`;
      let body = {};
      body.payload = {"id" : this.templateFormData.id };
      this.$http.post(url, body, {headers: this.headers})
        .then(this.deleteResHandler)
        .catch(this.deleteErrHandler);
    },
    deleteResHandler(res){
      if (!this.continueDeletion){
        if (res.data.includes){
          if (res.data.includes('500 - Server Error') ||
          res.data.includes('parse_json failed')){
            this.deleteErrHandler('500 POS Server Error');
          }
        } else {
          InsitesUtil.goBack();
          toastIt('success', `Template has been removed.`, InsitesUtil.ops());
          this.$store.dispatch('fetchSiteTemplates', this.$store);  // update template store
          this.$router.push('/templates');
        }
      }
    },
    deleteErrHandler(error){
      console.error(error);
      toastIt("error", "Failed to remove template. Please try again.", InsitesUtil.ops());
    },
    validateForm(){
      let errorsCount = 0;
      if (this.templateFormData.path === ''){
        this.errors.path = 'Path is required.';
        errorsCount++;
      } else {
        this.errors.path = '';
      }
      return errorsCount;
    },
    formValidate(){
      if (InsitesUtil.formValidate(Array.from($(".ins-form-field, .ins-select-value-input, .ins-textarea-field")))) {
        return true;
      } 
      return false;
    },
    submitForm(parsedData, word) {
      let pageData = { payload: JSON.parse(parsedData) }
      if (this.vclMasterData.info.is_enabled){
        this.runVcl(pageData, word);
      } else {
        this.runMutation(pageData, word);
      }
    },
    async runVcl(pageData, word){
      let vclPayload = this.constructVclPayload(pageData.payload);
      let vclRes;
      console.log('templateFormType', this.templateFormType);
      if (this.templateFormType === "Edit"){

        if (this.tempWholePath !== vclPayload.file.physical_file_path){
          vclPayload.git.rename_file = this.tempWholePath;
        }

        vclRes = await updateVclFile(vclPayload);

      } else {
        vclRes = await createVclFile(vclPayload);
      }
      if (vclRes.state){

        if (vclRes.results.message.includes("File already exist")) {
          this.overwriteHandler(pageData, word);
          return;
        } else if (vclRes.results.message.includes("Successfully")){
          this.runMutation(pageData, word);
          return;
        }
      }
      this.errorHandler(vclRes.results.response.data.message);
    },
    async overwriteHandler(pageData, word){
      let prompt = await this.vclPrompt(
        "File already exist. would you like to overwrite?",
        "Overwrite");
        
      if (prompt.value){
        this.gitOverwrite = true;
        this.runVcl(pageData, word);
      }
      this.resetFormStatus();
    },
    setFilePaths() {
        this.templateFormData.path = `${this.displayName}`;
        this.templateFormData.path = this.constructPath(this.templateFormData.path);
        this.templateFormData.physical_file_path = this.constructPhysicalPath(`layouts/${this.displayName}`);
    },
    publishHandler(event){
        this.submittingForm = true;
        this.savingTemplate = true;
        if (this.formValidate()){
          if(this.templateFormType === 'New') {            
              this.setFilePaths();
          } else {            
            this.templateFormData.path = this.templateFormData.path;
            this.templateFormData.physical_file_path = this.templateFormData.physical_file_path;           
          }
          this.templateFormData.content = this.$refs.myRedactor.getRedactorValue();
          let parsedData = JSON.stringify(this.templateFormData);
          let word = this.templateFormType === 'Edit' ? 'updated' : 'added';          
          this.submitForm(parsedData, word);
        } else {          
          this.resetFormStatus();
        }
    },
    async runPrompt(){
      let prompt = await this.vclPromptCommitMessage();
      if (prompt.value){
        this.gitMessage = prompt.value;
        this.publishHandler();
      }
    },
    clearData(){
      this.tempName = "";
      this.$refs.redactorEl.clearCode();
      this.templateFormData.path = "";
      this.templateFormData.content = "";
      this.templateFormData.physical_file_path = "";
      this.resetFormStatus();
    },
    resetFormStatus(){
        this.savingTemplate = false;
        this.submittingForm = false;
    },
    runMutation(pageData, word){
      let type = this.templateFormType === 'New' ? 'create':'update';
      let url = `/insites/api/${type}-layout.json`
      console.log('pageData', pageData);
      this.$http.post(url, pageData, {headers: this.headers})
        .then(res => this.resHandler(res, word, pageData.payload))
        .catch(this.errorHandler);
    },    
    fieldErrorHandler(event){

      let label = event.target.$el.label;
      let lastIndex = label.length - 1;
      let lastLetter = label[lastIndex];

      if (lastLetter === "*"){
        label = label.substring(0, lastIndex);
      }

      event.target.$el.hasError = event.error;
      event.target.$el.errorMessage = (`${event.target.$el.label} is required.`).replace('*','');
      // event.target.$el.errorMessage = `${label} is required.`;
    },
    errorHandler(error){
      console.error(error);
      if (this.templateFormType === "New"){
        this.templateFormData.metadata.status = "";
      }
      this.resetVclData();
      this.resetFormStatus();
      toastIt("error", "Failed to save template. Please try again.", InsitesUtil.ops());
    },
    resHandler(res, word, type){
      if (res.data.includes) {
        if (res.data.includes('500 - Server Error') ||
            res.data.includes('parse_json failed')){
          this.errorHandler('500 POS Server Error');
          toastIt("error", `Failed to ${word} template. Please try again.`, InsitesUtil.ops());
        }
      } else {
        if (word === 'saved') {
          this.templateFormData.id = res.data[`admin_liquid_layout_${type}`].id;
          this.$store.commit('setTemplateFormType', 'Edit');
        }
        let message = `Template has been ${word}.`;
        toastIt('success', message, InsitesUtil.ops());
        this.$store.dispatch('fetchSiteTemplates', this.$store);  // update template store
        this.$router.push('/templates');
      }
      this.resetFormStatus();
    },
    getRedactorData(code) {
      this.templateFormData.content = code;
    },
    constructPath(path){
      let cleanPath = path[0] === '/' ? path.substring(1) : path;
      if(this.copyTemplate) {
          cleanPath = `${cleanPath}_copy`;
          this.displayName = `${this.displayName}_copy`;
          this.copyTemplate = false;
          this.$store.commit('setTemplateFormType', 'New');
      }
      // if (path.includes('layouts/')) {
      //    return cleanPath;
      // }
      return `modules/ins_site_manager/${cleanPath}`;
    },
    constructPhysicalPath(path){
      let ext = path.lastIndexOf('.');
      if (path.substring(ext) === '.liquid'){
        return `${this.filePath}${path}`;
      }
      return `${this.filePath}${path}.html.liquid`;
    },
    copyTemplateHandler(){
      if (!this.validateForm()){
        let copyMsg = 'Are you sure you want to copy this template? This will copy the contents to a New Template.';
        swal({
          title: "",
          html: `
            <ins-loader
              state-icon="warning"
              icon-color="warning"
              state-message="${copyMsg}">
            </ins-loader>`,
          type: "",
          showCancelButton: true,
          confirmButtonText: "Copy"
        }).then(result => {
          if (result.value) {
            this.duplicateTemplate();
          }
        });
      }
    },
    duplicateTemplate(){
      this.copyTemplate = true;
      this.templateFormData.path = this.constructPath(this.displayName);
      this.templateFormData.content = this.$refs.myRedactor.getRedactorValue();
      this.templateFormData.physical_file_path = this.constructPhysicalPath(`layouts/${this.displayName}`);
      let message = `Template has been copied.`;
      console.log("duplicate template data", this.templateFormData);
      toastIt('success', message, InsitesUtil.ops());
    },
    errorTemplates() {
      let self = this;
      setTimeout(() => {
        self.$router.push({ name: 'ListTemplates' });
      }, 1300);
      self.toastInvalidIDNotification('template');
    },
    setEditTemplateFormData(data) {
      let result = {
        data: {
          rawData: data
        },
        action: "Edit"
      }
      this.$store.commit('setTemplateFormData', result);
    },
    async editTemplate() {
      let self = this;
      let response = await getTemplateByIDJSON(self.checkURLParamsID());

      return new Promise(resolve => {
        if (response.results.admin_liquid_layouts.total_entries) {
          self.setEditTemplateFormData(response.results.admin_liquid_layouts.results[0]);
          resolve({ state: true });
        } else {
          resolve({ state: false });
        }
      });
    },
    setBreadCrumbs() {
      let crumbs = [];
      crumbs = [sidebarMenuSiteManagerRoute, listTemplatesRoute];

      if (this.$route.name === "EditTemplate") {
        crumbs.push({
          link: `#/edit-template/${this.checkURLParamsID()}`,
          app: false,
          withSubmenu: false,
          label: 'Edit Template'
        });
      } else if (this.$route.name === "ManageTemplate") {
        crumbs.push(manageTemplateRoute);
      }
      InsitesUtil.setBreadCrumbs(crumbs);
    },
  },
  async mounted() {
    let self = this;
    let response = {};
    if (self.$route.name === "EditTemplate") {
      this.$store.commit("setTemplateFormType", "Edit");
      if (self.templateFormData.id) {
        response.state = true;
      } else if (self.checkURLParamsID()) {
        response = await self.editTemplate();
      }
      if(this.templateFormData.physical_file_path.indexOf('modules/') !== 0) {
        this.disableForm = true;
      }
    } else if (self.$route.name === "ManageTemplate") {
      this.$store.commit("setTemplateFormData", { data: {}, action: "New" });
      response.state = true;
      self.displayName = "";
    }
  
    if (response.state) {
      self.tempWholePath = self.templateFormData.physical_file_path;
      console.log('tempWholePath', self.tempWholePath);
      self.displayName = self.templateFormData.path.replace("layouts/", "");
      self.templateFormDataChecked = true;
      self.headers = self.apiHeader();
      self.setBreadCrumbs();
      self.templateFormType === 'Edit' ? self.displayName = self.templateFormData.path.replace('layouts/','') : '';
    } else {
      self.errorTemplates();
    }
  }
}
</script>

<style lang="scss" src="./ManageTemplates.scss"></style>
