export default {
  data() {
    return {
      insRendererEl: undefined,
    }
  },
  methods: {
    getAuthToken() {
      if (window.location.hostname !== "localhost") {
        return JSON.parse(document.getElementById("formConfig").innerHTML);
      } else {
        return '';
      }
    },
    goBack() {
      let currentCrumbs = JSON.parse(window.localStorage.getItem('ins_breadcrumbs'));
      currentCrumbs.pop();

      let insRendererEl = parent.document.querySelector('ins-renderer');
      insRendererEl.updateRoute(currentCrumbs);
    },
    renderPage(label, link, withSubmenu = false, app = false) {
      let newRoute = { label, link, withSubmenu, app }
      let currentCrumbs = JSON.parse(window.localStorage.getItem('ins_breadcrumbs'));
      let insRendererEl = parent.document.querySelector('ins-renderer');
      currentCrumbs.push(newRoute);
      insRendererEl.updateRoute(currentCrumbs);
    },
    capitalize(item){
      return this.checkValue(item).split("_").map((s) => s.charAt(0).toUpperCase() + s.substring(1)).join(' ');
    },
    validateInput(_field) {
        _field = _field.target ? _field.target: _field;

        if (!_field.value) {
            this.invalidFieldNotif(_field);
            return 1;
        } else {
            this.validFieldNotif(_field);
            return 0;
        }
    },
    // update material design to show error message
    invalidFieldNotif(_field) {
        _field.closest("ins-input").setAttribute("has-error", true);
    },
    // update material design to remove error message
    validFieldNotif(_field) {
        _field.closest("ins-input").removeAttribute("has-error");
    },
    checkURLinput(event, secondary){
      let clean = event.split(' ').join('-').toLowerCase();
      if (secondary === 'text'){
        return clean.replace(/[^a-z0-9-\/]/g,'');
      }
      return clean;
    },
    convertToPhysicalPath(path) {
        path = path.split('/');
        path.shift();
        path = path.join('/');

        return path;
    },
    removeArrLastIndex(path) {
      path = path.split('/');
      path.pop();
      path = path.join('/');

      return path;
  },
    convertToFolder(path) {
        return path.replace(/\//g, '|');
    },
    returnFormattedDate(dateTime) {
        return InsitesUtil.formatLocalisation(dateTime);
    },
    localhostAssetManagerGetFileData(physical_path, response) {
        let result = [];
        
        for (let counter = 0; counter < response.results.data.items.results.length; counter++) {
            let value = response.results.data.items.results[counter];
            if (value.physical_path === physical_path) {
                result = response.results.data.items.results[counter];
                break;
            }
        }

        return result;
    }
  }
}
