export default {
    data() {
        return {
            insRendererEl: undefined,
        }
    },
    methods: {    
        toastInvalidIDNotification(label = 'id') {        
            toastIt("warning", `Invalid ${label}, no data found.`, InsitesUtil.ops()); 
        },
        apiHeader(element = document.getElementsByName('csrf-token')[0].content) {
            var headers = {
                "X-CSRF-Token": "",
                "X-Requested-With": "XMLHttpRequest",
                Accept: "application/json",
                "Content-Type": "application/json"
            }

            if (window.location.hostname !== "localhost") {
                headers["X-CSRF-Token"] = element;
            }
            return headers;
        },
        checkValue(value, withPlus){
          return value && value !== 'undefined' ? `${withPlus ? '+':''}${value}` : '';
        },
        checkURLParamsID() { 
            return this.$route.params.id;
        },
        setURLParameter(url, value) {
            this.$router.push({path: `${url}/${value}`});
        },
        capitalize(item){
            return this.checkValue(item).split("_").map((s) => s.charAt(0).toUpperCase() + s.substring(1)).join(' ');
        },
        validateInput(_field) {
            _field = _field.target ? _field.target: _field;

            if (!_field.value) {
                InsitesUtil.invalidFieldNotif(_field);
                return 1;
            } else {
                InsitesUtil.validFieldNotif(_field);
                return 0;
            }
        },    
        convertToPhysicalPath(path) {
            path = path.split('/');
            path.shift();
            path = path.join('/');

            return path;
        },
        removeArrLastIndex(path) {
            path = path.split('/');
            path.pop();
            path = path.join('/');

            return path;
        },
        convertToFolder(path) {
            return path.replace(/\//g, '|');
        },
        returnFormattedDate(dateTime) {
            return InsitesUtil.formatLocalisation(dateTime);
        },
        localhostAssetManagerGetFileData(physical_path, response) {
            let result = [];   
            
            for (let counter = 0; counter < response.results.items.results.length; counter++) {
                let value = response.results.items.results[counter];
                if (value.physical_path === physical_path) {
                    result = response.results.items.results[counter];
                    break;
                }
            }
            return result;
        }
    }
}
