import { processRequestAndResponse } from "./CoreServices";

const api_url = "/insites/api/";
const vcl_url = "https://h9764sbehb.execute-api.us-east-1.amazonaws.com/dev/";
const vclHeaders = { "Content-Type": "application/json" }

export async function getInstanceUrl(){
    let data = await processRequestAndResponse(`${api_url}get-instance-url.json`, 'get');
    return data.results.SERVER_NAME;
}

export async function getAdminUsers(){
    let data = await processRequestAndResponse(`${api_url}get-all-administrator-data.json?size=10000&page=1&sortBy=&orderBy=asc`,'get');
    return data.results.items;
}

export async function getVclUsersProfile(page = 1, size = 10, sortBy, orderBy, keyword, is_enabled) {
    let url = 'get-vcl-users-profile.json';
    url += `?size=${size}`
    url += `&page=${page}`;
    url += `&sortBy=${sortBy}`;
    url += `&orderBy=${orderBy}`;
    url += `&keyword=${keyword}`;
    url += `&is_enabled=${is_enabled === "all" ? "" : is_enabled}`;

    let data = await processRequestAndResponse(`${api_url}${url}`,'get');
    return data;
}

export async function getCustomisationMaster(){
    let data = await processRequestAndResponse(`${api_url}get-vcl-master.json`,'get');
    if (data.results.items.results.length){
        return data.results.items.results[0];
    }
    return false;
}

export async function mutateUserVclProfile(payload){
    let data = await processRequestAndResponse(`${api_url}mutate-vcl-user-profile.json`, 'post', payload);
    return data;
}

export async function deleteUserVclProfile(payload){
    let data = await processRequestAndResponse(`${api_url}delete-vcl-user-profile.json`, 'post', payload);
    return data;
}

export async function createMasterCustomisation(payload){
    let data = await processRequestAndResponse(`${api_url}create-vcl-master.json`, 'post', payload);
    return data;
}

export async function updateMasterCustomisation(payload){
    let data = await processRequestAndResponse(`${api_url}update-vcl-master.json`, 'post', payload);
    return data;
}

// Lambda Endpoints
export async function getVclUser(instance_url, insites_key){
    let data = await processRequestAndResponse(
        `${vcl_url}get-user-cred?instance_url=${instance_url}&insites_key=${insites_key}`, 'get', {}, vclHeaders);
    return data;
}

export async function createVclUser(payload){
    let data = await processRequestAndResponse(`${vcl_url}create-user-cred`, 'post', payload, vclHeaders);
    return data;
}

export async function updateVclUser(payload){
    let data = await processRequestAndResponse(`${vcl_url}update-user-info`, 'put', payload, vclHeaders);
    return data;
}

export async function deleteVclUser(payload){
    let data = await processRequestAndResponse(`${vcl_url}delete-user-cred`, 'delete', payload, vclHeaders);
    return data;
}

