import InsitesMixins from '../mixins/InsitesMixins'

const axios = require("axios");
const api_url = "/insites/api/";
const insHeader = InsitesMixins.methods.apiHeader();

// return insites branding assets data
export async function getInsAssets() {
    let url = `${api_url}insites_assets.json`;
    return await processRequest(url, 'get')
        .then(response => {
            return ({ state: true, results: response.data });
        })
        .catch(error => {
            console.warn("Error: Failed to get Insites assets.");
            return ({ state: false, results: error });
        });
}

// return current logged in user data
export async function getCurrentUser() {
    let url = `${api_url}get-current-user-data.json`;
    return await processRequest(url, 'get')
        .then(response => {
            return ({ state: true, results: response.data });
        })
        .catch(error => {
            console.warn("No user data found. Session is invalid or expired.");
            return ({ state: false, results: error });
        });
}

// return settings & branding data
export async function fetchSettingsAndBrandingDataJSON() {
  let url = `${api_url}site-settings-data.json`;
    return await processRequest(url, 'get')
    .then(response => {
        return ({ state: true, results: response.data.items.results });
    })
    .catch(error => {
        // User is not subscribed to Settings & Branding Module.
        console.warn(
            "Note: You are not subscribed to Insites Settings & Branding Module."
        );
        return ({ state: false, results: error });
        // toastIt("error", "Failed to fetch settings and branding.", InsitesUtil.ops());
    });
}

// return system fields data
export async function fetchSystemFieldsDataJSON(type, page = 1, size = 10) {
    let url = `${api_url}get-system-fields-data.json?key=${type}&page=${page}&size=${size}`;
    return await processRequestAndResponse(url, 'get');
}


//******************************//
//**** Core Service Manager ****//
//******************************//

// returns response from XML Http Requests
// - let data = await processRequestAndResponse()
// - return await processRequestAndResponse()
export async function processRequestAndResponse(url, method, data = {}, headers = insHeader) {
    return await processRequest(url, method, data, headers)
        .then(response => {
            return { state: true, results: response.data };
        }).catch(error => {
            return { state: false, results: error };
        });
}

// returns response from XML Http Requests ONLY
// - let data = await processRequest()
// - return await processRequest()
export async function processRequest(endpoint, method, data = {}, headers = insHeader) {
  let url = `${endpoint}`;
  return await axios({ method, url, data, headers});
}