<template>
<div class="grid-container-full pages vcl-wrap">
  <div class="grid-x">
    
    <div class="large-12 cell">
      <div class="spacer small"></div>
      <div class="page-view-filter" v-if="hasData && vclMasterData">

        <span class="filter-label">Filter:</span>
        <InsSelectWrap label="Status" button
          v-model="filter"
          v-on:input="tableFilterHandler">

          <!-- <ins-select-option label="All" value="all"></ins-select-option> -->
          <ins-select-option label="Active" value="true" default></ins-select-option>
          <ins-select-option label="Archived" value="false"></ins-select-option>
        </InsSelectWrap>
      </div>

      <div v-if="!hasData || !vclMasterData">
        <div class="spacer small"></div>

        <ins-card steady><!-- TODO: Description -->
          <h2 class="title-page h2desc">Welcome to Version Control</h2>
          <p class="pdesc">Version Control allows you to keep track of your files in your repository. Currently supported repositories are <strong>'GitHub and AWS Code Commit'</strong></p>
        </ins-card>

        <div class="pages-bottom" v-if="!vclMasterData">
          <h2 class="pages-title-secondary h2desc">Version Control has not been setup yet.</h2>
          <div class="spacer small"></div>
          <ins-button label="CONFIGURE REPOSITORY" solid
            v-on:onClickInsButton="goToSetupVcl">
          </ins-button>
        </div>

        <div class="pages-bottom" v-if="!vclUsers.results.length && vclMasterData">
          <h2 class="pages-title-secondary h2desc">Version Control has no users yet.</h2>
          <div class="spacer small"></div>

          <ins-button label="CONFIGURE A USER" solid
            v-bind:disabled="!adminUsersNoProfile.length"
            v-on:onClickInsButton="goToVclNewUser">
          </ins-button>

          <ins-button label="CONFIGURE REPOSITORY" solid
            v-on:onClickInsButton="goToSetupVcl">
          </ins-button>

        </div>
      </div>

      <div class="header-buttons" v-show="hasData && vclMasterData">

        <ins-button label="CONFIGURE REPOSITORY" solid
          color="primary" size="normal"
          v-on:onClickInsButton="goToSetupVcl"
          v-if="vclInitialized">
        </ins-button>

        <ins-button label="CONFIGURE A USER" solid
          color="primary" size="normal"
          v-on:onClickInsButton="goToVclNewUser"
            v-bind:disabled="!adminUsersNoProfile.length"
          v-if="vclInitialized">
        </ins-button>
      </div>

      <div class="ins-table-container" v-show="hasData && vclMasterData">

        <ins-base-table ref="insBaseTable"
          searchbar-placeholder="Search by Name or Email..."
          v-on:onPaginationChange="tablePaginationHandler"
          v-on:onTableBulkAction="tableBulkActionHandler"
          v-on:onTableRowAction="tableRowActionHandler"
          v-on:onTableSearch="tableSearchHandler"
          v-on:onTableSort="tableSortHandler">
        </ins-base-table>
        <div class="spacer large"></div>
      </div>

    </div>
  </div>
</div>
</template>

<script>
// general resources
import InsitesService from '../../mixins/InsitesService';
import InsSelectWrap from '../../components/InsSelectWrap/InsSelectWrap.vue';
import { mapState } from 'vuex';

import { 
  sidebarMenuRoute,
  listVclRoute
} from '../../models/VclRoutes';

import { 
  getVclUsersProfile,
  updateVclUser,
  deleteVclUser,
  mutateUserVclProfile
} from '../../services/VclService';

export default {
  name: "ListVcl",
  mixins: [InsitesService],
  components: {
    InsSelectWrap
  },
  data() {
    return {
      hasData: true,
      searchKeyword: "",
      sortedKeyword: "",
      sortedBy: "asc",
      pageNumber: 1,
      pageSize: 10,
      vclUsers: {results:[]},
      continueDeletion: false,
      filter: "true"
    }
  },
  computed: mapState({
    vclInitialized: state => state.VclStore.vclInitialized,
    vclMasterData: state => state.VclStore.vclMasterData,
    adminUsersNoProfile: state => state.VclStore.adminUsersNoProfile
  }),
  methods: {
    tableFilterHandler(){ 
      this.fetchDataFromSrc();

      if (this.filter === "true"){
        this.$refs.insBaseTable.bulkActions = ["Archive", "Delete"];
        this.$refs.insBaseTable.rowActions = ["Update", "Archive", "Delete"];
      } else if (this.filter === "false"){
        this.$refs.insBaseTable.bulkActions = ["Restore"];
        this.$refs.insBaseTable.rowActions = ["Restore"];
      }
    },
    tablePaginationHandler(event) {
      this.$refs.insBaseTable.loadingScreen = true;
      this.pageSize = event.detail.pageSize;
      this.pageNumber = event.detail.pageNumber;

      this.fetchDataFromSrc();
    },
    tableSearchHandler(event) {
      
      if ((event.detail.keyCode === 13 && event.detail.value !==  "") ||
          (event.detail.value === "" && this.searchKeyword !== "")
        ){

        this.searchKeyword = event.detail.value;
        this.pageNumber = 1;
        this.$refs.insBaseTable.pageNumber = 1;
        this.fetchDataFromSrc();
      }
    },
    tableSortHandler(event) {

      switch(event.detail.keyword){
        case "Name":
          this.sortedKeyword = "first_name";
          break;

        case "Last Updated":
          this.sortedKeyword = "updated_at";
          break;

        default:
          this.sortedKeyword = event.detail.keyword;
      }

      this.sortedBy = event.detail.order;
      this.fetchDataFromSrc();
    },
    tableBulkActionHandler(event) {
      switch (event.detail.action) {
        case "Archive":
        case "Restore":
          this.archiveRestoreSelection(event.detail.selections);
          break;
      }
    },
    tableRowActionHandler(event) {
      let data = event.detail.data;
      let action = event.detail.action;

      switch (event.detail.action) {
        case 'Archive':
        case 'Restore':
          this.archiveRestoreItem(event.detail.data);
          break;

        case 'Update':
          this.goToVclUpdateUser(data);
          break;

        case 'Delete':
          this.archiveRestoreItem(event.detail.data, true);
          break;
      }
    },
    constructSwalWords(toDelete){
      if (toDelete){
        return {word: "delete", buttonText: "Delete", buttonClass: "negative"}
      }

      return {
        word: this.filter === "true" ? "archive" : "restore",
        buttonText: this.filter === "true" ? "Archive" : "Restore",
        buttonClass: this.filter === "true" ? "negative" : "positive"
      }
    },
    archiveRestoreItem(data, toDelete) {
      let filtered = this.constructSwalWords(toDelete);
      if (this.$store.getters.getCurrentUserId === data.rawData.id){

        swal({
          title: "",
          html: `
            <ins-loader
              state-icon="warning"
              icon-color="warning"
              state-message="Are you sure you want to ${filtered.word} this user?">
            </ins-loader>`,
          type: "",
          width: 330,
          showCancelButton: true,
          confirmButtonClass: filtered.buttonClass,
          confirmButtonText: filtered.buttonText
        }).then(result => {
          if (result.value){
            this.updateItem(data.rawData, "User has", filtered.word, toDelete);
          }
        });
      } else {
        this.notAllowed(filtered.word);
      }
    },
    archiveRestoreSelection(selections, toDelete) {
      let filtered = this.constructSwalWords();


        swal({
          title: "",
          html: `
            <ins-loader
              state-icon="warning"
              icon-color="warning"
              state-message="Are you sure you want to ${filtered.word} this users?">
            </ins-loader>`,
          type: "",
          width: 330,
          showCancelButton: true,
          confirmButtonClass: filtered.buttonClass,
          confirmButtonText: filtered.buttonText
        }).then(result => {
          if (result.value) {
            let counter = 0;
            this.continueDeletion = true;
            selections.forEach(async data => {

              if (this.continueDeletion){

                await this.updateItem(data.rawData, "The selected users have", filtered.word, toDelete);
                counter++;

                if (selections.length === counter) {
                  this.continueDeletion = false;
                  this.deleteSuccessHandler("The selected users have", filtered.word, toDelete);
                }
              }
            });
          }
        });
    },
    notAllowed(word){
      this.deletingUser = false;
      this.sendingForm = false;
      swal({
          html: `
          <ins-loader
              state-icon="warning"
              icon-color="warning"
              state-message="You can only ${word} your own profile.">
          </ins-loader>`,
          width: 330,
      });
    },
    async updateItem(data, message, word, toDelete){
      let payload = this.constructPayload(data);
      let vclRes = { state: true };
      
      if (toDelete){
        vclRes = await deleteVclUser(payload);
      }

      if (vclRes.state) {

        if (toDelete) {
          payload.cred.insites_key = "deleted";
        }
        let posRes = await mutateUserVclProfile(payload);

        if (posRes.state){

          if (!this.continueDeletion){

            if (toDelete){
              this.$store.commit('addAdminUsersNoProfile', data);
            }
            this.deleteSuccessHandler(message, word);
          }
          return;
        }
      }
      this.deleteErrHandler(word);
    },
    deleteSuccessHandler(message, word){
      toastIt('success', `${message} been ${word}d.`, InsitesUtil.ops());
      this.fetchDataFromSrc();
    },
    deleteErrHandler(word){
      toastIt("error", `Failed to ${word} user. Please try again.`, InsitesUtil.ops());
      this.continueDeletion = false;
      this.fetchDataFromSrc();
    },
    errorHandler(){
      let message = `Failed to load users. Please try again.`;
      this.setInsBaseTableLoader('', message, 'error');
    },
    bindTableData(tableData) {
      this.$refs.insBaseTable.tableData = tableData.map(this.constructTableData);
      this.hasData = true;
      this.$refs.insBaseTable.loadingScreen = false;

      setTimeout(() => {
        this.$refs.insBaseTable.totalCount = this.vclUsers.total_entries;
      }, 100);
    },
    setInsBaseTableLoader(title, message, icon){
      this.$refs.insBaseTable.loadingScreen = true;
      this.$refs.insBaseTable.loaderTitle = title;
      this.$refs.insBaseTable.loaderMessage = message
      this.$refs.insBaseTable.loaderIcon = icon;
    },
    setupInsBaseTable(){
      this.$refs.insBaseTable.bulkActions = ["Archive", "Delete"];
      this.$refs.insBaseTable.rowActions = ["Update", "Archive", "Delete"]; 
      this.$refs.insBaseTable.tableHeaders = [
        this.constructTableHeader("Name", "string", true),
        this.constructTableHeader("Email", "string", true),
        this.constructTableHeader("Last Updated", "string", true),
        this.constructTableHeader("Status", "string", false, true)
      ];
    },
    // TODO: Put this in Insites Service/Utils
    constructTableHeader(label, type, sortable, hasTag, hasLink, hasColumnAction, hasImage){
      return { label, type, sortable, hasTag, hasLink, hasColumnAction, hasImage }
    },
    constructTableData(item){
      let fullName = `${item.first_name} ${item.last_name}`;

      return {
        rawData: item,
        id: item.id,
        "Name": fullName,
        "Email": item.email,
        "Last Updated": InsitesUtil.formatLocalisation(item.updated_at),
        "Status": item.insites_vcl_user_profile.properties.is_enabled ? "active" : "archived"
      }
    },
    constructPayload(user){
      let props = user.insites_vcl_user_profile.properties;
      return {
        cred: {
          instance_url: props.instance_url,
          insites_key: props.insites_key,
          info: {
            admin_user_id: user.id,
            last_update_by: this.$store.getters.getCurrentUserId,
            is_enabled: this.filter === "true" ? "false" : "true",
          }
        }
      }
    },
    async fetchDataFromSrc(){
      this.setInsBaseTableLoader(
        "Processing Your Data",
        "Just a moment, we're fetching and processing your data.",
        "processing");

      let data = await getVclUsersProfile(
        this.pageNumber,
        this.pageSize,
        this.sortedKeyword,
        this.sortedBy,
        this.searchKeyword,
        this.filter
      );

      if (data.state){
        this.processTableData(data);
        return
      }
      this.errorHandler();
    },
    processTableData(data){
      if (data.state) {

        this.vclUsers = data.results.items;

        if (this.vclUsers.results.length) {

          this.$refs.insBaseTable.totalCount = this.vclUsers.total_entries;
          this.bindTableData(this.vclUsers.results);

        } else {

          if (this.searchKeyword) {
            
            let message = `No results for "${this.searchKeyword}"`;
            this.setInsBaseTableLoader('', message, 'no-data');

          } else if (this.filter === 'false') {
            
            let message = `No archived users yet.`;
            this.setInsBaseTableLoader('', message, 'no-data');

          } else {
            this.hasData = false;
          } 
        }

      } else {
        this.hasData = true;
        this.setInsBaseTableLoader(undefined, "Failed to get VCL Users", "error");
      }
    },
    activateRoutes(){
      let routes = [sidebarMenuRoute, listVclRoute];
      InsitesUtil.setBreadCrumbs(routes);
    },
    goToSetupVcl(){
      this.renderPage(`Configure Repository`, '#/version-control/setup');
      if (!this.vclMasterData){
        this.$store.commit("setVclFormType", 'setup');
      } else {
        this.$store.commit("setVclFormType", 'update-setup');
      }
      this.$router.push("/version-control/setup");
    },
    goToVclNewUser(){
      this.renderPage(`Configure A User`, '#/version-control/add-user');
      this.$store.commit("setVclFormType", 'new');
      this.$router.push("/version-control/add-user");
    },
    goToVclUpdateUser(data){
      let link = `/version-control/update-user/${data.id}`;
      this.$store.commit("setVclFormType", 'update');
      this.$store.commit("setSelectedAdminUser", data.rawData);
      this.$router.push(link);
      this.renderPage(`Update User Configuration`, `#${link}`);
    },
  },
  mounted(){
    this.setupInsBaseTable();
    this.fetchDataFromSrc();
    // this.activateRoutes();
  }
}
</script>

<style lang="scss" scoped src="./ListVcl.scss"></style>