<template>
  <div class="grid-x grid-padding-x">
    <div class="cell large-6 medium-12 small-12">
      <GssInputField label="Location Name"
        v-bind:liquid-tag="`${keyPlace}_name`"
        v-model="value[`${keyPlace}_name`]" />
    </div>
    <div class="cell large-6 medium-12 small-12">
      <GssSelectField label="Timezone"
        v-bind:options="timezoneOptions"
        v-bind:liquid-tag="`${keyPlace}_timezone`"
        v-model="value[`${keyPlace}_timezone`]" />
    </div>
    <div class="cell large-6 medium-12 small-12">
      <GssInputField label="Contact Person"
        v-bind:liquid-tag="`${keyPlace}_contact_person`"
        v-model="value[`${keyPlace}_contact_person`]" />
    </div>
    <div class="cell large-6 medium-12 small-12">
      <GssInputField label="Email" field="email"
        v-bind:liquid-tag="`${keyPlace}_email`"
        v-model="value[`${keyPlace}_email`]"
        v-on:fieldError="fieldErrorHandler" />
    </div>

    <div class="cell large-6 medium-12 small-12">
      <div class="grid-x grid-padding-x">
        <div class="cell large-7 medium-12 small-12">

          <ins-input-tel label="Phone" v-bind:ref="`${keyPlace}_phone`"
            v-bind:phonenum-value="value[`${keyPlace}_phone_number`]"
            v-bind:areacode-value="value[`${keyPlace}_phone_area_code`]"
            v-bind:country-code="value[`${keyPlace}_phone_country_code`]"
            v-on:insInputTelChange="locPhoneHandler">
          </ins-input-tel>

        </div>
        <div class="cell large-5 medium-12 small-12">
          <pre class="liquid-wrap"><span v-pre>&lbrace;&lbrace;</span> {{ keyPlace + '_phone' }} <span v-pre>&rbrace;&rbrace;</span></pre>
        </div>
      </div>
    </div>

    <div class="cell large-6 medium-12 small-12">
      <div class="grid-x grid-padding-x">
        <div class="cell large-7 medium-12 small-12">

          <ins-input-tel label="Fax" v-bind:ref="`${keyPlace}_fax`"
            v-bind:phonenum-value="value[`${keyPlace}_fax_number`]"
            v-bind:areacode-value="value[`${keyPlace}_fax_area_code`]"
            v-bind:country-code="value[`${keyPlace}_fax_country_code`]"
            v-on:insInputTelChange="locFaxHandler">
          </ins-input-tel>

        </div>
        <div class="cell large-5 medium-12 small-12">
          <pre class="liquid-wrap"><span v-pre>&lbrace;&lbrace;</span> {{ keyPlace + '_fax' }} <span v-pre>&rbrace;&rbrace;</span></pre>
        </div>
      </div>
    </div>

    <!-- ADDRESS -->
    <div class="cell large-12 medium-12 small-12">
      <div class="spacer"></div>
      <ins-heading label="Address"></ins-heading>
    </div>

    <div class="cell large-12 medium-12 small-12">
      <InsInputWrap name="searchAddress" label="Search Address"
        field="text" ref="searchAddressField" autocomplete="off"
        v-bind:placeholder="googleMapsApiKey !== '' ? 'Search Address':'No Google API Key'"
        v-bind:disabled="!googleMapsApiKey !== '' "
        v-model="searchAddress" />
    </div>

    <div class="cell large-6 medium-12 small-12">
      <GssInputField label="Address Line 1"
        v-bind:liquid-tag="`${keyPlace}_address.address`"
        v-model="value[`${keyPlace}_address`].address" />
    </div>
    <div class="cell large-6 medium-12 small-12">
      <GssInputField label="Address Line 2"
        v-bind:liquid-tag="`${keyPlace}_address.address2`"
        v-model="value[`${keyPlace}_address`].address2" />
    </div>
    <div class="cell large-6 medium-12 small-12">
      <GssInputField label="City"
        v-bind:liquid-tag="`${keyPlace}_address.city`"
        v-model="value[`${keyPlace}_address`].city" />
    </div>
    <div class="cell large-6 medium-12 small-12">
      <GssInputField label="State"
        v-bind:liquid-tag="`${keyPlace}_address.state`"
        v-model="value[`${keyPlace}_address`].state" />
    </div>
    <div class="cell large-6 medium-12 small-12">
      <GssInputField label="Post Code"
        v-bind:liquid-tag="`${keyPlace}_address.postcode`"
        v-model="value[`${keyPlace}_address`].postcode" />
    </div>
    <div class="cell large-6 medium-12 small-12">
      <GssInputField label="Country"
        v-bind:liquid-tag="`${keyPlace}_address.country`"
        v-model="value[`${keyPlace}_address`].country" />
    </div>

    <!-- SCHEDULE -->
    <div class="cell large-12 medium-12 small-12">
      <div class="spacer"></div>
      <ins-heading label="Opening Hours"></ins-heading>
    </div>

    <div class="cell large-12 medium-12 small-12">
      <div class="spacer"></div>

      <div class="grid-x grid-padding-x">
        <div class="cell large-offset-2 large-2 medium-offset-4 medium-4 hide-for-small-only">
          <div class="header text-center">Open Time</div>
        </div>
        <div class="cell large-2 medium-4 hide-for-small-only">
          <div class="header text-center">Close Time</div>
        </div>
        
        <div class="cell large-offset-2 large-2 medium-offset-2 medium-2 hide-for-small-only hide-for-medium-only">
          <div class="header text-center">Open Time</div>
        </div>
        <div class="cell large-2 medium-2 hide-for-small-only hide-for-medium-only">
          <div class="header text-center">Close Time</div>
        </div>
      </div>
    </div>

    <div class="cell large-6 medium-12 small-12">
      <GssHoursField label="Monday"
        key-day="mon"
        v-bind:liquid-tag="keyPlace"
        v-model="value" />
        
      <GssHoursField label="Tuesday"
        key-day="tue"
        v-bind:liquid-tag="keyPlace"
        v-model="value"/>

      <GssHoursField label="Wednesday"
        key-day="wed"
        v-bind:liquid-tag="keyPlace"
        v-model="value"/>

      <GssHoursField label="Thursday"
        key-day="thu"
        v-bind:liquid-tag="keyPlace"
        v-model="value"/>

      <GssHoursField label="Friday"
        key-day="fri"
        v-bind:liquid-tag="keyPlace"
        v-model="value"/>
    </div>

    <div class="cell large-6 medium-12 small-12">
      <GssHoursField label="Saturday"
        key-day="sat"
        v-bind:liquid-tag="keyPlace"
        v-model="value"/>

      <GssHoursField label="Sunday"
        key-day="sun"
        v-bind:liquid-tag="keyPlace"
        v-model="value"/>
    </div>
  </div>
</template>

<script>
// general resources
import { mapState } from "vuex";
import InsInputWrap from '../../InsInputWrap/InsInputWrap.vue';

// GSS module resources
import GssInputField from '../GssInputField/GssInputField.vue';
import GssSelectField from '../GssSelectField/GssSelectField.vue';
import GssHoursField from '../GssHoursField/GssHoursField.vue';

export default {
  name: "GssForm",
  components: {
    GssInputField,
    GssSelectField,
    GssHoursField,
    InsInputWrap
  },
  props: {
    keyPlace: String,
    value: Object
  },
  data() {
    return {
      searchAddress: '',
      timezoneOptions: [],
      googleMapsApiKey: ''
    }
  },
  computed: mapState({
    // googleMapsApiKey: state => state.SystemFieldsStore.google_maps_api_key,
   //  timezoneOptions: state => state.SettingsBrandingStore.timezoneOptions
  }),
  methods: {
    fieldErrorHandler(event){
      this.$emit('gssFormInvalid', event);
    },
    onValueChange(value){
      this.$emit("input", value);
    },
    insInputTelChangeHandler(event, type){
      switch (event.detail.field){
        case 'country_code':
          this.value[`${this.keyPlace}_${type}_country_code`] = event.detail.value.dialCode;
          break;

        case 'area_code':
          this.value[`${this.keyPlace}_${type}_area_code`] = event.detail.value;
          break;

        case 'phone_number':
          this.value[`${this.keyPlace}_${type}_number`] = event.detail.value;
          break;
      }
      this.concatFields(type);
    },
    concatFields(type){
      this.value[`${this.keyPlace}_${type}`] = this.value[`${this.keyPlace}_${type}_country_code`] ?
        `${this.value[`${this.keyPlace}_${type}_country_code`]} ` : '';

      this.value[`${this.keyPlace}_${type}`] += this.value[`${this.keyPlace}_${type}_area_code`] ?
        `${this.value[`${this.keyPlace}_${type}_area_code`]}` : '';

      this.value[`${this.keyPlace}_${type}`] += this.value[`${this.keyPlace}_${type}_number`] ?
        `${this.value[`${this.keyPlace}_${type}_number`]} ` : '';

      this.value[`${this.keyPlace}_${type}`] = this.value[`${this.keyPlace}_${type}`].trim();
    },
    locPhoneHandler(event){
      this.insInputTelChangeHandler(event, 'phone');
    },
    locFaxHandler(event){
      this.insInputTelChangeHandler(event, 'fax');
    },
    initGoogleMap(){
      setTimeout(() => {
        if (this.googleMapsApiKey){
          let inputEl = this.$refs.searchAddressField.$el.querySelector('input');

          if (inputEl){
            this.searchAddressField = new google.maps.places.Autocomplete(inputEl);
            this.searchAddressField.addListener('place_changed', this.fillAddress);
          } else {
            this.initGoogleMap();
          }
        }
      }, 1000);
    },
    fillAddress() {
      let searchAddressField = this.searchAddressField.getPlace();

      if (searchAddressField.address_components){
        this.searchAddress = searchAddressField.formatted_address;
        this.clearAddressFields();

        searchAddressField.address_components.forEach(item => {
          let addressType = item.types[0];
          switch (addressType) {
            case "street_number":
              this.value[`${this.keyPlace}_address`].address2 = `${item['short_name']} `;
              break;

            case "route":
              this.value[`${this.keyPlace}_address`].address2 += item['long_name'];
              break;

            case "locality":
              this.value[`${this.keyPlace}_address`].city = item['long_name'];
              break;

            case "colloquial_area":
              this.value[`${this.keyPlace}_address`].city = item['long_name'];
              break;

            case "administrative_area_level_1":
              this.value[`${this.keyPlace}_address`].state = item['short_name'];
              break;

            case "country":
              this.value[`${this.keyPlace}_address`].country = item['long_name'];
              break;

            case "postal_code":
              this.value[`${this.keyPlace}_address`].postcode = item['short_name'];
              break;
          }      
        });
      }
    },
    clearAddressFields(){
      this.value[`${this.keyPlace}_address`].address = "";
      this.value[`${this.keyPlace}_address`].address2 = "";
      this.value[`${this.keyPlace}_address`].city = "";
      this.value[`${this.keyPlace}_address`].state = "";
      this.value[`${this.keyPlace}_address`].country = "";
      this.value[`${this.keyPlace}_address`].postcode = "";
      return;
    },
    setData() {
      let settingsData = localStorage.getItem("settingsData") ? JSON.parse(localStorage.getItem("settingsData")): "";
      this.timezoneOptions = settingsData !== "" ? settingsData.time_zone : [];
      this.googleMapsApiKey = localStorage.getItem("google_maps_api_key") ? localStorage.getItem("google_maps_api_key") : "";
    }
  },
  mounted(){
    this.initGoogleMap();
    this.setData();
  }
}
</script>

<style lang="scss" src="./GssForm.scss">
</style>