<template>
  <div class="grid-container-full fm-preview">
    <div class="grid-x medium-12 cell content-blocks-container" v-show="selectedFile.file_name">
      <div class="grid-x fm-preview-header"  v-show="getFileType(selectedFile.file_name) === 'image'">
        <div class="cell medium-auto">
        <p>Preview</p>
        </div>
      </div>
      <div id="imagePreview" class="preview-holder" v-show="getFileType(selectedFile.file_name) === 'image'">
        <!-- <img id="imagePreview" v-bind:src="`${selectedFile.direct_link}?t=${+ new Date()}`"> -->
      </div>
      <p class="text-center fm-file-icon small-12"><i class="icon-notepad" v-if="getFileType(selectedFile.file_name) !== 'image'"></i></p>
      <ul class="preview-info">
        <li class="grid-x">
            <div class="medium-4 small-12 info-name">
              <p>File Name:</p>
            </div>
            <div class="small-12 medium-auto info-details">
              <p>{{ selectedFile.file_name }}</p>
            </div>
          </li>
        <li class="grid-x">
          <div class="medium-4 small-12 info-name">
            <p>Path:</p>
          </div>
          <div class="small-12 medium-auto info-details">
            <div class="path-link-holder">
             <a v-bind:href="selectedFile.direct_link" target="_blank">{{ selectedFile.physical_path }}</a>
            </div>
          </div>
        </li>
         <!-- <li class="grid-x">
          <div class="medium-4 small-12 info-name">
            <p>Size:</p>
          </div>
          <div class="small-12 medium-auto info-details">
            <p>???</p>
          </div>
        </li>
        <li class="grid-x">
          <div class="medium-4 small-12 info-name">
            <p>Dimensions:</p>
          </div>
          <div class="small-12 medium-auto info-details">
            <p>???</p>
          </div>
        </li> -->
        <li class="grid-x">
          <div class="medium-4 small-12 info-name">
            <p>{{ customizationDate(selectedFile.customization_date, selectedFile.asset_url_date)
                  ? "Last Modified:"
                  : "Date Added:" }}</p>
          </div>
          <div class="small-12 medium-auto info-details">
            <p>{{ customizationDate(selectedFile.customization_date, selectedFile.asset_url_date)
                  ? convertPreviewDate(selectedFile.customization_date)
                  : convertPreviewDate(selectedFile.asset_url_date) }}</p>
          </div>
        </li>
        <li class="grid-x">
          <div class="medium-4 small-12 info-name">
            <p>Modified By:</p>
          </div>
          <div class="small-12 medium-auto info-details">
          <p>{{ customizationDate(selectedFile.customization_date, selectedFile.asset_url_date)
                  ? `${selectedFile.userData.first_name } ${selectedFile.userData.last_name}`
                  : "Marketplace Kit CLI" }}</p>
          </div>
        </li>
        <li class="grid-x" v-if="selectedFile.tags.length > 0">
          <div class="medium-4 small-12 info-name">
            <p>Tags:</p>
          </div>
          <div class="small-12 medium-auto info-details">
            <template
              v-for="(tags, index) in selectedFile.tags">
              {{ tags }}<template v-if="index+1 < selectedFile.tags.length">, </template>
            </template>
          </div>
        </li>
      </ul>
      <div class="grid-x preview-btns">
        <div class="small-12">
         <span class="btn-holder">
           <ins-button v-bind:label="downloadOrPreview(selectedFile.file_name)"
            v-bind:icon="downloadOrPreview(selectedFile.file_name) === 'Preview' ? 'icon-external-link' : 'icon-file-download'"
            solid color="blue"
            size="normal"
            v-on:click="downloadFile(selectedFile.direct_link)">
            </ins-button>
         </span>
         <span class="btn-holder">
           <ins-button label="TAGS"
              icon="icon-label"
              solid color="blue"
              size="normal"
              v-on:click="editTagHandler">
            </ins-button>
         </span>
         <span class="btn-holder">
            <ins-button label="Liquid Tag"
              icon="icon-content_copy"
              solid color="blue"
              size="normal"
              v-on:click="liquidTagHandler">
            </ins-button>
         </span>
         <span class="btn-holder">
          <ins-button
              v-if="archiveLabel(selectedFile.is_enabled)"
              label="Archive" icon="icon-delete"
              solid color="negative"
              size="normal"
              v-on:click="deleteFileHandler"></ins-button>
            <ins-button
              v-else
              label="Restore" icon="icon-rotate"
              solid color="green"
              size="normal"
              v-on:click="restoreFileHandler"></ins-button>
         </span>
         <div class="liquid-tag-container">
             <input type="text" id="liquid-tag-clipboard" v-bind:value="`{{ '${selectedFile.physical_path}' | asset_url }}`">
         </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import moment from "moment";

// general resources
import { mapState } from "vuex";
import InsitesMixins from '../../../mixins/InsitesMixins';

// file manager resources
import SelectedFilesManager from '../../../mixins/SelectedFilesManager';

export default {
  data () {
    return {
      fileDimensions: ""
    }
  },
  mixins: [SelectedFilesManager, InsitesMixins],
  computed: mapState({
    selectedFile: state => state.FileManagerStore.selected_file
  }),
  methods: {
    convertPreviewDate(preview_date) {
      return InsitesUtil.formatLocalisation(moment.unix(preview_date).toISOString());
    },
    customizationDate(customization_date, asset_url_date) {
      if (customization_date > parseInt(asset_url_date)) {
        return 1;
      }
      return 0;
    },
    downloadFile(direct_link) {
      window.open(direct_link, '_blank');
    },
    editTagHandler() {
      this.$emit('editTag');
    },
    deleteFileHandler() {
      this.$emit('deleteFile');
    },
    restoreFileHandler() {
      this.$emit('restoreFile');
    },
    archiveLabel(is_enabled){
      return is_enabled  === "true" || !is_enabled ? true : false;
    },
    liquidTagHandler() {
      var elem = document.getElementById('liquid-tag-clipboard');
      elem.select();
      document.execCommand('copy');

      toastIt('success', 'Copied to clipboard.', InsitesUtil.ops());
    }
  }
}
</script>

<style lang="scss" src="./FMPreview.scss">
</style>
