import TemplateFormModel from '../models/TemplateFormModel';

const TemplatesStore = {
  state: {
    templateFormType: '',
    templateFormData: {},
  },
  mutations: {
    setTemplateFormData(state, data){
      if (data.action !== 'New') {
        state.templateFormData = data.data.rawData;
      } else {
        state.templateFormData = JSON.parse(JSON.stringify(TemplateFormModel));
      }
      state.templateFormType = data.action;
    },
    setTemplateFormType(state, data){
      state.templateFormType = data;
    }
  },
}
export default TemplatesStore;