<template>
<div class="redactor">
  <label class="redactor-label">{{ label }}</label>
  <textarea
    class="redactor-wrap language-css language-js language-html"
    id="content"
	ref="redactorEl"
    v-bind:value="value"
    v-bind:placeholder="placeholder"
    v-bind:name="name"
    v-bind:disabled="disabled"
    v-bind:readonly="readonly"
    v-bind:required="required"
    v-bind:maxlength="maxlength"
    @valueChange="onValueChange">
  </textarea>
  <textarea id="temp-content"></textarea>
  <div v-if="errorMessage" class="ins-form-error">{{ errorMessage }}</div>
</div>
</template>
<script>
import S3UploaderService from '../../mixins/S3UploaderService';
import FileUploadManager from '../../mixins/FileUploadManager';

import { mapState } from 'vuex';
// redactor-source classname
export default {
  name: "RedactorHolder",
  mixins: [S3UploaderService, FileUploadManager],
  props: {
    id: String,
    label: String,
    value: String,
    placeholder: String,
    name: String,
    maxlength: String,
    disabled: Boolean,
    readonly: Boolean,
    required: String,
    errorMessage: String,
    sourceCode: String
  },
  computed: mapState({
    redactorDoctypeHTML: state => state.RedactorStore.redactor_doctype_html,
    redactorHTML: state => state.RedactorStore.redactor_html,
    redactorBody: state => state.RedactorStore.redactor_body,
    redactorBodyTag: state => state.RedactorStore.redactor_body_tag,
    redactorStyle: state => state.RedactorStore.redactor_style,
    redactorHead: state => state.RedactorStore.redactor_head
  }),
  data(){
    return {
        defaultContent: this.prDefaultContent,
        currentContent: "",
        s3FormDataURL: "/insites/api/s3-file-upload.json",
        sourceView: false,
        hasInsComponent: false,
        firstLoadRedactor: true,
        tempEditor: {},
        editor: {}
    }
  },
  mounted() {
    let self = this;
    self.tempEditor = self.initCodeMirror("temp-content", "");
    self.editor = self.initCodeMirror("content", "");
    self.checkIfHTMLHasInsComponent(this.sourceCode);
    let showSource = false;

    if (this.hasInsComponent) {
        showSource = true;
        this.sourceView = true;
    }

    self.redactorEl = $R('#content', {
        showSource: showSource,
        pastePlainText: true,
        imageFigure: false,
        plugins: ['imagemanager', 'alignment', 'table'],
        focus: true,
        multipleUpload: false,
        callbacks: {
            started: () => {
                this.firstLoadRedactor = true;
            },
            source: {
                opened: () => {
                    self.toggleRedactor(true);
                },
                closed: () => {
                    self.toggleRedactor(false);
                }
            }
        },
        imageUpload: async (data, files, e, upload) => {
            let self = this;
            let path = 'assets/uploads';

            if (files.length > 1) {
                upload.complete({
                    "error": true,
                    "message": "Please one image at a time."
                });
                toastIt("error", "Please one image at a time.", InsitesUtil.ops());
            } else {
                let file_type = self.checkFileType(files[0].name);
                if (file_type !== "image") {
                    upload.complete({
                        "error": true,
                        "message": "Only image file types are accepted."
                    });
                    toastIt("error", "Only image file types are accepted.", InsitesUtil.ops());
                    return false;
                }

                let response = await self.uploadFile(path, files[0]);
                if (response.state) {
                    upload.complete({
                        "file": {
                            "url": `{{ '${response.results.physical_path}' | asset_url }}`,
                            "id": response.results.physical_path
                        }
                    });
                } else {
                    upload.complete({
                        "error": true,
                        "message": "Failed to upload image. Please try again."
                    });
                    toastIt("error", "Failed to upload image. Please try again.", InsitesUtil.ops());
                }
            }
        },
        imageManagerJson: '/insites/api/get-all-redactor-images.json'
    });

    self.$store.commit('updateRedactorDoctypeHTML', this.searchFromCode(this.editor.getValue(), '<!doctype html>'));
    self.setValueCodeMirror(self.sourceCode, self.editor);
    self.toggleRedactor(false);

    if (self.readonly){
		self.enableReadOnlyRedactor();
    }
  },
  methods: {
	enableReadOnlyRedactor() {
		this.redactorEl.enableReadOnly();
		this.destroyCodeMirror(this.editor)
	},
    initCodeMirror(id, html) {
        let self = this;
        let editor = CodeMirror.fromTextArea(document.getElementById(id), {
            lineNumbers: true,
            lineWrapping: true,
            mode: "htmlmixed",
            extraKeys: {"Alt-F": "findPersistent"},
			readOnly: this.readonly
        });

        editor.on('change', () => {
            self.checkIfHTMLHasInsComponent(this.editor.getValue());
            if (self.hasInsComponent) {
                document.querySelector('.redactor-toolbar .re-html').className += " redactor-button-disabled";
            } else {
                document.querySelector('.redactor-toolbar .re-html').className = document.querySelector('.redactor-toolbar .re-html').className.replace(" redactor-button-disabled", "");
            }
        });

        CodeMirror.commands["selectAll"](editor);
        self.setValueCodeMirror(html, editor);
        editor.setOption("theme", "monokai");

        return editor;
    },
    readOnlyRedactor() {
        document.querySelector('.redactor-styles').setAttribute('contenteditable', 'false');
        let buttons = document.querySelectorAll('.redactor-toolbar .re-button');

        for (let counter = 0; counter < buttons.length; counter++) {
            if (buttons[counter].className.indexOf('re-html') === -1) {
                buttons[counter].className += " redactor-button-disabled";
            }
        }
    },
    editableRedactor() {
        document.querySelector('.redactor-styles').setAttribute('contenteditable', 'true');
        let buttons = document.querySelectorAll('.redactor-toolbar .re-button');

        for (let counter = 0; counter < buttons.length; counter++) {
            buttons[counter].className.replace(" redactor-button-disabled", "");
        }
    },
    checkFileType(file_name) {
        if (file_name) {
            const img = ['png', 'jpg', 'jpeg', 'gif', 'svg', 'tiff', 'bmp'];
            let file_extension = file_name.split('.').slice(-1)[0].toLowerCase();
            if (file_extension.length === 1) {
                return 'folder'
            } else {
                return img.indexOf(file_extension) >= 0
                    ? 'image'
                    : file_extension;
            }
        }
    },
    setValueCodeMirror(html, editor) {
        html = html ? html : "";
        editor.setValue(html);
        // this.autoFormatCodeMirror(editor, editor.getValue().split('\n').length);
    },
    autoFormatCodeMirror(editor, end_line) {
        editor.autoFormatRange({line: 0, ch: 0}, {line: end_line, ch: 0});
        editor.setSelection({line: 1, ch: 0});
    },
    destroyCodeMirror(editor) {
        editor.CodeMirror.toTextArea();
        document.getElementById("content").style.display = "none";
    },
    onValueChange(ev) {
      this.$emit("input", ev.detail);
    },
    getBase64(file) {
      return new Promise(function(resolve, reject) {
        let reader = new FileReader();
        reader.onload = function() { resolve(reader.result); };
        reader.onerror = reject;
        reader.readAsDataURL(file);
      });
    },
    clearCode(){
      this.redactorEl.source.setCode('');
    },
    uncommentOriginalStyles(html) {
        return html.replace(new RegExp(`<!--([\\s\\S]+?)`, 'gi'), '<style')
                   .replace(new RegExp(`\\([\\s\\S]+?)-->`, 'gi'), 'style>');
    },
    removeRedactorStyles(html) {
        return html.replace(new RegExp(`<style data-redactor-style([\\s\\S]+?)</style>`, 'gi'), '');
    },
    removeGeneratedFigure(html) {
        return html.replace(new RegExp(`<figure class="redactor-component redactor-component-active"([\\s\\S]+?)</span>`, 'gi'), '')
                   .replace(new RegExp(`<figure class="redactor-component([\\s\\S]+?)>`, 'gi'), '')
                   .replace(new RegExp(`<figure([\\s\\S]+?)>`, 'gi'), '')
                   .replace(/<\/figure>/g, '');
    },
    setValueRedactorCodeMirror(html) {
        // console.log(html);
        // this.setValueCodeMirror(html, this.tempEditor);
        this.setValueCodeMirror(html, this.editor);
    },
    sourceViewRedactor() {
        this.editor.focus();

        let html_body = this.uncommentOriginalStyles(document.querySelector('.redactor-styles').innerHTML);
        html_body = this.removeRedactorStyles(html_body);
        html_body = this.removeGeneratedFigure(html_body);

        // if (this.hasInsComponent) {
        //     html_body = this.editor.getValue();
        // }
        // let html =`${this.redactorHTML ? '<html>' : ''}
        // ${this.redactorHead ? '<head>' : ''}
        // ${this.redactorHead}
        // ${this.redactorHead ? '</head>' : ''}
        // ${this.redactorBodyTag ? '<body>' : ''}
        // ${html_body}
        // ${this.redactorBodyTag ? '</body>' : ''}
        // ${this.redactorHTML ? '</html>' : ''}`;

        let html = (this.redactorDoctypeHTML !== -1 ? '<!DOCTYPE html>' : '') + '\n' +
        (this.redactorHTML ? '<html>' : '') + '\n' +
        (this.redactorHead ? '<head>' : '') + '\n' +
        (this.redactorHead) + '\n' +
        (this.redactorHead ? '</head>' : '') + '\n' +
        (this.redactorBodyTag ? '<body>' : '') + '\n' +
        html_body +
        (this.redactorBodyTag ? '</body>' : '') + '\n' +
        (this.redactorHTML ? '</html>' : '');

        return html.replace(/^\s*[\r\n]/gm, '');
    },
    checkIfHTMLHasInsComponent(html) {
        if (this.searchFromCode(html, '<ins-') !== -1) {
            this.hasInsComponent = true;
        } else {
            this.hasInsComponent = false;
        }
    },
    searchFromCode(html, value) {
        let index = -1;
        if (html) {
            index = html.toLowerCase().indexOf(value);
        }
        return index;
    },
    getRedactorValue() {
        if (!this.sourceView && !document.querySelector('.redactor-source-view')) {
            let sourceCode = this.sourceViewRedactor();
            this.setValueRedactorCodeMirror(sourceCode);
        } else {
            this.setValueRedactorCodeMirror(this.editor.getValue());
        }

        return this.editor.getValue();
    },
    getEditorValue() {
        let value = this.editor.getValue();
        if (!this.sourceView && !document.querySelector('.redactor-source-view')) {
            value = document.querySelector('.redactor-styles').innerHTML;
        }

        return value;
    },
    visualEditorRedactor() {
        let editor_value = this.editor.getValue();
        let value = '';
        let self = this;

        if (!editor_value || editor_value === '\n') {
            value = '<p></p>';
        } else {
            let values_html = self.convertValues('html', editor_value);
            let values_style = self.convertValues('style', editor_value);
            let values_head = self.convertValues('head', values_style.body);
            editor_value = self.toggleRedactorUpdateEditorValue(editor_value, values_head.tags);

            let values_body = self.convertValues('body', values_style.body);
            editor_value = self.toggleRedactorUpdateEditorValue(editor_value, values_body.tags);
            editor_value = self.removeTag('html', editor_value);

            self.$store.commit('updateRedactorDoctypeHTML', self.searchFromCode(self.editor.getValue(), '<!doctype html>'));
            self.$store.commit('updateRedactorHTML', self.removeTag('html', values_html.original));
            self.$store.commit('updateRedactorBodyTag', self.removeTag('body', values_body.original));
            self.$store.commit('updateRedactorStyle', values_style.tags);
            self.$store.commit('updateRedactorHead', self.removeTag('head', self.uncommentOriginalStyles(values_head.original)));
            self.$store.commit('updateRedactorBody', self.removeTag('body', editor_value + self.uncommentOriginalStyles(values_body.original)));

            value = `${self.removeTag('style', values_style.converted) ? `<style data-redactor-style>${self.removeTag('style', values_style.converted)}</style>`: ''}${self.formatTags('style', editor_value).value}${self.removeTag('body', values_body.original)}`;
        }

        return value;
    },
    toggleRedactor(source_view) {
        this.sourceView = source_view;

        if (source_view) {
            if (document.querySelector('.redactor-styles').innerHTML.split("p").join() === "<,></,>") {
                this.setValueCodeMirror("", this.editor);
                this.setValueCodeMirror("", this.tempEditor);
            } else {
                this.setValueRedactorCodeMirror(this.sourceViewRedactor());
            }
        } else {
            document.querySelector('.redactor-styles').innerHTML = this.visualEditorRedactor();
        }

        if (this.hasInsComponent && this.firstLoadRedactor) {
            document.querySelector('.redactor-styles').innerHTML = "";
            setTimeout(() => { document.querySelector('.re-button.re-html').className += " redactor-button-disabled"; }, 100);
        }

        this.firstLoadRedactor = false;
    },
    toggleRedactorUpdateEditorValue(editor_value, tags) {
        for (let counter = 0; counter < tags.length; counter++) {
            editor_value = editor_value.replace('<' + tags[counter].tag + tags[counter].attribute + '>' + this.uncommentOriginalStyles(tags[counter].original) + '</' + tags[counter].tag + '>', '');
        }
        return editor_value;
    },
    generateStyleTags(tags) {
        let styles = '';

        for (let counter = 0; counter < tags.length; counter++) {
            styles += `<style${tags[counter].attribute}>${tags[counter].original}</style>\n`;
        }

        return styles;
    },
    convertValues(tag, value) {
        value = this.removeHTMLMarkers(value);
        let data = this.formatTags(tag, value);
        let converted = '';
        let original = '';
        let tags = [];

        if (data.index.length) {
            let tag_children = this.getTagChildren(tag, data, value);
            converted = tag_children.converted;
            original = tag_children.original;
            tags = tag_children.tags;
        }

        return {
            original: original,
            converted: converted,
            tags: tags,
            body: data.value
        };
    },
    getTagChildren(tag, data, value) {
        let converted = `<${tag}>`;
        let original = `<${tag}>`;
        let tags = [];

        for (let counter = 0; counter < data.index.length; counter++) {
            let substr_value = value.substr(data.index[counter].start, data.index[counter].end - data.index[counter].start);
            let temp_str = this.removeTag(tag, substr_value);

            tags.push({
                tag: tag,
                attribute: this.getTagAttributes(tag, substr_value),
                original: temp_str,
                converted: tag === "style" ? this.convertStyles(temp_str) : temp_str
            });

            original += temp_str;
            converted += this.convertStyles(temp_str);
        }

        converted += `</${tag}>`;
        original += `</${tag}>`;

        return {
            name: tag,
            original: original,
            converted: converted,
            tags: tags
        };
    },
    removeHTMLMarkers(value) {
        return value.replace('<span id="selection-marker-start" class="redactor-selection-marker"></span>', '')
                    .replace('<span id="selection-marker-end" class="redactor-selection-marker"></span>', '')
                    .replace('</span><span id="selection-marker-end" class="redactor-selection-marker"></span>', '')
                    .replace('<span id="selection-marker-start" class="redactor-selection-marker"></span>', '')
                    .replace(new RegExp(` data-redactor-style-cache="([\\s\\S]+?)"`, 'gi'), '');
    },
    convertString(value, delimeter) {
        let new_value = '';
        for (let counter = 0; counter < value.length; counter++) {
            new_value += delimeter;
        }

        return new_value;
    },
    convertStyles(value) {
        value = value.replace(/\{/g, ' { ').replace(/\}/g, ' } ').replace(/\s+/g, " ").trim().split(' } ');
        for (let counter = 0; counter < value.length; counter++) {
            value[counter] = '.redactor-styles ' + value[counter];
            if (value[counter].indexOf('.redactor-styles body ') === 0) {
                value[counter] = value[counter].replace('.redactor-styles body ', '.redactor-styles ');
            }
            value[counter] = value[counter].replace(/,/g, ', .redactor-styles ');
            value[counter] = value[counter].replace(/,\s\.redactor-styles\sbody\s/g, ', .redactor-styles ');
            value[counter] = value[counter].replace(/,\s\.redactor-styles\sbody,/g, ', .redactor-styles,');
            value[counter] = value[counter].replace(/\.redactor-styles\sbody,/g, '.redactor-styles,');
        }

        return value.join(' } ');
    },
    getTagAttributes(tag, value) {
        var start = value.indexOf('<') + 1;
        var end = value.indexOf('>') - 1;

        return value.substr(start, end).replace(tag, '');
    },
    removeTag(tag, value) {
        let original_value = value;

        if (value.indexOf(`<${tag}>`) > -1) {
            value = value.replace(`</${tag}>`, '').replace(new RegExp(`<${tag}>`, 'gi'), '');
        } else {
            value = value.replace(`</${tag}>`, '').replace(new RegExp(`<${tag}([\\s\\S]+?)>`, 'gi'), '');
        }

        if (!value.trim()) {
            value = original_value.replace(`<${tag}>`, '').replace(`</${tag}>`, '');
        }

        return value;
    },
    formatTags(tag, value) {
        let indices = [];
        let index = 0;
        // let counter = 0;
        let new_value = value;
        let orig_value = value;

        do {
            let start = 0, end = 0;
            start = value.indexOf(`<${tag}`);
            end = (value.indexOf(`</${tag}>`) + (`</${tag}>`).length);

            if (start < 0) {
                index = -1;
            } else if (value.indexOf(`</${tag}>`) < 0) {
                let temp_value = `${value.substr(0, start)}*${value.substr(start + 1)}`;
                end = temp_value.indexOf(`<${tag}>`) + (`<${tag}>`).length;
                let temp_str = value.substr(start, end - start);
                new_value = new_value.replace(temp_str, this.convertString(temp_str, ''));
                value = value.replace(temp_str, this.convertString(temp_str, '-'));
                indices.push({ start: start, end: end });
            } else {
                let temp_str = value.substr(start, end - start);
                new_value = new_value.replace(temp_str, this.convertString(temp_str, ''));
                value = value.replace(temp_str, this.convertString(temp_str, '-'));
                indices.push({ start: start, end: end });
            }

            if (end < start) {
                // delete no closing tags
                index = -1;
            }
            // counter += 1;
        } while (index > -1)

        if (tag === "style") {
            new_value = orig_value;
            for (let counter = 0; counter < indices.length; counter++) {
                let counter_str = String.fromCharCode(counter);
                new_value = new_value.substr(0, indices[counter].start) + `<!--${counter_str}` + new_value.substr(indices[counter].start + (`<!--${counter_str}`).length - 1);
                new_value = new_value.substr(0, indices[counter].end - (`${counter_str}-->`).length) + `${counter_str}-->` + new_value.substr(indices[counter].end - (`${counter_str}-->`).length + (`</${tag}`).length - 1);
            }
        }

        return {
            index: indices,
            value: new_value
        }
    }
  }
};
</script>

<style lang="scss" src="./Redactor.scss">    
</style>
