<template>
  <div class="grid-x grid-padding-x">
    
    <div class="cell large-7 medium-12 small-12">
      <InsInputWrap
        v-bind:label="label"
        v-bind:value="value"
        v-bind:field="field"
        v-bind:placeholder="placeholder"
        v-on:input="onValueChange"
        v-bind:has-error="hasError"
        v-bind:error-message="errorMessage" />
    </div>

    <div class="cell large-5 medium-12 small-12">
      <pre class="liquid-wrap"><span v-pre>{{</span> {{ liquidTag }} <span v-pre>}}</span></pre>
    </div>

  </div>
</template>

<script>
// general resources
import InsInputWrap from '../../InsInputWrap/InsInputWrap.vue';
import Validators from '../../../mixins/Validators';

export default {
  name: "GssInputField",
  props: {
    label: String,
    liquidTag: String,
    placeholder: String,
    value: String,
    field: String,
    required: Boolean
  },
  mixins: [Validators],
  components: {
    InsInputWrap,
  },
  data() {
    return {
      hasError: false,
      errorMessage: ''
    }
  },
  methods: {
    onValueChange(value) {
      this.$emit("input", value);
      this.checkInput(value);
    },
    checkInput(value){

      if (this.required && !value) {
        return this.toggleError(true, `${this.label} is required.`);
      } else if (value) {

        let error;
        let message;

        switch (this.field) {
          case 'email':
            error = this.validateEmail(value);
            message = error ? 'Invalid email address.' : '';
            break;

          case 'url':
            error = this.validateURL(value);
            message = error ? 'Invalid url.' : '';
            break;
        }

        this.toggleError(error, message);

      } else {
        this.toggleError(false, '');
      }
    },
    toggleError(error, message){
      this.hasError = error ? true : false;
      this.errorMessage = message;
      this.$emit("fieldError", this.hasError);
    }

  }
}
</script>

<style lang="scss" src="./GssInputField.scss">
</style>