<template>
<div class="grid-container-full pages">
  <div class="grid-x">
    <div class="large-12 cell content-blocks-container">

      <div v-show="!hasData">
        <ins-card steady>
          <h2 class="title-page h2desc">Welcome to Templates</h2>
          <p class="pdesc">Templates help you save time by re-using common site elements (for example, your header and footer) instead of having them individually inserted in each page.</p>
        </ins-card>

        <div class="pages-bottom">
          <h2 class="pages-title-secondary h2desc">You don't have any templates yet.</h2>
          <p class="pdesc">Would you like to create your first template?</p>

          <ins-button label="ADD NEW TEMPLATE" solid
            v-on:onClickInsButton="goToAddNewTemplate">
          </ins-button>
        </div>
      </div>
      
      <div class="header-buttons no-search">
        <ins-button label="ADD NEW TEMPLATE" solid
          color="primary" size="normal"
          v-show="hasData"
          v-on:onClickInsButton="goToAddNewTemplate">
        </ins-button>
      </div>
      
      <div class="ins-table-container" v-show="hasData">
        <ins-base-table ref="insBaseTable" without-search
          v-on:onPaginationChange="tablePaginationHandler"
          v-on:onTableBulkAction="tableBulkActionHandler"
          v-on:onTableRowAction="tableRowActionHandler">
        </ins-base-table>
      </div>
      <div class="spacer"></div>
      <div class="spacer"></div>
    </div>
  </div>
</div>
</template>

<script>
// general resources
import InsitesMixins from '../../mixins/InsitesMixins';
import VclMixins from '../../mixins/VclMixins';
import { getAllTemplatesJSON } from '../../services/SiteManagerServices';
import { deleteVclFile } from '../../services/VclService';

export default {
  name: "ListTemplates",
  mixins: [InsitesMixins, VclMixins],
  data() {
    return {
      hasData: true,
      insBaseTable: "",
      tableHeaders: [],
      pageNumber: 1,
      pageSize: 10,
      headers: {},
      continueDeletion: false
    }
  },
  methods: {
    goToAddNewTemplate() {
      this.$store.commit("setTemplateFormData", { data: {}, action: "New" });
      this.$router.push({ name: "ManageTemplate" });
    },
    goToManageTemplate(data, action) {
      InsitesUtil.renderPage(`${action} Template`, `#/edit-template/${data.id}`);
      this.$store.commit("setTemplateFormData", { data, action });
      this.$router.push(`/edit-template/${data.id}`);
    },
    tablePaginationHandler(event) {
      this.$refs.insBaseTable.loadingScreen = true;
      this.pageSize = event.detail.pageSize;
      this.pageNumber = event.detail.pageNumber;
      this.fetchDataFromSrc();
    },
    tableBulkActionHandler(event) {
      switch (event.detail.action) {
        case "Delete":
          this.archiveSelection(event.detail.selections);
          break;
      }
    },
    tableRowActionHandler(event) {
      let data = event.detail.data;
      let action = event.detail.action;

      switch (event.detail.action) {
        case 'Delete':
          this.archiveItem(event.detail.data);
          break;

        case 'Edit':
          this.goToManageTemplate(data, action);
          break;
      }
    },
    archiveItem(data) {
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="Are you sure you want to permanently delete this template? Pages that use this template will be affected.">
          </ins-loader>`,
        type: "",
        showCancelButton: true,
        confirmButtonClass: "negative",
        confirmButtonText: "Delete"
      }).then(result => {
        if (result.value){
          this.deleteHandler(data);
        }
      });
    },
    async deleteHandler(pageData) {
      let vclPayload = this.constructVclPayload(pageData.rawData);
      
      let vclRes;
      vclRes = await deleteVclFile(vclPayload);
      if (vclRes.state){
        if (vclRes.results.message.includes("Successfully")){
          this.deleteItem(pageData.id);
          return;
        }
      }
      toastIt("error", "Failed to delete template. File may already have been deleted.", InsitesUtil.ops());
    },
    archiveSelection(selections) {
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="Are you sure you want to permanently delete these templates? Pages that use these templates will be affected.">
          </ins-loader>`,
        type: "",
        showCancelButton: true,
        confirmButtonClass: "negative",
        confirmButtonText: "Delete"
      }).then(result => {
        if (result.value) {
          let counter = 0;
          this.continueDeletion = true;
          selections.forEach(data => {

            if (this.continueDeletion){

              this.deleteHandler(data);
              counter++;

              if (selections.length === counter) {
                this.continueDeletion = false;
              }
            }
          });
        }
      });
    },
    deleteItem(id){
      let url = `/insites/api/delete-layout.json`;
      // let url = `/insites/api/delete-layout.json?id=${id}`;
      let templateFormData = {};
      templateFormData.payload = {"id" : id };
      this.$http.post(url, templateFormData, {headers: this.headers})
        .then(this.deleteResHandler)
        .catch(this.deleteErrHandler);
    },
    deleteResHandler(res){
      if (!this.continueDeletion){
        if (res.data.includes){
          if (res.data.includes('500 - Server Error') ||
          res.data.includes('parse_json failed')){
            this.deleteErrHandler('500 POS Server Error');
          }
        } else {
          toastIt('success', `Template has been deleted.`, InsitesUtil.ops());
          this.$store.dispatch('fetchSiteTemplates', this.$store);  // update template store  
          this.fetchDataFromSrc();
        }
      }
    },
    deleteErrHandler(error){
      console.error(error);
      toastIt("error", "Failed to delete template. Please try again.", InsitesUtil.ops());
    },
    async fetchDataFromSrc() {
      let self = this;

      self.setInsBaseTableLoader(
        "Processing Your Data",
        "Just a moment, we're fetching and processing your data.",
        "processing"
      )

      let response = await getAllTemplatesJSON(self.pageNumber, self.pageSize);
      if (response.state) {
        self.resHandler(response.results);
      } else {
        self.errorHandler(response.results);
      }
    },
    errorHandler(error){
      console.error(error);
      let type = this.continueDeletion ? 'archive':'load';
      let message = `Failed to ${type} data. Please try again.`;
      this.continueDeletion = false;
      this.setInsBaseTableLoader('', message, 'error');
    },
    resHandler(res) {
      let tableData = res.admin_liquid_layouts;
      if (tableData.results.length) {
        this.$refs.insBaseTable.totalCount = tableData.total_entries;
        this.bindTableData(tableData.results);
      } else {
        this.hasData = false;
      }
    },
    bindTableData(tableData) {
      this.$refs.insBaseTable.tableData = tableData.map(this.constructTableData);
      this.hasData = true;
      this.$refs.insBaseTable.loadingScreen = false;
    },
    setInsBaseTableLoader(title, message, icon){
      this.$refs.insBaseTable.loadingScreen = true;
      this.$refs.insBaseTable.loaderTitle = title;
      this.$refs.insBaseTable.loaderMessage = message
      this.$refs.insBaseTable.loaderIcon = icon;
    },
    setupInsBaseTable(){
      this.$refs.insBaseTable.bulkActions = ["Delete"];
      this.$refs.insBaseTable.rowActions = ["Edit", "Delete"]; 
      this.$refs.insBaseTable.tableHeaders = [
        this.constructTableHeader("Path"),
        this.constructTableHeader("Status", "string", false, true)
      ];
    },
    constructTableHeader(label, type = "string", sortable, hasTag, hasLink, hasColumnAction, hasImage){
      return { label, type, sortable, hasTag, hasLink, hasColumnAction, hasImage }
    },
    constructTableData(item){
      return {
        rawData: item,
        id: item.id,
        "Path": item.path,
        "Status": "enabled"
      }
    }
  },
  mounted () {
    this.setupInsBaseTable();
    this.fetchDataFromSrc();
    this.headers = this.apiHeader();
  }
}
</script>
<style lang="scss" src="./ListTemplates.scss">
</style>