import GssFormModel from '../models/GssFormModel';

const GssStore = {
  state: {
    gssData: GssFormModel,
    formID: '',
    formMethod: 'PUT',
    fetchingGssData: true,
    failedToLoadGssData: true
  },
  mutations: {
    setGssData(state, data){
      state.gssData = data;
      state.formID = state.gssData.id;
      state.fetchingGssData = false;
    },
    setGssFormMethod(state, data){
      state.formMethod = data;
    },
    setFetchingGssData(state, data){
      state.fetchingGssData = data;
    },
    setFailedToLoadGssData(state, data){
      state.failedToLoadGssData = data;
    }
  },
  actions: {
    fetchGssData(context) {
      let url = '/insites/api/get-global-content-data.json';
      this._vm.$http.get(url)
        .then(res => {
          let results = res.data.items.results;
          if (results.length) {
            context.commit("setGssData", results[0]);
          } else {
            context.commit('setGssFormMethod', 'POST');
            context.commit('setFetchingGssData', false);
          }
        }).catch((error) => {
          console.error('Failed to load Global Content on background', error);
          context.commit('setFailedToLoadGssData', true);
        });
    }
  }
}
export default GssStore;