<template>
  <ins-select ref="insSelectEl"
    v-bind:label="label"
    v-bind:value="value"
    v-bind:placeholder="placeholder"
    v-bind:disabled="disabled"
    v-bind:readonly="readonly"
    v-bind:button="button"
    v-bind:small="small"
    v-bind:searchable="searchable"
    v-bind:multiple="multiple"
    v-bind:has-error="hasError"
    v-bind:error-message="errorMessage"
    v-bind:with-dynamic-option="withDynamicOption"
    v-bind:dynamic-placeholder="dynamicPlaceholder"
    v-on:onSubmitOption="onSubmitOptionHandler"
    @valueChange="onValueChange">
    <slot></slot>
  </ins-select>
</template>

<script>
export default {
  name: "InsSelectWrap",
  props: {
    label: String,
    value: String,
    placeholder: String,
    dynamicPlaceholder: String,
    disabled: Boolean,
    readonly: Boolean,
    button: Boolean,
    small: Boolean,
    searchable: Boolean,
    multiple: Boolean,
    withDynamicOption: Boolean,
    hasError: Boolean,
    errorMessage: String,
    required: Boolean
  },
  methods: {
    onValueChange(ev) {
      let self = this;
      self.$emit("input", ev.detail);
      this.checkInput(ev.detail)
    },
    onSubmitOptionHandler(event){
      this.$emit('onSubmitOption', event.detail);
    },
    checkInput(value){
      if (this.required && !value) {
        return this.toggleError(true, `${this.label} is required.`);
      } else {
        this.toggleError(false, '');
      }
    },
    toggleError(error, message){
      
      this.$refs.insSelectEl.hasError = error ? true : false;
      this.$refs.insSelectEl.errorMessage = message;

      this.$emit("fieldError", {
        error: error ? true : false,
        target: this
      });
    }
  }
};

</script>
<style></style>