import { mapState } from 'vuex';

export default {
    data(){ return { gitMessage: '', gitOverwrite: false } },
    computed: mapState({
      currentUser: state => state.VclStore.currentUser,
      vclMasterData: state => state.VclStore.vclMasterData,
      vclInitialized: state => state.VclStore.vclInitialized
    }),
    watch: {
        vclInitialized(newState, oldState){
            if (newState) {
                this.checkCurrentUserVCL();
            };
        }
    },
    methods: {
        constructVclPayload(file){
            return {
                file,
                git: {
                    overwrite: this.gitOverwrite,
                    message: this.gitMessage,
                    repo: this.vclMasterData.info.git_url
                },
                cred: this.currentUser.insites_vcl_user_profile.properties
            }
        },
        vclPromptCommitMessage(){
            return this.vclPrompt(
                "Please enter your commit message.",
                "Publish", "textarea"
            );
        },
        vclPrompt(text, confirmButtonText, input){
            return swal({
                text, confirmButtonText, input,
                html: `
                <ins-loader
                    state-icon="warning"
                    icon-color="warning"
                    state-message="${text}">
                </ins-loader>`,
                width: 330,
                showCancelButton: true
            }).then(result => {
                return result;
            });
        },
        vclInvalidPrompt(text){
            return swal({
                text, 
                html: `
                <ins-loader
                    state-icon="warning"
                    icon-color="warning"
                    state-message="${text}">
                </ins-loader>`,
                width: 330
            }).then(() => {
                InsitesUtil.goBack();
            });
        },
        resetVclData(){
            this.gitMessage = "";
            this.gitOverwrite = false;
        },
        checkCurrentUserVCL() {
            if (!this.currentUser.insites_vcl_user_profile ||
                this.currentUser.insites_vcl_user_profile.properties.insites_key === 'deleted') {
                this.vclInvalidPrompt('Your Version Control is enabled but your account is not yet configured.');
            }
        }
    },
    mounted() {
        if (this.vclInitialized) {
            this.checkCurrentUserVCL();
        }
    }
}
  