<template>
<div class="grid-container-full sm-list-content-block-form sm-content-block" v-if="contentBlockFormDataChecked && vclInitialized">
  <div class="grid-x">
    <div class="large-12 medium-12 small-12 cell content-blocks-container">
      <ins-card steady v-if="!resetingData">
          <h2>Content Block Details</h2>
            <FMInputWrap
              id="contentBlockName"
              maxlength="50"
              label="Content Block Path*"
              v-bind:liquid-tag="contentBlockData.path"
              v-bind:readonly="formType === 'view' || formType === 'edit'"
              v-bind:disabled="formType === 'edit'"
              v-model="tempName"
              v-on:input="onValueChange"
              v-on:fieldError="FMFormInvalidHandler"
              v-on:keyup.native="validateInput"
              v-bind:errorMessage="errorMsg"
              field="text"
              v-bind:required="'true'"
            />

        <Redactor label="Content*" ref="redactorEl"
          v-bind:source-code="contentBlockData.content"
          v-bind:readonly="formType === 'view'"
          v-on:fieldError="FMFormInvalidHandler"
          v-on:keyup.native="valdidateRedactorInput"
          v-bind:errorMessage="redactorErrorMsg"
          field="text"
          v-bind:required="'true'" />
      </ins-card>
      <div class="spacer"></div>
      
      <div class="grid-x grid-margin-x">
        <div class="large-6 medium-4 small-12 small-order-2 medium-order-1 cell footer-btn-container"
          v-if="formType === 'edit'">
          <ins-button label="DELETE"
            color="information-1"
            size="normal"
            v-bind:loading="deletingItem"
            v-bind:disabled="saveLoading || deletingItem"
            v-on:onClickInsButton="archiveItem"
            v-if="formType === 'edit'">
          </ins-button>
        </div>

        <div v-bind:class="`${formType === 'add' ? 'large-12 medium-12' : 'large-6 medium-8'} small-12 small-order-1 medium-order-2 cell text-right footer-btn-container`">
          <ins-button label="CANCEL" color="primary" outlined size="normal"
            v-on:onClickInsButton="cancelContentBlockHandler"
            v-bind:disabled="saveLoading || deletingItem">
          </ins-button>

          <ins-button 
            solid
            color="primary" size="normal"
            v-if="formType === 'edit' || formType === 'add'"
            v-bind:label="formType === 'edit' ? 'UPDATE' : 'SAVE'"
            v-bind:options="this.vclMasterData.info.is_enabled ? 'Save with commit message':''"
            v-bind:disabled="saveLoading || deletingItem"
            v-bind:loading="saveLoading"
            v-on:onClickInsButton="publishHandler"
            v-on:onClickInsButtonOption="runPrompt">
          </ins-button>          
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
// general resources
import { each } from "lodash";
import { mapState } from 'vuex';

import InsitesMixins from '../../mixins/InsitesMixins';
import VclMixins from '../../mixins/VclMixins';
import SiteManagerService from '../../mixins/SiteManagerService';
import InsInputWrap from "../../components/InsInputWrap/InsInputWrap.vue";
import Redactor from '../../components/Redactor/Redactor.vue';
import { 
  createVclFile,
  updateVclFile,
  deleteVclFile
} from '../../services/VclService';

// site settings module resources
import FMInputWrap from "../../components/_SiteManager/FMInputWrap/FMInputWrap.vue";
import { getContentBlockByIDJSON } from '../../services/SiteManagerServices';
import { sidebarMenuSiteManagerRoute, listContentBlocksRoute, manageContentBlockRoute } from "../../models/InsSiteManagerRoutes.js";

export default {
  name: "NewContentBlock",
  mixins: [SiteManagerService, InsitesMixins, VclMixins],
  components: {
    Redactor,
    InsInputWrap,
    FMInputWrap
  },
  data() {
    return {
      tempName: "",
      errorMsg: "Content block name is required.",
      redactorErrorMsg: "Content is required.",
      postURL: "/insites/api/create-partial.json",
      deleteURL: "/insites/api/delete-partial.json?id=",
      contentBlockFormDataChecked: false,
      submittingForm: false,
      deletingItem: false,
      hasInvalidField: false,
      saveAndCreateAnother: false,
      saveLoading: false,
      saveAndCreateAnotherLoading: false,
      resetingData: false,
      headers: {},
      missing_fields: 0,
      tempWholePath: ''
    }
  },
  computed : mapState({
    contentBlockData: state => state.ContentBlockStore.contentBlockData,
    formType: state => state.ContentBlockStore.formType
  }),
  methods: {     
    valdidateRedactorInput() {
      if (!this.$refs.redactorEl.getEditorValue() || this.$refs.redactorEl.getEditorValue() === "<p></p>\n" || this.$refs.redactorEl.getEditorValue() === "<p></p>") {
        $('#content').closest('.redactor').attr("has-error", true);
        this.FMFormInvalidHandler(true);
        return 1;
      } else {
        $('#content').closest('.redactor').attr("has-error", false);
        this.missing_fields += 0;
        !this.missing_fields ? this.FMFormInvalidHandler(false) : '';
        return 0;
      }
    },
    FMFormInvalidHandler(event){
      this.hasInvalidField = event;
    },
    onValueChange(value) {
      this.$emit("input", InsitesUtil.removeWhitespace(value));
      if(this.formType === 'add') {
        this.contentBlockData.path = this.setModuleName(`modules/ins_site_manager/${InsitesUtil.removeWhitespace(this.tempName)}`);
        this.contentBlockData.physical_file_path = this.setPhysicalPath("partials", this.setModuleName(this.tempName));
      } else {
        this.contentBlockData.path = this.contentBlockData.path;
        this.contentBlockData.physical_file_path = this.contentBlockData.physical_file_path;              
      }
    },
    saveAndCreateAnotherHandler(){
      this.saveAndCreateAnother = true;
      this.formValidate(); 
    },
    formValidate() {
      this.missing_fields = 0;
      let _self = this;

      this.contentBlockData.content = this.$refs.redactorEl.getRedactorValue();      
      if(InsitesUtil.formValidate(Array.from($(".ins-form-field, .ins-select-value-input, .ins-textarea-field")))) {
        this.FMFormInvalidHandler(false);
        return true;
      }
      return false;
    },    
    submitForm(parsedData, word){
      this.submittingForm = true;
      this.saveLoading = true;
      let pageData = { payload: JSON.parse(parsedData) }
      if (this.vclMasterData.info.is_enabled){
        this.runVcl(pageData, word);
      } else {
        this.runMutation(pageData, word);
      }
    },
    async runVcl(pageData, word){
      let vclPayload = this.constructVclPayload(pageData.payload);
      let vclRes;
      
      if (this.formType === "edit"){

        if (this.tempWholePath !== vclPayload.file.physical_file_path){
          vclPayload.git.rename_file = this.tempWholePath;
        }

        vclRes = await updateVclFile(vclPayload);

      } else {
        vclRes = await createVclFile(vclPayload);
      }
      if (vclRes.state){
        if (vclRes.results.message.includes("File already exist")) {
          this.overwriteHandler(pageData, word);
          return;
        } else if (vclRes.results.message.includes("Successfully")){
          this.runMutation(pageData, word);
          return;
        }
      }
      this.errorHandler(vclRes.results.response.data.message);
    },
    async overwriteHandler(pageData, word){
      let prompt = await this.vclPrompt(
        "File already exist. would you like to overwrite?",
        "Overwrite");
        
      if (prompt.value){
        this.gitOverwrite = true;
        this.runVcl(pageData, word);
      }
      this.resetFormStatus();
    },
    publishHandler(event){
        this.submittingForm = true;
        this.saveLoading = true;
        if (this.formValidate()){
          let parsedData = JSON.stringify(this.contentBlockData);
          let word = this.formType === 'edit' ? 'updated' : 'added';
          this.submitForm(parsedData, word);
        } else {
          this.submittingForm = false;
          this.saveLoading = false;
        }
    },
    async runPrompt(){
      let prompt = await this.vclPromptCommitMessage();
      if (prompt.value){
        this.gitMessage = prompt.value;
        this.publishHandler();
      }
    },
    clearData(){
      this.tempName = "";
      this.$refs.redactorEl.clearCode();
      this.contentBlockData.path = "";
      this.contentBlockData.content = "";
      this.contentBlockData.physical_file_path = "";
      this.resetFormStatus();
    },
    resetFormStatus(){      
      this.submittingForm = false;
      this.deletingItem = false;
      this.saveAndCreateAnother = false;
      this.saveAndCreateAnotherLoading = false;
      this.saveLoading = false;
    },
    runMutation(pageData, word){
      let type = this.formType === 'add' ? 'create':'update';
      let url = `/insites/api/${type}-partial.json`
      this.$http.post(url, pageData, {headers: this.headers})
        .then(res => this.resHandler(res, word, pageData.payload))
        .catch(this.errorHandler);
    },
    resHandler(res, word, pageData){
      if (res.data.includes) {
        if (res.data.includes('500 - Server Error') ||
            res.data.includes('parse_json failed')){
          this.errorHandler('500 POS Server Error');
        }
      } else {
        this.clearData();
        let message = `Content Block has been ${word}.`
        if (word === 'added') {
          this.contentBlockData.id = res.data.admin_liquid_partial_create.id;
          this.$store.commit('pushFormType', 'edit');
        }
        toastIt('success', message, InsitesUtil.ops());
      }
      this.resetVclData();
      this.submittingForm = false;
      this.saveLoading = false;
      
    },
    errorHandler(error){
      console.error(error);
      this.resetVclData();
      this.resetFormStatus();
      toastIt("error", "Failed to save content block. Please try again.", InsitesUtil.ops());
    },
    cancelContentBlockHandler(){
      InsitesUtil.renderPage('ContentBlocks', '#content-blocks');
      this.$router.push('/content-blocks');
    },
    getRedactorData(code) {
      this.contentBlockData.content = code;
    },
    setInsRendererLabel(){
      let insRendererEl = document.querySelector('ins-renderer');
      if (insRendererEl){

          if (this.formType === "add" || this.formType === "") {
            insRendererEl.updateRouteLabel('Add Content Block');
            this.$store.commit("pushFormType", "add");
          } else if (this.formType === "edit") {
            insRendererEl.updateRouteLabel('Edit Content Block');
            this.$store.commit("pushFormType", "edit");
          } else if (this.formType === "view") {
            insRendererEl.updateRouteLabel('View Content Block');
            this.$store.commit("pushFormType", "view");
          }

      } else {
        setTimeout(() => {
          this.setInsRendererLabel();
        }, 300);
      }

      this.setFormType();
    },
    setFormType() {
      if (this.formType === 'add') {
        this.postURL = `/insites/api/create-partial.json`;
      } else if (this.formType === 'edit') {
        this.postURL = `/insites/api/update-partial.json`;
      } else if (this.formType === 'view') {
          this.postURL = "";
      }
    },
    archiveItem(value) {
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="Are you sure you want to delete this content block?">
          </ins-loader>`,
        type: "",
      width: 330,
        showCancelButton: true,
        confirmButtonClass: 'negative',
        confirmButtonText: "Delete"
      }).then(result => {
        if (result.value){
          this.deleteHandler(this.contentBlockData);
        }
      });
    },
    async deleteHandler(pageData) {
      this.deletingItem = true;
      let vclPayload = this.constructVclPayload(pageData);
      
      let vclRes;
      vclRes = await deleteVclFile(vclPayload);
      if (vclRes.state){
        if (vclRes.results.message.includes("Successfully")){
          this.deleteItem(pageData);
          return;
        }
      }
      toastIt("error", "Failed to delete content block. File may already have been deleted.", InsitesUtil.ops());
    },
    deleteItem(value) {
      let url = `${this.deleteURL}`;

      this.$http
        .post(url, value.id)
        .then(response => {
          if (!this.continueDeletion){
            toastIt("success", "Content Block has been deleted.", InsitesUtil.ops());
            this.$router.push('/content-blocks');
            this.deletingItem = false;
          }
        }).catch(error => {
          this.continueDeletion = false;
          toastIt("error", "Failed to delete content block. Please try again.", InsitesUtil.ops());
          this.deletingItem = false;
        });
    },
    errorContentBlock() {
      let self = this;
      self.$router.push({ name: 'ListContentBlocks' });
      self.toastInvalidIDNotification('content block');
    },
    setEditContentBlockFormData(data) {
      let result = {
        "Data": data,
        "Last Modified": null,
        "Name": this.capitalize(data.path.substring(data.path.lastIndexOf('/')+1)),
        "Status": "enabled",
        "id": data.id
      }
      this.$store.commit("pushPartialData", result);
      this.tempName = this.contentBlockData.path;
      // this.$store.commit('setContentBlockData', result);
    },
    async editContentBlock() {
      let self = this;
      let response = await getContentBlockByIDJSON(self.checkURLParamsID());
      
      return new Promise(resolve => {
        if (response.results.admin_liquid_partials.total_entries) {
          self.setEditContentBlockFormData(response.results.admin_liquid_partials.results[0]);
          resolve({ state: true });
        } else {
          resolve({ state: false });
        }
      });
    },
    setBreadCrumbs() {
      let crumbs = [];
      crumbs = [sidebarMenuSiteManagerRoute, listContentBlocksRoute];

      if (this.$route.name === "EditContentBlock") {
          this.$store.commit("pushFormType", "edit");
        crumbs.push({
          link: `#/edit-content-block/${this.checkURLParamsID()}`,
          app: false,
          withSubmenu: false,
          label: 'Edit Content Block'
        });
      } else if (this.$route.name === "ManageContentBlock") {
            this.$store.commit("pushFormType", "add");
        crumbs.push(manageContentBlockRoute);
      }
      InsitesUtil.setBreadCrumbs(crumbs);
    },
  },
  async mounted () {
    let self = this;
    let response = {};

    if (self.$route.name === "EditContentBlock") {
      this.$store.commit("pushFormType", "edit");
      if (self.contentBlockData.id) {
        self.tempName = self.contentBlockData.path;
        response.state = true;
      } else if (self.checkURLParamsID()) {
        response = await self.editContentBlock();
      }
    } else if (self.$route.name === "ManageContentBlock") {
      this.$store.commit("pushFormType", "add");
      response.state = true;
    }

    if (response.state) {
      self.tempWholePath = self.contentBlockData.physical_file_path;
      self.headers = self.apiHeader();
      self.setBreadCrumbs();
      // self.setInsRendererLabel();
      self.contentBlockFormDataChecked = true;
    } else {
      self.errorContentBlock();
    }
  }
}
</script>
<style lang="scss" src="./ManageContentBlocks.scss"></style>