<template>
  <div class="accordion-wrap">
    <div class="accordion-header">
      <span class="icon-caret-right"
        v-on:click="toggleBody"
        ref="toggler">
      </span>
      <div v-on:click="toggleBody">
        <slot name="header"></slot>
      </div>
    </div>

    <div class="accordion-body">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "Accordion",
  props: {
      opened: Boolean
  },
  methods: {
    toggleBody(e){
      let accHeader = $(e.target).closest('.accordion-header')
      accHeader.next('.accordion-body').slideToggle();
      accHeader.toggleClass('opened');
    }
  },
  mounted() {
      if(this.opened) {
        let e = {
          target: this.$refs.toggler
        }
        this.toggleBody(e);
      }  
  }
};
</script>
<style lang="scss" src="./Accordion.scss">
</style>