import { mapState } from 'vuex';

// file manager module resources
import InsitesMixins from './InsitesMixins';
import SiteManagerService from './SiteManagerService';
import ComponentManager from './ComponentManager';
import { getAllDeletedAssetsJSON, getDeletedAssetsByFolderDataJSON, getDeletedAssetsDataJSON } from '../services/SiteManagerServices';

export default {
    name: "ArchiveFileManager",
    mixins: [SiteManagerService, ComponentManager, InsitesMixins],
    data() {
        return {
            formData: {
                form_configuration_name: "modules/insites_site_manager/insites_assets",
                parent_resource_id: "modules/insites_site_manager/insites_assets",
                form: {
                    properties: {
                        is_enabled: "false"
                    }
                }
            },
            actionUrl: "/api/customizations",
            headers: ""
        }
    },
    computed: mapState({
        selectedFolder: state => state.FileManagerStore.selected_folder,
        selectedFolderData: state => state.FileManagerStore.selected_folder_data,
        selectedFile: state => state.FileManagerStore.selected_file,
        listArchive: state => state.FileManageStore.listArchive
    }),
    methods: {
        deleteFileCustomizationData() {
            let self = this;
            let method = self.selectedFile.id ? 'patch' : 'post';
            let url = `${self.actionUrl}${typeof self.selectedFile.id === "undefined" ? '' : `/${self.selectedFile.id}`}`;
            self.formData.form.properties.is_enabled = "false";

            if (method === 'post') {
                self.formData.form.properties.file_name = self.selectedFile.file_name;
                self.formData.form.properties.direct_link = self.selectedFile.direct_link;
                self.formData.form.properties.folder = self.selectedFile.folder;
                self.formData.form.properties.physical_path = self.selectedFile.physical_path;
                self.formData.form.properties.tags = [];
            }

            self.$http[method](url, self.formData, { headers: self.headers })
                .then(() => {
                    toastIt('success', `${self.selectedFile.file_name} has been archived`, self.ops);
                    self.updateNodeTreeSelectedFolder(self.selectedFolderData);   
                    self.$store.commit('resetFileManagerSelectedFile');
                }).catch(error => {
                    console.error('Error: File was not archived. Please try again.', error);
                    toastIt('error', 'File was not archived. Please try again.', self.ops);
                });
        },
        async showArchiveList() {
            let archiveList = await getAllDeletedAssetsJSON();

            this.$store.commit("updateFileManagerSearchResults", archiveList.results.items.results);
            this.$store.commit("updateSearchKeyword", "");
            this.$store.commit("updateListArchiveFiles", true);
        },
        restoreCustomizationData() {
            let self = this;
            let url = `${this.actionUrl}/${this.selectedFile.id}`;

            this.formData.form.properties.is_enabled = "true";

            this.$http.patch(url, this.formData, { headers: self.headers })
                .then(response => {
                    toastIt('success', `${self.selectedFile.file_name} has been restored`, self.ops);
                    self.updateNodeTreeSelectedFolder(self.selectedFolderData);
                    self.$store.commit('resetFileManagerSelectedFile');
                }).catch(error => {
                    console.error('Error: File was not restored. Please try again.', error);
                    toastIt('error', 'File was not restored. Please try again.', InsitesUtil.ops());
                });
        },
        async checkArchivedFile(file, path) {
            let self = this;
            let response = await getDeletedAssetsDataJSON('physical_path', path);

            return new Promise(resolve => {
                if(response.state) {
                    if (window.location.hostname === "localhost") {
                        let local_response = self.localhostAssetManagerGetFileData(self.convertToPhysicalPath(`${self.selectedFolder}/${file.name}`), response);
                        if (local_response.id) {
                            resolve({ state: true, results: [local_response] });
                        } else {
                            resolve({ state: true, results: local_response });
                        }
                    }

                    resolve({ state: true, results: response.results.items.results });
                } else {
                    console.log("ERROR: failed to get archived data.", response.results)
                    return false;
                }
            });
        }
    },
    mounted() {
        this.headers = this.apiHeader();
    }
}
