const api_url = "/insites/api/";
import { processRequestAndResponse } from "./CoreServices.js";

export async function getAllPagesJSON(keyword = "", status = "published", page = 1, size = 10) {
    let url = `${api_url}get-all-pages-data.json?keyword=${keyword}&status=${status}&page=${page}&size=${size}`;
    return await processRequestAndResponse(url, 'get');
}

export async function getPageByIDJSON(id, page = 1, size = 10) {
    let url = `${api_url}get-all-pages-data.json?id=${id}&page=${page}&size=${size}`;
    return await processRequestAndResponse(url, 'get');
}

