<template>
    <div class="grid-container-full fm-list">
        <div class="medium-12 cell content-blocks-container fm-list-display">
            <!-- list empty -->
            <div v-if="currentItemList === ''">
                <ins-loader
                state-title="List"
                state-message="Please click a folder on the left">
                </ins-loader>
            </div>
            <!-- /list empty -->

            <!-- preloader + main list -->
            <div v-else>
              <!-- preloader -->
              <div v-if="loading">
                <ins-loader
                state-title="Processing"
                :state-message="preloaderMessage"
                state-icon="processing">
                </ins-loader>
              </div>
              <!-- /preloader -->
              <!-- mail list -->
              <div v-else>
              <!-- fm-list-display-header-->
              <p class="search-result-notif" v-if="searchKeyword !== ''"><strong>Search results for:</strong>  <i> {{ searchKeyword }}</i></p>
              <p class="search-result-notif" v-if="listArchive"><strong>Archived Files:</strong></p>
              <div class="grid-x fm-list-header hide-for-small-only"
                v-if="showList() && fileList.length > 0">
                <div class="cell medium-auto">
                <!-- <p @click="toggleSort()">Name
                  <span><i :class="sortAsc ? 'icon-keyboard-arrow-down' : 'icon-keyboard-arrow-up'"></i></span>
                </p> -->
                  <p>File Name</p>
                </div>
                <div class="cell medium-auto" v-if="toggleView === 'list' && searchKeyword !== ''  || listArchive">
                    <p>Path</p>
                </div>
                <div class="cell medium-auto">
                  <p>Date Added</p>
                </div>
                <div class="cell medium-auto" v-if="toggleView === 'list' && searchKeyword !== '' && searchBy === 'tags'">
                  <p>Tags</p>
                </div>
              </div>
              <!-- /fm-list-display-header-->
              <!-- fm-list-main -->
              <!-- selected folder files -->
              <ul class="fm-list-main"
                v-if="searchKeyword === '' && !listArchive"
                v-bind:class="[toggleView, {'grid-x' : toggleView === 'grid'}]">
                <!-- <li class="grid-x">
                  <div class="cell medium-auto">
                  <p><span><i class="icon-arrow-up"></i></span> ...</p>
                  </div>
                </li> -->

                <li class="file-details up-folder" v-if="!checkIfRootFolder()" @click="fileParentFolderHandler()"><p><span class="icon-arrow-up"></span>...</p></li>

                <li class="file-details"
                  v-bind:class="[{'grid-x' : toggleView === 'list'}, selectedFile.physical_path === file.physical_path ? ' selected' : '']"
                  v-bind:key="file.id" v-for="file in fileList"
                  v-on:click="updateSelectedFile(file, $event)">
                    <div class="cell medium-auto">
                      <img class="img-thumbnail" v-bind:src="file.direct_link" v-if="getFileType(file.file_name) === 'image' && toggleView === 'grid'" />
                      <p>
                        <span v-if="getFileType(file.file_name) !== 'image' || toggleView === 'list'"><i v-bind:class="`icon-${getFileType(file.file_name)}`" ></i></span>
                        {{ file.file_name }}
                      </p>
                    </div>
                    <div class="cell medium-auto" v-if="toggleView === 'list'">
                      <p>{{ file.date_added /* returnFormattedDate() */ }}</p>
                    </div>
                </li>
                <li v-if="fileList.length <= 0"
                  class="file-details no-files"><p class="text-center">No files in directory.</p></li>
              </ul>

              <!-- search results -->
              <ul class="fm-list-main"
                v-else
                v-bind:class="[toggleView, {'grid-x' : toggleView === 'grid'}]">
                <li class="file-details"
                  v-bind:class="[{'grid-x' : toggleView === 'list'}, selectedFile.physical_path === file.physical_path ? ' selected' : '']"
                  v-bind:key="index" v-for="(file, index) in searchList"
                  v-on:click="updateSelectedFile(file, $event)">
                    <div class="cell medium-auto">
                      <img class="img-thumbnail" v-bind:src="file.direct_link" v-if="getFileType(file.file_name) === 'image' && toggleView === 'grid'" />
                      <p>
                        <span v-if="getFileType(file.file_name) !== 'image' || toggleView === 'list'"><i v-bind:class="`icon-${getFileType(file.file_name)}`" ></i></span>
                        {{ file.file_name }}
                      </p>
                    </div>
                    <div class="cell medium-auto" v-if="toggleView === 'list' && (searchKeyword !== '' || listArchive)">
                      <p>{{ file.physical_path }}</p>
                    </div>
                    <div class="cell medium-auto" v-if="toggleView === 'list'">
                    <p>{{ file.date_added }}</p>
                    </div>
                    <div class="cell medium-auto" v-if="toggleView === 'list' && searchKeyword !== '' && searchBy === 'tags'">
                      <p>
                        <template
                          v-for="(tags, index) in file.tags">
                          {{ tags }}<template v-if="index+1 < file.tags.length">, </template>
                        </template>
                      </p>
                    </div>
                </li>
                <li v-if="searchList.length <= 0"
                  class="file-details">
                  <p class="text-center" v-if="!listArchive">No results found.</p>
                  <p class="text-center" v-else>Archive is empty</p>
                </li>
              </ul>
              <!-- /-fm-list-main -->
            </div>
            <!-- /main list -->
        </div>
        <!-- /preloader + main list -->
      </div>
    </div>
</template>
<script>
// general resources
import { mapState } from 'vuex';
import InsitesMixins from '../../../mixins/InsitesMixins';

// file manager resources
import SelectedFilesManager from '../../../mixins/SelectedFilesManager';
import ComponentManager from '../../../mixins/ComponentManager';
import PreviewSelectedFileManager from '../../../mixins/PreviewSelectedFileManager';

export default {
  mixins: [SelectedFilesManager, PreviewSelectedFileManager, InsitesMixins, ComponentManager],
  data () {
    return {
      sortAsc: false,
      currentItemList : "as",
      loading: null,
      preloaderMessage: ""
    }
  },
  computed: mapState({
    fileList: state => state.FileManagerStore.files,
    toggleView: state => state.FileManagerStore.toggleView,
    searchKeyword: state => state.FileManagerStore.searchKeyword,
    selectedFile: state => state.FileManagerStore.selected_file,
    selectedFolderData: state => state.FileManagerStore.selected_folder_data,
    searchList: state => state.FileManagerStore.search_files,
    searchBy: state => state.FileManagerStore.searchBy,
    listArchive: state => state.FileManagerStore.listArchive
  }),
  methods: {
    checkIfRootFolder() {
      if(this.selectedFolderData.details) {
        return this.selectedFolderData.details.id === this.root_folder 
          ? true 
          : false;
      }
    },
    toggleSort() {
      this.sortAsc = !this.sortAsc;
    },
    fileParentFolderHandler() {
        this.goToParentFolder();
    },
    loadItemList() {
      // preloader
      setTimeout(() =>{
        this.loading = false;
      }, 1000);
    },
    async updateSelectedFile(file) {
      // get file customization data & update store;
      await this.setFileCustomization(file);

      // commit update Selected file customization
      //  this.$store.commit('updateFileManagerSelectedFile', file);
      // console.log(event);
      // $(event.currentTarget.closest(".file-details")).siblings().removeClass("selected");
      // $(event).addClass("selected");
      this.updateImagePreview(file);
    },
    showList() {
      if(this.toggleView === 'list') {
        if(this.listArchive) {
          return this.searchList.length
        } else {
          if(this.searchKeyword !== "") {
            return this.searchList.length ? true: false;
          }
        }
        return true;
      } else {
        return false;
      }
    }
  },
}
</script>

<style lang="scss" src="./FMList.scss">
</style>