// general resources
import { mapState } from "vuex";
import InsitesMixins from './InsitesMixins';
// file manager module resources
import ComponentManager from './ComponentManager';
import { getAssetsFolderJSON } from '../services/SiteManagerServices';

export default {
    name: "NewFolderManager",
    data() {
        return {
            selectFolderData: {},
            newFolderName: "",
            formData: {
                form_configuration_name: "modules/insites_site_manager/insites_assets",
                parent_resource_id: "modules/insites_site_manager/insites_assets",
                form: {
                    properties: {
                        file_name: "",
                        folder: "",
                        is_enabled: "true"
                    }
                }
            },
            headers: "",
            method: "post",
            actionUrl: "/api/customizations"
        }
    },
    computed: mapState({
      selectedFolderData: state => state.FileManagerStore.selected_folder_data
    }),
    mixins: [ComponentManager, InsitesMixins],
    methods: {
        getFolderDataMethod(type) {
            return type === 'create' ? 'post' : 'put';
        },
        getFolderActionURL(id) {
            return id ? this.actionUrl = `${this.actionUrl}/${id}` : this.actionUrl;
        },
        buildFolderData(newFolderName, method) {
            this.formData.form.properties.physical_path = this.convertToPhysicalPath(newFolderName);
            this.formData.form.properties.folder = this.convertToFolder(newFolderName),
            this.formData.form.properties.is_enabled = method === 'delete' ? "false" : "true";

            return JSON.stringify(this.formData);
        },
        async createAssetFolder(headers, type, newFolderName, id) {
            let self = this;
            let has_physical_path = await this.checkIfFolderNameExist(newFolderName);
            let method = this.getFolderDataMethod(type);

            return new Promise(resolve => {
                if (has_physical_path) {
                    resolve({ state: false, results: {}, type: "error", message: 'Folder name already exist.' });
                } else {
                    self.$http[method](self.getFolderActionURL(id), self.buildFolderData(newFolderName, method), { headers : headers })
                        .then(results => {
                            let selected_folder_data = this.selectedFolderData;
                            selected_folder_data.details.has_children = true;
                            self.$store.commit("updateFileManagerSelectedFolderData", selected_folder_data);
                            resolve({ state: true, results: results, type: "success", message: `Folder has been ${type}d.` });
                        }).catch(error => {
                            resolve({ state: false, results: error, type: "error", message: `Error ${type} folder. Please try again.` });
                        });
                }
            });
        },
        async checkIfFolderNameExist(newFolderName) {
            let response = await getAssetsFolderJSON();
            let physical_path = "";

            if (response.state) {
                for (let index in response.results.items.results) {
                    if (index.indexOf(this.convertToPhysicalPath(newFolderName)) === 0) {
                        physical_path = index;
                    }
                }
                return physical_path;
            } else {
                console.error('error: ', response.error.messsage);
                return;
            }
        }
    }
}
