<template>
<div class="grid-container-full pages">
  <div class="grid-x">
    <div class="large-12 cell content-blocks-container">

      <div class="page-view-filter">
        <!-- has data -->

        <!-- PENDING: Filters from POS 
        <div class="page-view-filter">
          <ins-filter with-date-filter
            v-on:onFilterApply="filterHandler">-->
            <span class="filter-label">Filter:</span>  
            <InsSelectWrap label="Status" button
              v-model="pageStatus" v-on:input="filterHandler">

              <!-- <ins-select-option label="All" value="all"></ins-select-option> -->
              <ins-select-option label="Published" value="published" default></ins-select-option>
              <ins-select-option label="Archived" value="archived"></ins-select-option>
            </InsSelectWrap>
          <!-- </ins-filter> -->
        <!-- </div> -->
      </div>

      <div v-show="!hasData">
        <!-- no data -->
        <ins-card steady>
          <h2 class="title-page h2desc">Welcome to Pages</h2>
          <p class="pdesc">Pages display your site's content online. Each page has an assigned template, so you
            don't have to re-insert the header, footer or other common elements each time you create
            new content.</p>
        </ins-card>

        <div class="pages-bottom">
          <h2 class="pages-title-secondary h2desc">You don't have any pages yet.</h2>
          <p class="pdesc">Would you like to create your first page?</p>

          <ins-button label="ADD NEW PAGE" solid
            v-on:onClickInsButton="goToAddNewPage">
          </ins-button>
        </div>
        <!-- /no data -->
      </div>
        
      <div class="header-buttons" v-show="hasData">
        <ins-button label="ADD NEW PAGE" solid
          color="primary" size="normal"
          v-on:onClickInsButton="goToAddNewPage">
        </ins-button>
      </div>

      <!-- table PENDING: Search Filter from POS 
        v-on:onTableSort="tableSortHandler"  -->
      <div class="ins-table-container" v-show="hasData">
        <ins-base-table ref="insBaseTable"
          searchbar-placeholder="Search by Page Content"
          v-on:onPaginationChange="tablePaginationHandler"
          v-on:onTableBulkAction="tableBulkActionHandler"
          v-on:onTableRowAction="tableRowActionHandler"
          v-on:onTableSearch="tableSearchHandler">
        </ins-base-table>
        <div class="spacer large"></div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import InsitesMixins from '../../mixins/InsitesMixins';
import VclMixins from '../../mixins/VclMixins';

import InsSelectWrap from '../../components/InsSelectWrap/InsSelectWrap.vue';
import { getAllPagesJSON } from '../../services/SiteManagerServices';
import { renameVclFile } from '../../services/VclService';

export default {
  name: "ListPages",
  mixins: [ InsitesMixins, VclMixins ],
  components: { InsSelectWrap },
  data() {
    return {
      hasData: true,
      source: "/insites/api/get-all-pages-data.json",
      location: "",
      insBaseTable: "",
      tableHeaders: [],
      pageNumber: 1,
      pageSize: 10,
      pageStatus: 'published',
      lastModified: '',
      searchKeyword: "",
      sortedKeyword: "",
      sortedBy: "asc",
      headers: {},
      continueDeletion: false
    }
  },
  methods: {
    goToAddNewPage() {
      this.$store.commit("setPageFormData", { data: {}, action: "New" });
      this.$router.push({ name: "ManagePage" });
    },
    goToManagePage(data, action) {
      InsitesUtil.renderPage(`${action} Page`, `#/edit-page/${data.id}`);
      this.$store.commit("setPageFormData", { data, action });
      this.$router.push(`/edit-page/${data.id}`);
    },
    filterHandler(event){

      if (event.detail){
        // PENDING: Date Filter from POS
      } else {
        this.pageStatus = event;
        if (this.pageStatus === 'archived'){
          this.$refs.insBaseTable.rowActions = ["View"]; 
          this.$refs.insBaseTable.bulkActions = [];
        } else {
          this.$refs.insBaseTable.rowActions = ["Preview", "Edit", "Archive"];
          this.$refs.insBaseTable.bulkActions = ["Archive"];
        }
      }
      this.fetchDataFromSrc();
    },
    tablePaginationHandler(event) {
      this.$refs.insBaseTable.loadingScreen = true;
      this.pageSize = event.detail.pageSize;
      this.pageNumber = event.detail.pageNumber;
      this.fetchDataFromSrc();
    },
    tableSearchHandler(event) {
      if ((event.detail.keyCode === 13 && event.detail.value !==  "") ||
          (event.detail.value === "" && this.searchKeyword !== "")
        ){

        this.searchKeyword = event.detail.value;
        this.pageNumber = 1;
        this.$refs.insBaseTable.pageNumber = 1;
        this.fetchDataFromSrc();
      }
    },
    // tableSortHandler(event) {
    //   switch (event.detail.keyword) {
    //     case "Page Name":
    //       this.sortedKeyword = 'title';
    //       break;

    //     case "URL":
    //       this.sortedKeyword = 'slug';
    //       break;
    //   }
    //   this.sortedBy = event.detail.order;
    //   this.fetchDataFromSrc();
    // },
    tableBulkActionHandler(event) {
      switch (event.detail.action) {
        case "Archive":
          this.archiveSelection(event.detail.selections);
          break;
      }
    },
    tableRowActionHandler(event) {
      let data = event.detail.data;
      let action = event.detail.action;

      switch (event.detail.action) {
        case 'Archive':
          this.archiveItem(event.detail.data);
          break;

        case 'Edit':
        case 'View':
          this.goToManagePage(data, action);
          break;

        case 'Preview':
          let loc = event.detail.data.URL;

          if (loc[0] === '/'){
            window.open(loc);
          }

          window.open(`/${loc}`);
          break;
      }
    },
    archiveItem(data) {
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="Are you sure you want to archive this page?">
          </ins-loader>`,
        type: "",
        width: 330,
        showCancelButton: true,
        confirmButtonClass: "negative",
        confirmButtonText: "Archive"
      }).then(result => {
        if (result.value){
          this.deleteItem(data.rawData, "Page has");
        }
      });
    },
    async archiveSelection(selections) {
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="Are you sure you want to archive these pages?">
          </ins-loader>`,
        type: "",
        width: 330,
        showCancelButton: true,
        confirmButtonClass: "negative",
        confirmButtonText: "Archive"
      }).then(result => {
        if (result.value) {
          let counter = 0;
          this.continueDeletion = true;
          selections.forEach(async data => {

            if (this.continueDeletion){

              counter++;
              if (selections.length === counter) {
                this.continueDeletion = false;
              }
              await this.deleteItem(data.rawData, "The selected pages have");
            }
          });
        }
      });
    },
    async deleteItem(data, message) {
      let dataCopy = {
        payload: JSON.parse(JSON.stringify(data))
      };
      let timestamp = + new Date();
      let currPath = dataCopy.payload.physical_file_path;
      dataCopy.payload.redirect_to = "404";
      dataCopy.payload.slug = this.constructSlug(dataCopy.payload.slug, 'archived', timestamp);
      dataCopy.payload.physical_file_path = this.constructPath(dataCopy.payload.physical_file_path, 'archived', timestamp);
      dataCopy.payload.metadata.search_engine_visibility = false;
      dataCopy.payload.metadata.status = 'archived';
      
      let vclPayload = this.constructVclPayload(dataCopy.payload);
      vclPayload.git.rename_file = currPath;
      let vclRes = await renameVclFile(vclPayload);

      if (vclRes.state){
        let parsedData = JSON.stringify(dataCopy);
        this.submitForm(parsedData, message);
      } else if (vclRes.results.response.data.message.includes('File does not exist')) {
        this.deleteErrHandler(`${currPath} does not exist in repository.`);
      } else {
        this.deleteErrHandler(vclRes.results.response.data.message);
      }
    },
    constructSlug(slug, type, timestamp){
      let ext = slug.lastIndexOf('.');
      let suffix = type === 'archived' ? `${type}_${timestamp}` : type;
      
      if (slug.substring(ext) === '.html'){
        return `${slug.slice(0, ext)}_${suffix}${slug.slice(ext)}`;
      }
      return `${slug}_${suffix}`;
    },
    constructPath(path, type, timestamp){
      let suffix = type === 'archived' ? `${type}_${timestamp}` : type;
      return path.replace('.html.liquid',`_${suffix}.html.liquid`);
    },
    submitForm(parsedData, message){
      let url = `/insites/api/update-page.json`;
      this.$http.post(url, parsedData ,{headers: this.headers})
        .then(res => this.deleteResHandler(res, message))
        .catch(this.deleteErrHandler);
    },
    deleteResHandler(res, message){
      if (!this.continueDeletion){
        if (res.data.includes){
          if (res.data.includes('500 - Server Error') ||
          res.data.includes('parse_json failed')){
            this.deleteErrHandler('500 POS Server Error');
          }
        } else {
          toastIt('success', `${message} been archived.`, InsitesUtil.ops());
          this.fetchDataFromSrc();
        }
      }
    },
    deleteErrHandler(error){
      console.error(error);
      this.continueDeletion = false;
      let message = "Failed to archive page. Please try again.";
      if (typeof error === "string"){
        message = error;
      }
      toastIt("error", message, InsitesUtil.ops());
    },
    async fetchDataFromSrc() {
      let self = this;

      self.setInsBaseTableLoader(
        "Processing Your Data",
        "Just a moment, we're fetching and processing your data.",
        "processing"
      );

      let response = await getAllPagesJSON(self.searchKeyword, self.pageStatus, self.pageNumber, self.pageSize);
      if (response.state) {
        self.resHandler(response.results);
      } else {
        self.errorHandler(response.results);
      }
    },
    errorHandler(error){
      console.error(error);
      let type = this.continueDeletion ? 'archive':'load';
      let message = `Failed to ${type} data. Please try again.`;
      this.continueDeletion = false;
      this.setInsBaseTableLoader('', message, 'error');
    },
    resHandler(res) {
      let tableData = res.admin_pages;
      if (tableData.results.length) {

        this.$refs.insBaseTable.totalCount = tableData.total_entries;
        this.bindTableData(tableData.results);

      } else {

        if (this.searchKeyword) {
          
          let message = `No results for "${this.searchKeyword}"`;
          this.setInsBaseTableLoader('', message, 'no-data');

        } else {
          this.hasData = false;
        } 
      }
    },
    bindTableData(tableData) {
      this.$refs.insBaseTable.tableData = tableData.map(this.constructTableData);
      this.hasData = true;
      this.$refs.insBaseTable.loadingScreen = false;
    },
    setInsBaseTableLoader(title, message, icon){
      this.$refs.insBaseTable.loadingScreen = true;
      this.$refs.insBaseTable.loaderTitle = title;
      this.$refs.insBaseTable.loaderMessage = message
      this.$refs.insBaseTable.loaderIcon = icon;
    },
    setupInsBaseTable(){
      this.$refs.insBaseTable.bulkActions = ["Archive"];
      this.$refs.insBaseTable.rowActions = ["Preview", "Edit", "Archive"]; 
      this.$refs.insBaseTable.tableHeaders = [
        this.constructTableHeader("Page Title"),
        this.constructTableHeader("URL"),
        this.constructTableHeader("Template"),
        this.constructTableHeader("Status", "string", false, true)
      ];
    },
    constructTableHeader(label, type = "string", sortable, hasTag, hasLink, hasColumnAction, hasImage){
      return { label, type, sortable, hasTag, hasLink, hasColumnAction, hasImage }
    },
    constructTableData(item){
      let pageName = '';

      if (item.metadata.page_title) {
        pageName = item.metadata.page_title;
      } else {
        pageName = this.getSlugName(item.slug) 
        item.metadata.page_title = pageName;
      }
        
      return {
        rawData: item,
        id: item.id,
        "Page Title": pageName,
        "URL": item.slug,
        "Template": item.layout,
        "Status": item.metadata.status
      }
    },
    getSlugName(slug){
      let lastFS = slug.lastIndexOf('/') + 1;
      return `${slug.substr(lastFS, slug.length)}.html`;
    }
  },
  created(){
    this.$store.dispatch('fetchSiteTemplates');
  },
  mounted () {
    this.setupInsBaseTable();
    this.fetchDataFromSrc();
    this.headers = this.apiHeader();
  }
}
</script>

<style lang="scss" src="./ListPages.scss">
</style>