<template>
  <ins-textarea ref="insTextareaEl"
    v-bind:label="label"
    v-bind:value="value"
    v-bind:placeholder="placeholder"
    v-bind:name="name"
    v-bind:disabled="disabled"
    v-bind:readonly="readonly"
    v-bind:required="required"
    v-bind:maxlength="maxlength"
    v-bind:error-message="errorMessage"
    v-bind:has-error="hasError"
    @valueChange="onValueChange" />
</template>

<script>
export default {
  name: "InsTextareaWrap",
  props: {
    label: String,
    value: String,
    placeholder: String,
    name: String,
    errorMessage: String,
    maxlength: String,
    hasError: Boolean,
    disabled: Boolean,
    readonly: Boolean,
    required: String
  },
  methods: {
    onValueChange(ev) {
      this.$emit("input", ev.detail);
      this.checkInput(ev.detail)
    },
    checkInput(value){
      if (this.required && !value) {
        return this.toggleError(true, `${this.label} is required.`);
      } else {
        this.toggleError(false, '');
      }
    },
    toggleError(error, message){
      
      this.$refs.insTextareaEl.hasError = error ? true : false;
      this.$refs.insTextareaEl.errorMessage = message;

      this.$emit("fieldError", {
        error: error ? true : false,
        target: this
      });
    }
  }
};


</script>
