<template>
<div class="grid-container-full mvc-wrap">
  <div class="grid-x">
    <div class="large-8 medium-12 small-12 cell">
      <ins-card steady v-if="!vclInitialized">
        <div class="spacer large"></div>
        <ins-loader
          state-title="Processing Your Data"
          state-message="Just a moment, we're fetching and processing your data."
          state-icon="processing">
        </ins-loader>
        <div class="spacer large"></div>
      </ins-card>

      <div class="custom-ins-card" v-if="vclInitialized">

        <div class="toggleFieldFormWrap">
          <InsToggleWrap
            label="Enable Version Control"
            v-bind:checked="tempEnabled"
            v-model="tempEnabled">
          </InsToggleWrap>
        </div>

        <InsInputWrap label="Instance URL*"
          placeholder="Please enter the Instance URL"
          maxlength="120"
          v-model="instanceUrl"
          v-bind:required="true ? 'true':'false'"
          v-bind:readonly="true"
          v-bind:validate="true"
          v-bind:has-error="errors.instance_url ? true:false"
          v-bind:error-message="errors.instance_url"
          v-on:fieldError="fieldErrorHandler">
        </InsInputWrap>

        <InsInputWrap label="Repository SSH URL*" maxlength="120"
          placeholder="Please enter the Repository SSH URL" 
          v-bind:required="vclFormType === 'setup'? 'true':'false'"
          v-model="tempGitURL"
          v-bind:validate="true"
          v-bind:has-error="errors.git_url ? true:false"
          v-bind:error-message="errors.git_url"
          v-on:fieldError="gitErrorHandler">
        </InsInputWrap>

        <InsInputWrap label="Marketplace Kit Token*" maxlength="120"
          v-bind:placeholder="MPKPlaceholder" 
          v-bind:required="vclFormType === 'setup'? 'true':'false'" 
          v-model="tempMpkToken"
          v-bind:validate="true"
          v-bind:has-error="errors.marketplace_kit_token ? true:false"
          v-bind:error-message="errors.marketplace_kit_token"
          v-on:fieldError="fieldErrorHandler">
        </InsInputWrap>

        <div class="ins-note-wrap">
          <em>Please note that Marketplace Kit Token will be stored securely on our Insites Data Storage and will not be displayed here for security reasons.</em>
        </div>

      </div>
    </div>
  </div>
  <div class="grid-x">
    <div class="large-8 medium-12 small-12 cell text-right">
      <div class="spacer small"></div>

      <ins-button label="Cancel" outlined
        v-on:onClickInsButton="goBackToListView">
      </ins-button>

      <ins-button solid label="SAVE"
        v-if="vclInitialized"
        v-bind:disabled="sendingForm"
        v-on:onClickInsButton="validateData">
      </ins-button>

    </div>
  </div>
</div>
</template>

<script>
import InsitesService from '../../mixins/InsitesService';
import InsInputWrap from '../../components/InsInputWrap/InsInputWrap.vue';
import InsSelectWrap from '../../components/InsSelectWrap/InsSelectWrap.vue';
import InsToggleWrap from '../../components/InsToggleWrap/InsToggleWrap.vue';

import { mapState } from 'vuex';
import { 
  sidebarMenuRoute,
  listVclRoute,
  setupVclUserRoute,
} from '../../models/VclRoutes';

import {
  createVclUser,
  updateVclUser,
  updateMasterCustomisation,
  createMasterCustomisation
} from '../../services/VclService';

export default {
  name: "ManageVclSetup",
  mixins: [InsitesService],
  components: { InsInputWrap, InsSelectWrap, InsToggleWrap },
  watch: { vclInitialized: function(){this.checkData()} },
  computed: mapState({
    instanceUrl: state => state.VclStore.instanceUrl,
    customisationMasterData: state => state.VclStore.customisationMasterData,
    vclMasterData: state => state.VclStore.vclMasterData,
    vclInitialized: state => state.VclStore.vclInitialized,
    vclFormType: state => state.VclStore.vclFormType,
  }),
  data() {
    return {
      MPKPlaceholder: "Please enter the Masterplace Kit Token",
      MPKTokenEdit: false,
      buttonLabel: "ADD",
      errors: {
        instance_url: '',
        git_url: '',
        marketplace_kit_token: ''
      },
      sendingForm: false,
      tempEnabled: false,
      tempMpkToken: '',
      tempGitURL: '',
    }
  },
  methods: {
    mpkEdited(){
        this.MPKTokenEdit = true;
        this.MPKPlaceholder = "Please enter the Masterplace Kit Token";
    },
    checkData(){
      if (!this.vclFormType){
        if (!this.vclMasterData) {
          // this.MPKTokenEdit = true;
          this.$store.commit('setVclFormType', 'setup');
        } else {
          this.$store.commit('setVclFormType', 'update-setup');
          this.tempEnabled = this.vclMasterData.info.is_enabled;
          this.tempGitURL = this.vclMasterData.info.git_url;
          // this.MPKPlaceholder = !this.MPKTokenEdit ? "**************" : "Please enter the Masterplace Kit Token";
        } 
      }
    },
    fieldErrorHandler(event){
      event.target.$el.hasError = event.error;
      event.target.$el.errorMessage = (`${event.target.$el.label} is required.`).replace('*','');
    },
    gitErrorHandler(event){
      this.fieldErrorHandler(event);
      let input = event.target.$el.value;

      if (input){
        if(!this.isGitUrl(input)){
          event.target.$el.hasError = true;
          event.target.$el.errorMessage = 'Not a valid Repository URL';
        } else if(!this.isGitUrlSupported(input)){
          event.target.$el.hasError = true;
          event.target.$el.errorMessage = 'Repository not yet supported.';
        }
      }
    },
    hasRequiredValues(){
      let errorsCount = 0;
      
      if (this.instanceUrl === ''){
        this.errors.instance_url = 'Instance URL is required.';
        errorsCount++;
      } else {
        this.errors.instance_url = '';
      }
    
      if (this.tempMpkToken === '' && this.vclFormType === 'setup'){
        this.errors.marketplace_kit_token = 'Marketplace Kit Token is required.';
        errorsCount++;
      } else {
        this.errors.marketplace_kit_token = '';
      }

      if (this.tempGitURL === '' && this.vclFormType === 'setup'){
        this.errors.git_url = 'Repository SSH URL is required.';
        errorsCount++;

      } else {
        this.errors.git_url = '';
      }

      return errorsCount ? false:true;
    },
    validateData(){
      this.sendingForm = true;
      if (this.hasRequiredValues()){
        this.sendData();
      } else {
        this.sendingForm = false;
      }
    },
    sendData(){
      let payload = this.constructPayload();
      switch(this.vclFormType){
        case "setup":
          this.createData(payload);
          break;
        
        case "update-setup":
          this.updateData(payload);
          break;
      }
    },
    async createData(payload){
      let data = await createVclUser(payload);

      if (data.state && data.results.insites_key){
        payload.cred.insites_key = data.results.insites_key;
        let res = await createMasterCustomisation(payload);
        if (res.state){
          this.updateStore(payload, res.results.customization_create);
          return;
        }
      }
      this.showError();
    },
    async updateData(payload){
      let data = await updateVclUser(payload);
      if (data.state && data.results.Attributes) {
        let res = await updateMasterCustomisation(payload);
        if (res.state){
          this.updateStore(payload, res.results.customization_update);
          return;
        }
      }
      this.showError();
    },
    updateStore(payload, customisation){
      this.$store.commit('setCustomisationMaster', customisation);
      this.$store.commit('setVclMaster', payload.cred);
      
      toastIt('success', 'Successfully configured version control.', InsitesUtil.ops());
      this.goBackToListView();
    },
    constructPayload(){
      let payload = {
        cred: {
          id: this.customisationMasterData.id,
          instance_url: this.instanceUrl,
          insites_key: this.customisationMasterData.properties.insites_key,
          info: {
            master_key: true,
            created_on: 'Insites Site Manager',
            marketplace_kit_token: this.tempMpkToken,
            git_url: this.tempGitURL,
            is_enabled: this.tempEnabled,
            last_update_by: this.$store.getters.getCurrentUserId
            // last_update_by_email: this.$store.getters.getCurrentUserEmail,
            // last_update_by_name: this.$store.getters.getCurrentUserName
          }
        }
      }

      if (this.tempMpkToken === ""){
        delete payload.cred.info.marketplace_kit_token;
      }

      if (this.tempGitURL === ""){
        delete payload.cred.info.git_url;
      }

      return payload;
    },
    showError(){
      this.sendingForm = false;
      toastIt('error', `Failed to setup version control. Please try again.`, InsitesUtil.ops());
    },
    goBackToListView(){
      // this.$router.push("/version-control");
      InsitesUtil.goBack();
    },
    activateRoute(){
      let routes = [sidebarMenuRoute, listVclRoute, setupVclUserRoute];
      InsitesUtil.setBreadCrumbs(routes);
    },
    isGitUrl(url){
      const regex = url.includes('codecommit')
        ? /(?:git|ssh|https?|git@[-\w.]+)(\/\/)?(.*?)(\/?|\#[-\d\w._]+?)$/
        : /(?:git|ssh|https?|git@[-\w.]+):(\/\/)?(.*?)(\.git)(\/?|\#[-\d\w._]+?)$/;
      return regex.test(url);
    },
    isGitUrlSupported(url){
      return url.includes('github') || url.includes('codecommit');
    }
  },
  mounted(){
    if (this.vclMasterData){
      this.tempEnabled = this.vclMasterData.info.is_enabled;
      this.tempGitURL = this.vclMasterData.info.git_url;
    }
    this.activateRoute();
  }
}
</script>
<style lang="scss" src="./ManageVclSetup.scss"></style>