import PageFormModel from '../models/PageFormModel';

const PagesStore = {
  state: {
    pageFormType: '',
    pageFormData: {},
    siteTemplateOptions: [],
    siteAuthPoliciesOptions: [],
  },
  mutations: {
    setPageFormData(state, data){
      state.pageFormData = {};
      if (data.action !== 'New') {
        state.pageFormData = data.data.rawData;
      } else {
        state.pageFormData = JSON.parse(JSON.stringify(PageFormModel));
      }
      state.pageFormType = data.action;
    },
    setPageFormType(state, data){
      state.pageFormType = data;
    },
    setSiteTemplatesOptions(state, data){
      state.siteTemplateOptions = data;
    },
    setSiteAuthPoliciesOptions(state, data){
      state.siteAuthPoliciesOptions = data;
    }
  },
  actions: {
    fetchSiteTemplates(context) {
      let url = `/insites/api/get-all-layouts-data.json?page=1&size=1000`;
      
      this._vm.$http.get(url)
        .then(res => {
          let results = res.data.admin_liquid_layouts.results;
          if (results.length) {
            context.commit("setSiteTemplatesOptions", results);
          }
        }).catch((error) => {
          console.error('Failed to load site templates', error);
        });
    },
    fetchSiteAuthPolicies(context) { // Secure Zone Ready
      let url = '/insites/api/get-all-auth-policies-data.json';
      this._vm.$http.get(url)
        .then(res => {
          let results = res.data.items.results;
          if (results.length) {
            context.commit("setSiteAuthPoliciesOptions", results[0]);
          }
        }).catch((error) => {
          console.error('Failed to load auth policies', error);
        });
    },
  }
}
export default PagesStore;