<template>
  <div class="grid-container-full fm-selectsearch">
    <div class="grid-x">
      <div class="medium-5 small-12 cell">
          <!-- <div class="fm-select-container">
            <InsSelectWrap
              label="Current Folder"
              placeholder="Current Folder"
              v-bind:disabled="false"
              searchable>
              <ins-select-option label="assets" value="assets"></ins-select-option>
              <ins-select-option v-for="(item,key,index) in folders" v-bind:key="index"
              v-bind:label="item" v-bind:value="item"></ins-select-option>
            </InsSelectWrap>
          </div> -->
      </div>
      <div class="medium-offset-2 medium-5 small-12 cell">
        <div class="fm-search-container grid-x">
          <div class="large-4 medium-12 small-12 cell">
            <form
              v-on:submit.prevent="setSearchParameter">
              <InsSelectWrap
                label="Search By"
                ref="searchBy"
                v-bind:disabled="false"
                v-on:input="checkSearchBy"
                searchable>
                  <ins-select-option label="File Name" value="file_name" default></ins-select-option>
                  <ins-select-option label="Tags" value="tags"></ins-select-option>
                </InsSelectWrap>
              </form>
          </div>
          <div class="large-8 medium-12 small-12 cell fm-search-field">
            <ins-searchbar
              class="large-8 small-12 cell"
              placeholder="Enter keyword"
              ref="keyword"
              v-on:keyup.enter="setSearchParameter"
              v-bind:value="searchKeyword"
              v-on:input="checkSearchField"></ins-searchbar>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// general resources
import { mapState } from "vuex";
import InsSelectWrap from "../../InsSelectWrap/InsSelectWrap.vue";
import InsitesMixins from '../../../mixins/InsitesMixins';
// file manager module resources
import ComponentManager from '../../../mixins/ComponentManager';
import { getDeletedAssetsByFolderDataJSON, getAssetsCustomizationByTagsJSON } from '../../../services/SiteManagerServices';

export default {
  name: "FMSelectSearch",
  mixins: [InsitesMixins, ComponentManager],
  components: {
    InsSelectWrap
  },
  computed: mapState({
    selectedFolder: state => state.FileManagerStore.selected_folder,
    folders: state => state.FileManagerStore.folders,
    searchBy: state => state.FileManagerStore.searchBy,
    searchKeyword: state => state.FileManagerStore.searchKeyword,
    assets_url: state => state.FileManagerStore.assets_url.results.items.results
  }),
  methods: {
      searchFilesByName(searchValue) {
        // search for files by filename
        let tempList = [];
        let self = this;
        Object.keys(self.assets_url).forEach(item => {
          let filename = item.split('/').pop();
          if(self.assets_url[item] != '' && filename.indexOf(searchValue) > -1) {
            let obj = {
              file_name: item.split('/').pop(),
              physical_path : item
            }
            tempList.push(obj);
          }
        });
        self.removeDeletedFilesOnList(tempList);
      },
      async removeDeletedFilesOnList(tempList){
        let customizations = await getDeletedAssetsByFolderDataJSON();
        let filteredList = this.compareFilesOfSelectedFolderToCustomization(tempList, customizations.results.items.results);
        this.$store.commit("updateFileManagerSearchResults", filteredList);
      },
      async searchFilesByTags(searchValue) {
        // search for files by tag or filename
        let self = this;
        let response = await getAssetsCustomizationByTagsJSON(searchValue);
        
        if (response.state) {
          self.$store.commit("updateFileManagerSearchResults", response.results.items.results);
        } else {
          toastIt('error', 'Failed to search file. Please try again.', InsitesUtil.ops());
        }
      },
      checkSearchField(ev) {
        if(ev.target.value === "") {
          this.$store.commit("updateSearchKeyword", "");
        }
      },
      checkSearchBy(ev) {
        this.$store.commit("updateSearchBy", ev);
        this.$store.commit("updateSearchKeyword", "");
      },
      setSearchParameter(ev) {
        let searchValue = ev.target.value;
        this.$store.commit("updateSearchKeyword", searchValue);
        this.$store.commit("updateFileManagerSelectedFile", {});
        if(searchValue.trim() !== "" ) {
          this.searchBy === 'tags'
            ? this.searchFilesByTags(searchValue)
            : this.searchFilesByName(searchValue);
        }
      }
  }
}
</script>

