<template>
  <div class="grid-container-full">
    <div class="grid-x sm-file-manager">
      <div class="medium-12 cell content-blocks-container">
          <FMSelectSearch  />
          <FMContainer
            v-on:openModal="openModalHandler"
            v-on:openFolderModal="openFolderModalHandler"
            v-on:openTagModal="editTagHandler"
          />
      </div>
    </div>
    <div class="fm-modals">
      <!-- file upload -->
      <InsModal
        v-bind:showModal="showModal"
        v-on:closeModal="closeModalHandler"
        v-on:successModal="successModalHandler"
        headerLabel="Upload File(s)"
        successButton="Proceed"
        v-bind:isLoadingSuccess="isLoadingSuccess"
        v-bind:isDisabledCancel="isDisabledCancel"
        v-bind:isDisabledSuccess="checkFilesToUpload()"
      >
        <div class="grid-x footer-btn-container">
          <div class="large-12 medium-12 small-12 cell">
            <p class="folder-path">Current Folder: {{ selectedFolder }}</p>
            <input
              type="file"
              multiple
              ref="modalFiles"
              class="hide"
              v-on:input="setFiles($event.target.files)"
              fileCount="$event.target.files.length"
            >
          </div>
          <div class="large-12 medium-12 small-12 cell">
                <p class="select-button" v-if="!isDisabledCancel" v-on:click="selectFile">
                    <span>+ CHOOSE FILE(S)</span>
                </p>

                <ul class="files-list" v-if="filesToUpload.length">
                  <li v-for="(item, key, index) in filesToUpload" v-bind:key="index" v-if="item.status === 'new'">
                    <i v-bind:class="`icon-${getFileType(item.name)} file-icon`"></i>
                    <div class="file-name">{{ item.name }}</div>
                    <span v-if="item.state === 'pending'"
                      class="remove-file icon-close"
                      v-on:click="removeFile(item)"
                    ></span>
                    <span v-else-if="item.state === 'loading'"
                      class="spinner-uploading file-icon">
                      <i class="spinner">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</i>
                    </span>
                    <span v-else
                    class="uploading-file icon-check-2 file-icon"
                    ></span>
                  </li>
                </ul>

                <div class="existing-files" v-show="filesToBeReplaced.length">
                    <p>Uploading will overwrite the following. Would you like to continue?</p>
                    <ul class="files-list" v-if="filesToUpload.length">
                      <li v-for="(item, key, index) in filesToUpload" v-bind:key="index" v-if="item.status === 'existing'">
                        <i v-bind:class="`icon-${getFileType(item.name)} file-icon`"></i>
                        <div class="file-name">{{ item.name }}</div>
                        <span v-if="item.state === 'pending'"
                          class="remove-file icon-close"
                          v-on:click="removeFile(item)"
                        ></span>
                        <span v-else-if="item.state === 'loading'"
                          class="spinner-uploading file-icon">
                          <i class="spinner">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</i>
                        </span>
                        <span v-else
                        class="uploading-file icon-check-2 file-icon"
                        ></span>
                      </li>
                    </ul>
                </div>

                <div class="archived-files" v-show="filesToBeRestored.length">
                    <p>{{ filesToBeRestored.length > 1 ? 'These files have' : 'This file has' }} been archived, uploading will restore & overwrite the following.</p>
                    <ul class="files-list" v-if="filesToUpload.length">
                      <li v-for="(item, key, index) in filesToUpload" v-bind:key="index" v-if="item.status === 'archived'">
                        <i v-bind:class="`icon-${getFileType(item.name)} file-icon`"></i>
                        <div class="file-name">{{ item.name }}</div>
                        <span v-if="item.state === 'pending'"
                          class="remove-file icon-close"
                          v-on:click="removeFile(item)"
                        ></span>
                        <span v-else-if="item.state === 'loading'"
                          class="spinner-uploading file-icon">
                          <i class="spinner">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</i>
                        </span>
                        <span v-else
                        class="uploading-file icon-check-2 file-icon"
                        ></span>
                      </li>
                    </ul>
                </div>
          </div>
        </div>
      </InsModal>
      <!-- new folder -->
      <InsModal
        v-bind:showModal="newFolderModal"
        v-on:closeModal="closeFolderModalHandler"
        v-on:successModal="createFolderHandler"
        v-bind:isLoadingSuccess="newFolderLoadingSuccess"
        v-bind:isDisabledCancel="newFolderDisabledCancel"
        headerLabel="Create Folder"
        successButton="Create"
      >
        <div class="grid-x footer-btn-container">
          <div class="large-12 medium-12 small-12 cell">
            <p class="folder-path">Folder Path: {{ newFolderName }}</p>
            <InsInputWrap
              maxlength="30"
              ref="newFolder"
              name="form[properties_attributes][physical_path]"
              label="Folder Name"
              placeholder="Folder Name"
              errorMessage="Folder Name is required."
              field="text"
              v-if="newFolderModal"
              v-bind:hasError="invalidFolderName"
              v-on:input="setNewFolder"
              v-model="inputFolderName"
              v-bind:validate="true"
              v-bind:required="'true'"
              v-bind:has-error="!isValidFolder"
              v-bind:disabled="newFolderLoadingSuccess"
            ></InsInputWrap>
            <span class="input-sublabel">The following characters are not allowed: \/:*?|</span>
          </div>
        </div>
      </InsModal>
    </div>

    <!-- File Tags -->
    <InsModal
      v-bind:showModal="tagModal"
      v-on:closeModal="closeTagsModalHandler"
      v-on:successModal="applyTags"
      v-bind:data="selectedFileData"
      headerLabel="File Tag(s)"
      successButton="Update Tags"
      successColor="green"
    >
      <form onsubmit="event.preventDefault()" class="grid-x footer-btn-container">
        <div class="large-12 medium-12 small-12 cell">
          <p>File Name: {{ selectedFileData.file_name }}</p>
        </div>
        <div class="large-8 medium-7 small-12 cell">
          <InsInputWrap
            maxlength="50"
            ref="fileTags"
            name="form[properties_attributes][tags]"
            label="Tags"
            placeholder="Tags"
            field="text"
            v-model="listTags"
          ></InsInputWrap>
          <span class="input-sublabel">Separate tags by comma ( , )</span>
        </div>
        <div class="large-4 medium-5 small-12 cell text-right">
          <ins-button
            class="add-tags-btn"
            type="submit"
            solid
            label="Add Tags"
            v-on:onClickInsButton="filterTags"
          ></ins-button>
        </div>
        <div class="large-12 medium-12 small-12 cell" v-if="selectedFileData">
          <p>Applied Tags:</p>
          <ul class="applied-tags-list">
            <li v-for="(tag, key, index) in tempTags" class="tagged" v-bind:key="index">
              <span class="ins-tags" v-on:click="removeTag(tag)">{{ tag }}</span>
            </li>
          </ul>
        </div>
      </form>
    </InsModal>
  </div>
</template>

<script>
// general resources
import { mapState } from "vuex";
import { isEmpty, map } from "lodash";
import InsInputWrap from "../../components/InsInputWrap/InsInputWrap.vue";
import InsModal from "../../components/InsModal/InsModal.vue";
import SiteManagerService from '../../mixins/SiteManagerService';
// import InsitesMixins from "../../mixins/InsitesMixins";
// import InsSelectWrap from "../../components/InsSelectWrap/InsSelectWrap.vue";

// site settings module resources
import FMContainer from "../../components/_SiteManager/FMContainer/FMContainer.vue";
import FMSelectSearch from "../../components/_SiteManager/FMSelectSearch/FMSelectSearch.vue"
import ComponentManager from '../../mixins/ComponentManager';
import SelectedFolderManager from '../../mixins/SelectedFolderManager';
import FileUploadManager from "../../mixins/FileUploadManager";
import NewFolderManager from "../../mixins/NewFolderManager";
import NewTagManager from "../../mixins/NewTagManager";
import ArchiveFileManager from "../../mixins/ArchiveFileManager";
// import { getDeletedAssetsDataJSON } from '../../services/SiteManagerServices';

export default {
  data() {
    return {
      data: Object,
      showModal: false,
      fileCount: 0,
      headers: "",
      formData: {
        form: {
          properties: {
            physical_path: "",
            direct_link: "",
            file_name: "",
            folder: "",
            tags: [],
            is_enabled: "true"
          }
        }
      },
      url: "",
      uploadPath: "",
      invalidFolderName: false,
      newFolderModal: false,
      newFolderName: "",
      inputFolderName: "",
      tagModal: false,
      listTags: "",
      tempTags: [],
      duplicateFiles: false,
      filesToBeReplaced: [],
      filesToBeRestored: [],
      existingFileNames: [],
      // archivedFiles: [],
      isDisabledSuccess: true,
      isLoadingSuccess: false,
      isDisabledCancel: false,
      newFolderLoadingSuccess: false,
      newFolderDisabledCancel: false,
      isValidFolder: true
    };
  },
  mixins: [SiteManagerService, FileUploadManager, NewFolderManager, NewTagManager, SelectedFolderManager, ComponentManager, ArchiveFileManager],
  props: {},
  components: {
    FMSelectSearch,
    FMContainer,
    InsModal,
    InsInputWrap
  },
  computed: mapState({
    options: state => state.FileManagerStore.folders,
    selectedFolder: state => state.FileManagerStore.selected_folder,
    selectedFolderData: state => state.FileManagerStore.selected_folder_data,
    selectedFiles: state => state.FileManagerStore.files,
    archivedFiles: state => state.FileManagerStore.archived_files,
    selectedFileData: state => state.FileManagerStore.selected_file,
    filesToUpload: state => state.FileManagerStore.files_to_upload,
    assetsURL: state => state.FileManagerStore.assets_url
  }),
  methods: {
    checkFilesToUpload() {
      return this.filesToUpload.length < 1;
    },
    checkFileExist() {
      let self = this;
      self.existingFileNames = [];
      self.filesToBeReplaced = [];
      self.filesToBeRestored = [];

      self.selectedFiles.forEach((file) => {
        self.existingFileNames.push(file.file_name);
      });

      for (let counter in self.filesToUpload) {
        if (self.existingFileNames.includes(self.filesToUpload[counter].name)) {
          self.filesToBeReplaced.push(self.filesToUpload[counter].name);
        }

        // check archived files
        // let archiveData = await self.checkArchivedFile(self.filesToUpload[counter], `${self.convertToPhysicalPath(`${self.selectedFolder}/${self.filesToUpload[counter].name}`)}`);
        let archiveData = self.checkStoredArchivedFile(`${self.convertToPhysicalPath(`${self.selectedFolder}/${self.filesToUpload[counter].name}`)}`);

        if (archiveData) {
          // save customization - restore file
          self.filesToBeRestored.push(archiveData);
        }
      }

      let files = this.filesToUpload;
      for (let counter = 0; counter < files.length; counter++) {
        if (self.filesToBeReplaced.includes(files[counter].name)) {
          files[counter].status = "existing";
        } else if (self.filesToBeRestored.includes(files[counter].name)) {
          files[counter].status = "archived";
        }
      }
      this.$store.commit('updateFilesToUpload', files);
    },
    checkStoredArchivedFile(physical_path) {
      let self = this;
      let file_name = "";
      for (let counter = 0; counter < self.archivedFiles.length; counter++) {
        if (self.archivedFiles[counter].physical_path === physical_path) {
          file_name = self.archivedFiles[counter].file_name;
          break;
        }
      }

      return file_name;
    },
    removeFile(item) {
      let indexOf = this.filesToUpload.indexOf(item);
      let files = this.filesToUpload;

      if (indexOf != -1) {
        files.splice(indexOf, 1);
      }

      this.$store.commit('updateFilesToUpload', files);
      this.checkFileExist();
    },
    setFiles(fileList) {
      let self = this;
      let formData = new FormData();
      let files = self.filesToUpload;
      let names = map(files, 'name');

      if (!fileList.length) return;

      // check file exist
      Array.from(Array(fileList.length).keys()).map(x => {
        if (!names.includes(fileList[x].name)) {
          formData.append(fileList[x], fileList[x].name);
          fileList[x].status = "new";
          if (fileList[x].name)
          files.push(fileList[x]);
        }
      });

      self.$store.commit('updateFilesToUpload', files);
      self.checkFileExist();
    },
    selectFile() {
      this.$refs.modalFiles.click();
    },
    listAssetFolders() {
      this.refreshNodeTree();
    },
    openModalHandler() {
      this.isLoadingSuccess = false;
      this.isDisabledCancel = false;
      this.showModal = true;
    },
    closeModalHandler() {
      this.showModal = false;
      // clear array of files
      this.$refs.modalFiles.value = "";
      this.$store.commit('resetFilesToUpload');
      this.existingFileNames = [];
      this.filesToBeReplaced = [];
      this.filesToBeRestored = [];
    },
    setPOSField(field, value) {
      this.formData.form.properties[field] = value;
    },
    editTagHandler() {
      this.tagModal = true;
      this.type = "edit"; // edit tags
      this.tempTags = this.selectedFileData.tags ? JSON.parse(JSON.stringify(this.selectedFileData.tags)) : [];
    },
    async successModalHandler() {
      this.type = 'upload';
      this.isLoadingSuccess = true;
      this.isDisabledCancel = true;

      let files = [];
      for (let index in this.filesToUpload) {
        this.filesToUpload[index].state = 'loading';
        files.push(this.filesToUpload[index]);
      }
      this.$store.commit('loadingStateFilesToUpload', files);

      let response = await this.initializeUploadFiles(this.selectedFolder, this.filesToUpload);
      return response;
    },
    openFolderModalHandler() {
      // this.filesToUpload = [];
      this.$store.commit('resetFilesToUpload');
      this.newFolderDisabledCancel = false;
      this.newFolderLoadingSuccess = false;
      this.newFolderModal = true;
      this.newFolderName = `${this.selectedFolder}/`;
      this.inputFolderName = "";
    },
    closeFolderModalHandler() {
      this.newFolderDisabledCancel = false;
      this.newFolderLoadingSuccess = false;
      this.newFolderModal = false;
      this.invalidFolderName = false;
      this.isValidFolder = true;
    },
    async createFolderHandler() {
      // let current = this.newFolderName;
      // let folder_names = map(this.selectedFolderData.children, "details.file_name");
      this.newFolderDisabledCancel = true;
      this.newFolderLoadingSuccess = true;

      if(!isEmpty(this.newFolderName) && this.newFolderName !== `${this.selectedFolder}/`) {
        let response = await this.createAssetFolder(this.headers, "create", this.newFolderName);
        toastIt(response.type, response.message, InsitesUtil.ops());

        if (response.state) {
          await this.updateNodeTreeSelectedFolder(this.selectedFolderData);
        } else {
          this.invalidFolderName = true;
        }
        this.closeFolderModalHandler();
      } else {
        this.isValidFolder = false;          
        this.newFolderDisabledCancel = false;
        this.newFolderLoadingSuccess = false;
      }
    },
    setNewFolder(value) {
      this.inputFolderName = value.replace(/[ \/\\\:\"\*\?\|]/g, "-");
      this.newFolderName = this.inputFolderName ? `${this.selectedFolder}/${this.inputFolderName}/` : `${this.selectedFolder}/`;
      this.isValidFolder = true;
    },
    closeTagsModalHandler() {
      this.tagModal = false;
    },
    filterTags() {
      let self = this;
      if (!this.listTags) return;

      // split tags & save
      this.listTags.split(",").filter(item => {
        if (!this.tempTags) {
          this.tempTags = [];
        }

        if (item.trim() !== "") {
          !(self.tempTags.indexOf(item) >= 0)
            ? self.tempTags.push(item)
            : '';
        }
      });

      // empty array after push
      this.listTags = "";
    },
    removeTag(item) {
      let indexOf = this.tempTags.indexOf(item);
      if (indexOf != -1) {
        this.tempTags.splice(indexOf, 1);
      }
    },
    applyTags() {
      // required field 'id' = POS id.
      let id = this.selectedFileData.id
      let type = id ? 'update' : 'create';
      // submit & update insites_assets customization - tags
      this.setNewTagData(this.selectedFileData, this.headers, type, this.tempTags, id);
    }
  },
  mounted() {
    this.listAssetFolders();
    this.uploadPath = this.selectedFolder;
    this.$store.commit('resetFilesToUpload');
  },
  destroyed() {
    // const self = this;
  }
};
</script>

<style lang="scss" src="./FileManagerPage.scss">
</style>
