import { mapState } from "vuex";
import ComponentManager from '../mixins/ComponentManager';
import PreviewSelectedFileManager from '../mixins/PreviewSelectedFileManager';

export default {
    name: "NewTagManager",
    mixins: [ComponentManager, PreviewSelectedFileManager],
    data() {
        return {
            tagsList: [],
            tagData: {
                form_configuration_name: "modules/insites_site_manager/insites_assets",
                parent_resource_id: "modules/insites_site_manager/insites_assets",
                form: {
                    properties: {
                        tags: [],
                        is_enabled: "true"
                    }
                }
            },
            fileData: {},
            headers: "",
            method: "post",
            actionUrl: "/api/customizations"
        }
    },
    computed: mapState({
        selectedFolderData: state => state.FileManagerStore.selected_folder_data,
        selectedFile: state => state.FileManagerStore.selected_file
    }),
    methods: {
        setNewTagData(file, headers, type, newTagsList, id) {
            // if(isEmpty(id)) {
            //     console.log("Error: File data not passed correctly.")
            //     toastIt("error", `Error file not found. Please try again.`, InsitesUtil.ops());
            //     return false;
            // } else {
            // }
                this.fileData = file;
                this.headers = headers;
                this.tagsList = newTagsList;
                this.actionUrl = '/api/customizations';

                if(type === 'create') {
                    this.method = 'post';
                } else {
                    this.method = 'put';
                    id ? this.actionUrl = `${this.actionUrl}/${id}` : '';
                }

                this.buildTagData();

        },
        buildTagData() {
            this.tagData.form.properties.physical_path = this.fileData.physical_path;
            this.tagData.form.properties.direct_link = this.fileData.direct_link;
            this.tagData.form.properties.file_name = this.fileData.file_name;
            this.tagData.form.properties.folder = this.fileData.physical_path.split("/").join("|");
            this.tagData.form.properties.tags = this.tagsList;
            this.tagData.form.properties.is_enabled = this.method === 'delete' ? "false" : "true";

            let data = JSON.stringify(this.tagData);
            this.submitTagData(data);
        },
        async submitTagData(data) {
            let self = this;

            await this.$http[this.method](this.actionUrl, data, { headers : this.headers })
            .then(async res => {
                this.$store.commit('updateTags', this.tagsList);
                let text = this.method === 'post' ? 'created' : 'updated';
                text = this.method === 'delete' ? 'deleted' : text;
                toastIt("success", `Tags has been ${text}`, InsitesUtil.ops());
                await self.updateNodeTreeSelectedFolder(self.selectedFolderData);
                await self.setFileCustomization(self.selectedFile);                
                self.updateImagePreview(self.selectedFile);
                return true;
            })
            .catch(error => {
                let text = this.method === 'post' ? 'creating' : 'updating';
                text = this.method === 'delete' ? 'deleting' : text;
                toastIt("error", `Error ${text} tags. Please try again.`, InsitesUtil.ops());
                return false;
            });
        }
    }
}