// general resources
import { mapState } from "vuex";
import InsitesMixins from './InsitesMixins';

// file manager module resources
import SiteManagerService from "./SiteManagerService";
import ComponentManager from './ComponentManager';
import { getAssetsFolderJSON, getAllAssetsCustomizationDataJSON } from '../services/SiteManagerServices';
import { processRequestAndResponse, getCurrentUser } from "../services/CoreServices";

export default {
    computed: mapState({
        selectedFolderData: state => state.FileManagerStore.selected_folder_data,
        filesToUpload: state => state.FileManagerStore.files_to_upload,
        vclInitialized: state => state.VclStore.vclInitialized,
        vclMasterData: state => state.VclStore.vclMasterData,
    }),
    mixins: [SiteManagerService, InsitesMixins, ComponentManager],
    methods: {
        async initializeUploadFiles(path, files, counter) {
            let self = this;            
            counter = !counter ? 0 : counter;
            if (counter < files.length) {
                if (window.location.hostname === "localhost") {
                    await self.testLocalhostUpload(path, files[counter]);
                } else {
                    await self.uploadFile(path, files[counter]);
                }

                self.filesToUpload[counter].state = 'success';
                counter++;
                self.$store.commit('successStateFilesToUpload', self.filesToUpload);
                self.initializeUploadFiles(path, files, counter);
            } else {
                setTimeout(async () => {
                    document.querySelector('.close-modal span').click();
                    await self.updateNodeTreeSelectedFolder(self.selectedFolderData);
                    toastIt("success",  `${files.length} file(s) successfully uploaded.`, self.ops);
                }, 1000);
            }
        },
        async getUploadFileMKPSyncHeaders() {
            // temporary
            let response = await getCurrentUser();

            if (response.state) {
                return new Promise(resolve => {
                    resolve({
                        "Authorization": `Token ${this.vclMasterData.info.marketplace_kit_token}`,
                        "From": response.results.current_user.email
                    });
                });
            } else {
                return false;
            }
        },
        convertFileToBase64(file) {
            return new Promise((resolve, reject) => {
                const reader = new FileReader();
                reader.readAsDataURL(file);
                reader.onload = () => resolve(reader.result);
                reader.onerror = error => reject(error);
            });
        },
        getUploadFileMKPSyncBody(path, file) {
            let self = this;
            return new Promise(async resolve => {
                let body = new FormData();

                await self.convertFileToBase64(file)
                    .then(base64 => {
                        body.append("marketplace_builder_file_body", InsitesUtil.getBase64Blob(base64));
                    });
                body.append("path", `${path}/${file.name}`);

                resolve(body);
            });
        },
        testLocalhostUpload() {
            return new Promise(resolve => {
                setTimeout(function() {
                    resolve();
                }, 1500);
            });
        },
        uploadDone(files) {
            return new Promise(resolve => {
                resolve({ state: true, type: "success", results: `${files.length} file(s) successfully uploaded.` });
            });
        },
        uploadFile(path, file) {
            let self = this;

            return new Promise(async resolve => {
                let is_uploaded = await self.uploadFileMKPSync(path, file);
                let physical_path = self.convertToPhysicalPath(`${path}/${file.name}`);
                let folder = self.convertToFolder(path);

                if (is_uploaded.state) {
                    // get direct URL from instance asset_url
                    let assets = await getAssetsFolderJSON();
                    
                    // check for exisiting customization data 
                    let file_customization = await getAllAssetsCustomizationDataJSON('physical_path', physical_path);
                    let data = self.setFileData(folder, file.name, assets.results.items.results[physical_path], path);
                    let customization_id = self.getCustomizationID(file_customization);

                    await self.updateAssetsCustomization(data, customization_id);
                    resolve({ state: true });
                } else {
                    resolve({ state: false });               
                }
            });
        },
        updateAssetsCustomization(data, customization_id) {
            let self = this;
            let headers = self.apiHeader();
            let type = customization_id ? 'update' : 'upload';

            let actionUrl = type === 'update' ? `/api/customizations/${customization_id}` : '/api/customizations';

            return new Promise(resolve => {
                self.managePOSCustomization(actionUrl, data, headers, type)
                    .then(results => {
                        resolve({ state: true, results: results });
                    }).catch(error => {
                        resolve({ state: false, results: error });
                    });
            });
        },
        async uploadFileMKPSync(path, file) {
            let self = this;
            let url = "/api/marketplace_builder/marketplace_releases/sync";
            let headers = await self.getUploadFileMKPSyncHeaders();
            let body = await self.getUploadFileMKPSyncBody(path, file);
            let response = await processRequestAndResponse(url, 'put', body, headers);

            return new Promise(resolve => {
                resolve({ state: response.state, results: response.results });
            });
        },
        getCustomizationID(file_customization) {
            if (file_customization.results.items.total_entries) {
                return file_customization.results.items.results[0].id;
            } else {
                return "";
            }
        },
        setFileData(folder, file_name, direct_link, path) {
            let data = {};
            data.name = file_name;
            data.direct_link = direct_link;
            data.tags = [];
            data.folder = folder;
            data.is_enabled = true;

            return this.setFileCustomizationData("upload", data, path);
        }
    }
}
