<template>
    <ins-toggle-switch ref="insToggleEl"
        v-bind:label="label"
        v-bind:value="value"
        v-bind:disabled="disabled"  
        v-bind:checked="value || value === 'true'"
        @onCheckInsToggleSwitch="onValueChange">            
    </ins-toggle-switch>
</template>

<script>
export default {
    name: "InsToggleWrap",
    props: {
        label: String,
        disabled: Boolean,
        checked: Boolean,
        value: Boolean
    },
    methods: {
        onValueChange(event) {
            // console.log(event.detail);
            this.$emit("input", event.detail.checked)
        }
    }
}
</script>

<style lang="scss" src="./InsToggleWrap.scss"></style>