import InsitesMixins from './InsitesMixins';

export default {
    mixins: [InsitesMixins],
    methods: {
        setPhysicalPath(dir, name) {
            let file_name = name.trim().replace(/[ -]/g, "_").replace(/[^a-zA-Z0-9_]/g,"").toLowerCase();
            return file_name !== ''? `modules/ins_site_manager/public/views/${dir}/${file_name}.liquid`: '';
        },
        setModuleName(name) {
            name = InsitesUtil.removeWhitespace(name);
            let module_name = name.replace(/[^\/a-zA-Z0-9]/g,"_").toLowerCase();
            return module_name !== '' ? module_name : '';
        },
        capitalize(item) {
            return this.checkValue(item).replace(/_/g," ").split(' ').map((s) => s.charAt(0).toUpperCase() + s.substring(1)).join(' ');
        },
        setFileCustomizationData(type, fileData, selectedFolder) {
            let data = {
                form_configuration_name: "modules/insites_site_manager/insites_assets",
                parent_resource_id: "modules/insites_site_manager/insites_assets",
                form: {
                    properties: {
                        physical_path: this.convertToPhysicalPath(`${selectedFolder}/${fileData.name}`),
                        direct_link: fileData.direct_link,
                        file_name: fileData.name,
                        folder: this.convertToFolder(selectedFolder),
                        tags: fileData.tags,
                        is_enabled: type === "delete" ? "false" : "true"
                    }
                }
            }
            return JSON.stringify(data);
        },
        managePOSCustomization(url, data, head, type) {
            type = type === 'upload' ? 'post' : 'put';
            return this.$http[type](url, data, { headers: head });
        },
        normalizeName(name) {
            let module_name = name.replace(/_/g," ").split(' ').map((s) => s.charAt(0).toUpperCase() + s.substring(1)).join(' ');
            return module_name !== '' ? module_name : '';
        },
        escapeContent(data) {
            return data.replace(/['"]/g, '\\$&').toString();
        }
    }
}
