const RedactorStore = {
    state: {
        redactor_doctype_html: "",
        redactor_html: "",
        redactor_body: "",
        redactor_style: "",
        redactor_body_tag: "",
        redactor_head: ""
    },
    mutations: {
        updateRedactorDoctypeHTML(state, new_state) {
            state.redactor_doctype_html = "";
            state.redactor_doctype_html = new_state;
        },
        updateRedactorHTML(state, new_state) {
            state.redactor_html = "";
            state.redactor_html = new_state;
        },
        updateRedactorBody(state, new_state) {
            state.redactor_body = "";
            state.redactor_body = new_state;
        },
        updateRedactorStyle(state, new_state) {
            state.redactor_style = "";
            state.redactor_style = new_state;
        },
        updateRedactorBodyTag(state, new_state) {
            state.redactor_body_tag = "";
            state.redactor_body_tag = new_state;
        },
        updateRedactorHead(state, new_state) {
            state.redactor_head = "";
            state.redactor_head = new_state;
        }
    }
};

export default RedactorStore;
