<template>
<li class="fm-node-tree">
  <div :class="`${folders.selected_folder === nodes.details.id ? 'fm-node-tree--selected ' : ' '}fm-node-tree--container`" :style="`padding-left: ${16 * nodes.details.id.split('/').length}px`">
    <span v-if="nodes.details.is_loading" class="spinner-node-tree">
      <i class="spinner"></i>
    </span>
    <span v-else-if="nodes.details.id === root_folder" class="root-node-tree">
      <i class="icon-site-manager"></i>
    </span>
    <span v-else-if="!nodes.details.has_children" class="no-child-node-tree">
      <i>&nbsp;</i>
    </span>
    <span v-else class="has-child-node-tree" @click="toggleSelectedArrow($event, nodes)">
      <i :class="nodes.details.is_open ? 'icon-caret-down' : 'icon-caret-right'"></i>
    </span>
    <p @click="toggleSelected($event, nodes)" :id="`${nodes.details.id.replace(/\//g, '-')}`"><i class="icon-folder"></i> {{ nodes.details.file_name }}</p>
  </div>
  <ul v-if="nodes.details.is_open">
    <child-node v-for="(i, key) in nodes.children" :nodes="i" v-bind:key="key"></child-node>
  </ul>
</li>

</template>

<script>
// general resources
import { mapState } from 'vuex';
import InsitesMixins from '../../../mixins/InsitesMixins';

// file manager resources
import ComponentManager from '../../../mixins/ComponentManager';
import SelectedFolderManager from '../../../mixins/SelectedFolderManager';
import SelectedFilesManager from '../../../mixins/SelectedFilesManager';
// import MarketPlaceSync from '../../../mixins/MarketPlaceSync';
// import { getAssetsFolder } from '../../../services/FMAPIService';

export default {
  name: 'child-node',
  mixins: [InsitesMixins, SelectedFolderManager, SelectedFilesManager, ComponentManager],
  props: {
    nodes: Object
  },
  data() {
    return {
      toggled_parent: false,
      filter_by: "physical_path",
      selected: '',
      items: {}
    };
  },
  computed: mapState({
    folders: state => state.FileManagerStore,
    selectedFolder: state => state.FileManagerStore.selected_folder,
    selectedFolderData: state => state.FileManagerStore.selected_folder_data,
    searchKeyword: state => state.FileManagerStore.searchKeyword,
    assetsURL: state => state.FileManagerStore.assets_url
  }),
  methods: {
    addNewPageHandler() {},
    toggleParentNode() {
      this.toggled_parent = !this.toggled_parent;
    },
    async toggleSelected(event, data) {
      document.querySelector(".ins-form-field").value = "";
      this.$store.commit("updateSearchKeyword", "");
      this.$store.commit("updateFileManagerSearchResults", []);
      if (data.details.id === this.root_folder) {
        this.updateFilesOfSelectedFolder(this.assetsURL.results.items.results, data);
      } else if (data.details.is_open) {
        this.selectedFolder === data.details.id ? data.details.is_open = false : this.$store.commit('resetFileManagerSelectedFile');
        this.updateFilesOfSelectedFolder(this.assetsURL.results.items.results, data);
      } else if (this.selectedFolder !== data.details.id || !data.details.is_open) {
        this.$store.dispatch('resetFMList');
        await this.updateNodeTreeSelectedFolder(data, this.assetsURL);
      } else {
        data.details.is_open = data.details.id === this.root_folder;
      }
      this.$store.dispatch("updateSelectedFolder", data);
    },
    toggleSelectedArrow(event, data) {
      let toggle = data.details.is_open ? false : true;
      this.toggleFolder(data, toggle)
    },
    toggleFolder(data, is_open) {
      if (!data.children.length) {
        this.updateChildrenOfSelectedAssetFolder(this.assetsURL.results.items.results, data);
      }
      data.details.is_open = is_open;
    }
  },
  mounted() {
    const self = this;
    self.toggleParentNode();
  }
};
</script>

<style lang="scss" src="./FMNodeTree.scss">
</style>
