<template>
  <div class="grid-container-full fm-container">
    <!-- panel -->
    <div class="grid-x">
      <div class="panel small-12 cell">
        <FMToolbar
    		v-on:openModal="openModalHandler"
    		v-on:createFolder="createFolderHandler"
    		v-on:editTag="editTagHandler"
				v-on:deleteFile="deleteFileHandler" />
      </div>
    </div>
    <div class="grid-x fm-body-wrap">
      <div id="fm-panel-tree" class="panel medium-4 cell">

				<!-- LOADER -->
        <div>
					<ins-loader
						v-if="loader.is_loading"
            state-title="Loading"
            state-message=""
            state-icon="processing">
          </ins-loader>

					<ins-loader
						v-if="isMarketPlaceBuilderEmpty"
            state-title=""
            state-message=""
            state-icon="">
          </ins-loader>
        </div>

				 <!-- TREE -->
        <div v-if="!isMarketPlaceBuilderEmpty">
          <FMTree />
        </div>
      </div>
      <div id="fm-panel-list" class="panel medium-4 cell">
        <FMList />
      </div>
      <div id="fm-panel-preview" class="panel medium-4 cell">
        <FMPreview
					v-on:editTag="editTagHandler"
					v-on:deleteFile="deleteFileHandler"
					v-on:restoreFile="restoreFileHandler" />
      </div>
    </div>
    <!-- end panel -->
  </div>
</template>

<script>
// general resources
import { mapState } from 'vuex';
import InsitesMixins from '../../../mixins/InsitesMixins';

// file manager resources
import Split from 'split.js';
import ArchiveFileManager from '../../../mixins/ArchiveFileManager';
import FMTree from '../FMTree/FMTree.vue';
import FMToolbar from '../FMToolbar/FMToolbar.vue';
import FMList from '../FMList/FMList.vue';
import FMPreview from '../FMPreview/FMPreview.vue';

export default {
	name: 'FMParent',
	mixins: [InsitesMixins, ArchiveFileManager],
	components: {
		FMTree,
		FMToolbar,
		FMList,
		FMPreview
	},
	data() {
		return {
			loader: {
				is_loading: true
			},
		};
	},
	computed: mapState({
    toggleView: state => state.FileManagerStore.toggleView,
    selectedFile: state => state.FileManagerStore.selected_file,
          selectedFolderData: state => state.FileManagerStore.selected_folder_data,
    isMarketPlaceBuilderEmpty() {
      const self = this;
      let file_manager_data_structure = self.$store.getters.getFileManagerDataStructure;
      if(file_manager_data_structure.children.length === 0) {
        self.loader.is_loading = false;
        return true;
      } else {
        self.loader.is_loading = false;
        return false;
      }
    },
	}),
	methods: {
		openModalHandler() {
			this.$emit('openModal');
		},
		createFolderHandler() {
			this.$emit('openFolderModal');
		},
		editTagHandler() {
			this.$emit('openTagModal');
		},
		restoreFileHandler() {
      let self = this;
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="green"
            state-message="Are you sure you want to restore ${this.selectedFile.file_name}?">
          </ins-loader>`,
        type: "",
        showCancelButton: true,
        confirmButtonClass: "green",
        confirmButtonText: "Restore"
      }).then(result => {
          if(result.value) {
            // function restore File
            self.restoreCustomizationData();
          }
      });
		},
		deleteFileHandler() {
			this.confirmDeleteFile();
		},
		confirmDeleteFile() {
      let self = this;
      swal({
        title: "",
        html: `
          <ins-loader
            state-icon="warning"
            icon-color="warning"
            state-message="Are you sure you want to archive ${this.selectedFile.file_name}?">
          </ins-loader>`,
        type: "",
        showCancelButton: true,
        confirmButtonClass: "negative",
        confirmButtonText: "Archive"
      }).then(result => {
        if(result.value) {
          // function delete File
          self.deleteFileCustomizationData();
        }
      });
    },
	},
	mounted() {
    const self = this;
		Split(['#fm-panel-tree', '#fm-panel-list', '#fm-panel-preview'], {
			sizes: [15, 65, 20],
			minSize: 280
		});
    self.$store.dispatch('resetFMContainer');    
	}
};
</script>

<style lang="scss" src="./FMContainer.scss"></style>
