
import InsitesMixins from "./InsitesMixins";
import { getAllAssetsCustomizationDataJSON } from '../services/SiteManagerServices';

export default {
    name: "PreviewSelectedFileManager",
    mixins: [InsitesMixins],
    methods: {
        async setFileCustomization(file) {
            let self = this;
            let response = await getAllAssetsCustomizationDataJSON("physical_path", file.physical_path);

            return new Promise(async resolve => {
                if (response.state) {
                    if (response.results.items.results.length) {
                        if (window.location.hostname === "localhost") {
                            let local_response = self.localhostAssetManagerGetFileData(file.physical_path, response);
                            self.appendCustomizationData(file, local_response.id ? local_response : {});
                        } else {
                            self.appendCustomizationData(file, response.results.items.results[0]);
                        }
                        resolve({ state: true, results: response.results.items.results[0] })
                    } else {
                        self.$store.commit("updateFileManagerSelectedFile", file);
                        resolve({ state: true, results: file });
                    }
                } else {
                    console.log('error', response.results)
                }
            });
        },
        appendCustomizationData(file, customizationData) {
            for (let key in customizationData) {
                file[key] = customizationData[key];
            }

            if (customizationData.id) {
                file.customization_date = + new Date(file.updated_at) / 1000;
                file.updated_at = InsitesUtil.formatLocalisation(file.updated_at);
            }
            
            this.$store.commit("updateFileManagerSelectedFile", file);
        }
    }
}
