<template>
<div class="grid-container-full mvc-wrap">
  <div class="grid-x">
    <div class="large-8 medium-12 small-12 cell">
      
      <ins-card steady v-if="!vclInitialized">
        <div class="spacer large"></div>
        <ins-loader
          state-title="Processing Your Data"
          state-message="Just a moment, we're fetching and processing your data."
          state-icon="processing">
        </ins-loader>
        <div class="spacer large"></div>
      </ins-card>

      <div class="custom-ins-card" v-if="vclInitialized">

        <InsSelectWrap label="Admin User*" searchable
          placeholder="Please select an Admin User"
          v-if="vclFormType === 'new' && adminUsersNoProfile.length"
          v-bind:has-error="errors.admin_user_id ? true:false"
          v-bind:error-message="errors.admin_user_id"
          v-bind:readonly="vclFormType === 'update'"
          v-model="tempAdminUserId"
          v-on:fieldError="fieldErrorHandler">
          
          <ins-select-option
            v-for="option in adminUsersNoProfile"
            v-bind:key="option.id"
            v-bind:label="`${option.first_name} ${option.last_name}`"
            v-bind:value="option.id">
          </ins-select-option>
        </InsSelectWrap>

        <InsInputWrap label="Admin User*" readonly
          placeholder="Please enter an Admin User" maxlength="120"
          v-if="vclFormType === 'update'"
          v-model="selectedUserName">
        </InsInputWrap>

        <InsInputWrap label="Access Key*"
          placeholder="Please enter an Access Key"
          required="true" maxlength="120"
          v-if="vclMasterData.info.git_url.includes('codecommit')"
          v-model="tempAccessKey"
          v-bind:validate="true"
          v-bind:has-error="errors.access_key ? true:false"
          v-bind:error-message="errors.access_key"
          v-on:fieldError="fieldErrorHandler">
        </InsInputWrap>

        <InsTextareaWrap 
          label="Private Key*"
          placeholder="Please enter the Private Key"
          v-bind:has-error="errors.id_rsa ? true:false"
          v-bind:error-message="errors.id_rsa"
          v-model="tempIdRsa">
        </InsTextareaWrap>

        <div class="ins-note-wrap">
          <em>Please note that the Private Key {{ vclMasterData.info.git_url.includes('codecommit') ? 'and Access Key ':''}}will be stored securely on our Insites Data Storage and will not be displayed here for security reasons.</em>
        </div>
      </div>
    </div>
  </div>
  <div class="grid-x">
    <div class="large-4 medium-6 small-12 cell">
      <div class="spacer small"></div>

      <ins-button label="ARCHIVE"
        color="information-1" size="normal"
        options="DELETE"
        v-if="vclFormType === 'update'"
        v-on:onClickInsButton="archiveUser"
        v-on:onClickInsButtonOption="deleteUser">
      </ins-button>
    </div>
    <div class="large-4 medium-6 small-12 cell text-right">
      <div class="spacer small"></div>

      <ins-button label="CANCEL" outlined
        v-on:onClickInsButton="goBackToListView">
      </ins-button>

      <ins-button solid
        v-if="vclInitialized"
        v-bind:label="`TEST AND ${buttonLabel}`"
        v-bind:disabled="sendingForm"
        v-on:onClickInsButton="validateData">
      </ins-button>

    </div>
  </div>
</div>
</template>

<script>
import InsitesService from '../../mixins/InsitesService';
import InsInputWrap from '../../components/InsInputWrap/InsInputWrap.vue';
import InsSelectWrap from '../../components/InsSelectWrap/InsSelectWrap.vue';
import InsTextareaWrap from '../../components/InsTextareaWrap/InsTextareaWrap.vue';

import { mapState } from 'vuex';
import { 
  sidebarMenuRoute,
  listVclRoute,
  newVclUserRoute,
  vclUpdateUserRoute
} from '../../models/VclRoutes';

import { 
  createVclUser,
  mutateUserVclProfile,
  updateVclUser,
  deleteVclUser
} from '../../services/VclService';

export default {
  name: "ManageVcl",
  mixins: [InsitesService],
  components: {
    InsInputWrap,
    InsSelectWrap,
    InsTextareaWrap
  },
  computed: mapState({
    instanceUrl: state => state.VclStore.instanceUrl,
    adminUsersNoProfile: state => state.VclStore.adminUsersNoProfile,
    selectedAdminUser: state => state.VclStore.selectedAdminUser,
    vclFormType: state => state.VclStore.vclFormType,
    vclMasterData: state => state.VclStore.vclMasterData,
    vclInitialized: state => state.VclStore.vclInitialized
  }),
  data() {
    return {
      buttonLabel: "ADD",
      errors: {
        admin_user_id: '',
        accessKey: '',
        id_rsa: '',
      },
      sendingForm: false,
      tempAdminUserId: '',
      tempIdRsa: '',
      tempAccessKey: '',
      selectedUserName: '',
      deletingUser: false
    }
  },
  methods: {
    fieldErrorHandler(event){
      event.target.$el.hasError = event.error;
      event.target.$el.errorMessage = (`${event.target.$el.label} is required.`).replace('*','');
    },
    gitErrorHandler(event){
      let input = event.target.$el.value;

      event.target.$el.hasError = event.error;
      event.target.$el.errorMessage = (`${event.target.$el.label} is required.`).replace('*','');

      if (input){
        if(!this.isGitUrl(input)){
          event.target.$el.hasError = true;
          event.target.$el.errorMessage = 'Not a valid Repository URL';
        } else if(!this.isGitUrlSupported(input)){
          event.target.$el.hasError = true;
          event.target.$el.errorMessage = 'Repository not yet supported.';
        }
      }
    },
    hasRequiredValues(){
      let errorsCount = 0;

      if (this.tempAdminUserId === ''){
        this.errors.admin_user_id = 'Admin User is required.';
        errorsCount++;
      } else {
        this.errors.admin_user_id = '';
      }
      
      if (this.vclMasterData.info.git_url.includes('codecommit')){

        if (this.tempAccessKey === ''){
          this.errors.access_key = 'Access Key is required.';
          errorsCount++;
        } else {
          this.errors.access_key = '';
        }
      }

      if (this.tempIdRsa === ''){
        this.errors.id_rsa = 'Private Key is required.';
        errorsCount++;
      } else {
        this.errors.id_rsa = '';
      }

      return errorsCount ? false:true;
    },
    validateData(){
      this.sendingForm = true;
      if (
        this.$store.getters.getCurrentUserId === this.selectedAdminUser.id ||
        this.vclFormType === "new"
      ){
        if (this.hasRequiredValues()){
          this.sendData();
        } else {
          this.sendingForm = false;
        }
      } else {
        this.notAllowed("configure");
      }
    },
    checkAccessKey(){
      let access_key = "deleted";
      if (this.vclMasterData.info.git_url.includes('codecommit')){
        access_key = this.tempAccessKey;
      }
      return access_key;
    },
    constructPayload(){
      let adminUser = this.$store.getters.getAdminUserById(this.tempAdminUserId);
      let access_key = this.checkAccessKey();
      return {
        cred: {
          instance_url: this.instanceUrl,
          insites_key: this.selectedAdminUser.insites_vcl_user_profile.properties.insites_key,
          info: {
            access_key,
            master_key: false,
            email: adminUser.email,
            id_rsa: this.tempIdRsa,
            admin_user_id: this.tempAdminUserId,
            git_url: this.vclMasterData.info.git_url,
            last_update_by: this.$store.getters.getCurrentUserId
          }
        }
      }
    },
    constructUpdatePayload(){
      let access_key = this.checkAccessKey();
      return {
        cred: {
          instance_url: this.instanceUrl,
          insites_key: this.selectedAdminUser.insites_vcl_user_profile.properties.insites_key,
          info: {
            access_key,
            id_rsa: this.tempIdRsa,
            admin_user_id: this.tempAdminUserId,
            last_update_by: this.$store.getters.getCurrentUserId,
            git_url: this.vclMasterData.info.git_url
          }
        }
      }
    },
    sendData(){
      this.sendingForm = true;

      switch (this.vclFormType){
        case 'new':
          this.createUser();
          break;
        
        case 'update':
          this.updateUser();
          break;
      }
    },
    async createUser(){
      let payload = this.constructPayload();
      let data = await createVclUser(payload);

      if (data.state && data.results.insites_key){
        payload.cred.insites_key = data.results.insites_key;
        payload.cred.info.is_enabled = "true";
        return await this.runMutation(payload, "configure");
      }

      if (data.results.response.data.message){
        return this.showPermissionError();
      }

      this.showError('create');
    },
    async updateUser(){
      // TODO: Create link when admin user change email address with VCL Profile
      let payload = this.constructUpdatePayload();
      let data = await updateVclUser(payload);
      if (data.state){

        payload.cred.info.is_enabled = "true";
        return await this.runMutation(payload, "configure");
      }

      if (data.results.response.data.message){
        return this.showPermissionError();
      }

      this.showError('update');
    },
    deleteUser(){
      this.deletingUser = true;
      this.alertUser('delete', 'Delete');
    },
    archiveUser(){
      this.alertUser('archive', 'Archive');
    },
    async alertUser(word, buttonText){
      if (this.$store.getters.getCurrentUserId === this.selectedAdminUser.id){
        swal({
          title: "",
          html: `
            <ins-loader
              state-icon="warning"
              icon-color="warning"
              state-message="Are you sure you want to ${word} this user?">
            </ins-loader>`,
          type: "",
          width: 330,
          showCancelButton: true,
          confirmButtonClass: "negative",
          confirmButtonText: buttonText
        }).then(async result => {
          if (result.value){
            this.sendingForm = true;
            let payload = this.constructUpdatePayload();
            payload.cred.info.is_enabled = "false";
            delete payload.cred.info.access_key;
            delete payload.cred.info.id_rsa;

            let data = this.deletingUser
              ? await deleteVclUser(payload)
              : { state: true }

            if (data.state){
              return await this.runMutation(payload, word);
            }
            this.showError(word);
          }
        });
      } else {
        this.notAllowed(word);
      }
    },
    notAllowed(word){
      this.deletingUser = false;
      this.sendingForm = false;
      swal({
          html: `
          <ins-loader
              state-icon="warning"
              icon-color="warning"
              state-message="You can only ${word} your own profile.">
          </ins-loader>`,
          width: 330,
      });
    },
    async runMutation(payload, word){
      if (this.deletingUser){
        payload.cred.insites_key = "deleted";
      }
      let res = await mutateUserVclProfile(payload);
      if (res.state){
        toastIt('success', `Successfully ${word}d vcl user.`, InsitesUtil.ops());
        if (this.vclFormType === "new") {
          this.$store.commit('updateAdminUsersNoProfile', this.tempAdminUserId);
        } else if (this.deletingUser){
          this.$store.commit('addAdminUsersNoProfile', this.selectedAdminUser);
        }
        this.sendingForm = false;
        this.deletingUser = false;
        return this.goBackToListView();
      }
      this.showError(word);
    },
    showPermissionError(){
      this.sendingForm = false;
      toastIt('error', `Permission denied. Please check your credentials and try again.`, InsitesUtil.ops());
    },
    showError(word){
      this.deletingUser = false;
      this.sendingForm = false;
      toastIt('error', `Failed to ${word} user. Please try again.`, InsitesUtil.ops());
    },
    goBackToListView(){
      this.$router.push("/version-control");
      InsitesUtil.goBack();
    },
    initialisePage(){
      let routes = [sidebarMenuRoute, listVclRoute];

      if (this.vclFormType === "update"){
        this.tempAdminUserId = this.selectedAdminUser.id;
        this.selectedUserName = `${this.selectedAdminUser.first_name} ${this.selectedAdminUser.last_name}`;

        this.buttonLabel = "UPDATE";
        vclUpdateUserRoute.link = `#${this.$route.path}`;
        routes.push(vclUpdateUserRoute);

      } else if (
        this.vclFormType === "new" ||
        this.$router.currentRoute.name === "VclNewUser"
      ){
        
        this.$store.commit('setVclFormType', 'new');
        routes.push(newVclUserRoute);

      } else {
        this.goBackToListView()
        return;
      }

      InsitesUtil.setBreadCrumbs(routes);
    }
  },
  mounted(){
    this.initialisePage();
  },
  destroyed() {
    this.$store.commit("setVclFormType", '');
    this.$store.commit('resetSelectedAdminUser');
  },
}
</script>
<style lang="scss" src="./ManageVcl.scss"></style>