import VclCustomisationMasterModel from '../models/VclCustomisationMasterModel';
import VclUserModel from '../models/VclUserModel';

import {
  getCustomisationMaster,
  getAdminUsers,
  getInstanceUrl,
  getVclUser
} from '../services/VclService';

import { getCurrentUser } from "../services/CoreServices";
import remove from 'lodash/remove';

const VclStore = {
  state: {
    instanceUrl: '',
    vclInitialized: false,

    // VCL Master States
    customisationMasterData: JSON.parse(JSON.stringify(VclCustomisationMasterModel)),
    vclMasterData: false,

    // VCL Form States
    vclFormType: '',
    adminUsers: {results:[]},
    adminUsersNoProfile: {results:[]},
    selectedAdminUser: JSON.parse(JSON.stringify(VclUserModel)),

    // VCL Current Users
    currentVclUser: false,
    currentUser: false
  },
  getters: {
    getAdminUserById: (state) => (id) => {
      return state.adminUsers.find(user => user.id === id);
    },
    getCurrentUserId: (state) => {
      return state.currentUser.id;
    },
    getCurrentUserEmail: (state) => {
      return state.currentUser.email;
    },
    getCurrentUserName: (state) => {
      return `${state.currentUser.first_name} ${state.currentUser.last_name}`;
    }
  },
  mutations: {
    setInstanceUrl(state, data){
      state.instanceUrl = `https://${data}`;
    },
    setInitializedState(state, data){
      state.vclInitialized = data;
    },
    setCustomisationMaster(state, data){
      state.customisationMasterData = data;
    },
    setVclMaster(state, data){
      state.vclMasterData = data;
    },
    setVclFormType(state, data){
      state.vclFormType = data;
    },
    updateAdminUsersNoProfile(state, id){
      remove(state.adminUsersNoProfile, { "id" : id });
    },
    addAdminUsersNoProfile(state, data){
      state.adminUsersNoProfile.push(data);
    },
    setVclAdminUsers(state, data){
      state.adminUsers = data.results;
      state.adminUsersNoProfile = data.results.filter(user => {
        return !user.insites_vcl_user_profile ||
          user.insites_vcl_user_profile.properties.insites_key === "deleted";
      });
    },
    setCurrentUser(state, data){
      state.currentUser = data;
    },
    setCurrentVclUser(state, data){
      state.currentVclUser = data;
    },
    setSelectedAdminUser(state, data){
      state.selectedAdminUser = data;
    },
    resetSelectedAdminUser(state){
      state.selectedAdminUser = JSON.parse(JSON.stringify(VclUserModel));
    }
  },
  actions: {
    async initializeVcl(context) {

      let custMaster = await getCustomisationMaster();
      if (custMaster){
        context.commit('setCustomisationMaster', custMaster);
        let vclMaster = await getVclUser(
          custMaster.properties.instance_url,
          custMaster.properties.insites_key);

        if (vclMaster.state){
          context.commit('setVclMaster', vclMaster.results.Item);
        }
      }
      context.dispatch('getVclAdminUsers')
      context.commit('setInstanceUrl', await getInstanceUrl());
      
      let data = await getCurrentUser();
      let currentUser = data.results.current_user;
      context.commit('setCurrentUser', currentUser);

      // let currentVclUser = await getVclUser(
      //   currentUser.insites_vcl_user_profile.properties.instance_url,
      //   currentUser.insites_vcl_user_profile.properties.insites_key
      // );

      // context.commit('setCurrentVclUser', currentVclUser);
      context.commit('setInitializedState', true);
    },
    async getVclAdminUsers(context){
      context.commit('setVclAdminUsers', await getAdminUsers());
    }
  }
}
export default VclStore;