import ContentBlockModel from '../models/ContentBlockModel';

const ContentBlockStore = {
    state: {
        contentBlockData: {},
        formType: "add"
    },
    mutations: { 
        pushPartialData(state, partial) {
            state.contentBlockData = {};
            state.contentBlockData = partial ? partial.Data : JSON.parse(JSON.stringify(ContentBlockModel));
            if (partial) {
                state.contentBlockData.body = unescape(partial.Data.body)
            } else {
                state.contentBlockData.body = "";
            }
        },
        resetPartialData(state){
            state.contentBlockData = JSON.parse(JSON.stringify(ContentBlockModel));
        },
        pushFormType(state, formType) {
            state.formType = formType;
        }        
    }
}
export default ContentBlockStore;