        "form[properties_attributes][loc_3_thu_close]": this.checkValue(this.gssData.loc_3_thu_close),
        "form[properties_attributes][loc_3_fri_enable]": this.checkValue(this.gssData.loc_3_fri_enable),
        "form[properties_attributes][loc_3_fri_open]": this.checkValue(this.gssData.loc_3_fri_open),
        "form[properties_attributes][loc_3_fri_close]": this.checkValue(this.gssData.loc_3_fri_close),
        "form[properties_attributes][loc_3_sat_enable]": this.checkValue(this.gssData.loc_3_sat_enable),
        "form[properties_attributes][loc_3_sat_open]": this.checkValue(this.gssData.loc_3_sat_open),
        "form[properties_attributes][loc_3_sat_close]": this.checkValue(this.gssData.loc_3_sat_close),
        "form[properties_attributes][loc_3_sun_enable]": this.checkValue(this.gssData.loc_3_sun_enable),
        "form[properties_attributes][loc_3_sun_open]": this.checkValue(this.gssData.loc_3_sun_open),
        "form[properties_attributes][loc_3_sun_close]": this.checkValue(this.gssData.loc_3_sun_close),
        "form[properties_attributes][loc_1_phone]": this.checkValue(this.gssData.loc_1_phone),
        "form[properties_attributes][loc_3_phone_country_code]": this.checkValue(this.gssData.loc_3_phone_country_code),
        "form[properties_attributes][loc_3_phone_area_code]": this.checkValue(this.gssData.loc_3_phone_area_code),
        "form[properties_attributes][loc_3_phone_number]": this.checkValue(this.gssData.loc_3_phone_number),
        "form[properties_attributes][loc_3_fax]": this.checkValue(this.gssData.loc_3_fax),
        "form[properties_attributes][loc_3_fax_country_code]": this.checkValue(this.gssData.loc_3_fax_country_code),
        "form[properties_attributes][loc_3_fax_area_code]": this.checkValue(this.gssData.loc_3_fax_area_code),
        "form[properties_attributes][loc_3_fax_number]": this.checkValue(this.gssData.loc_3_fax_number),
        "form[custom_addresses_attributes][loc_3_address_attributes][address]": this.checkValue(this.gssData.loc_3_address.address),
        "form[custom_addresses_attributes][loc_3_address_attributes][address2]": this.checkValue(this.gssData.loc_3_address.address2),
        "form[custom_addresses_attributes][loc_3_address_attributes][city]": this.checkValue(this.gssData.loc_3_address.city),
        "form[custom_addresses_attributes][loc_3_address_attributes][state]": this.checkValue(this.gssData.loc_3_address.state),
        "form[custom_addresses_attributes][loc_3_address_attributes][country]": this.checkValue(this.gssData.loc_3_address.country),
        "form[custom_addresses_attributes][loc_3_address_attributes][postcode]": this.checkValue(this.gssData.loc_3_address.postcode),
        "form[properties_attributes][po_1_num]": this.checkValue(this.gssData.po_1_num),
        "form[custom_addresses_attributes][po_1_address_attributes][address]": this.checkValue(this.gssData.po_1_address.address),
        "form[custom_addresses_attributes][po_1_address_attributes][address2]": this.checkValue(this.gssData.po_1_address.address2),
        "form[custom_addresses_attributes][po_1_address_attributes][city]": this.checkValue(this.gssData.po_1_address.city),
        "form[custom_addresses_attributes][po_1_address_attributes][state]": this.checkValue(this.gssData.po_1_address.state),
        "form[custom_addresses_attributes][po_1_address_attributes][country]": this.checkValue(this.gssData.po_1_address.country),
        "form[custom_addresses_attributes][po_1_address_attributes][postcode]": this.checkValue(this.gssData.po_1_address.postcode),
        "form[properties_attributes][po_2_num]": this.checkValue(this.gssData.po_2_num),
        "form[custom_addresses_attributes][po_2_address_attributes][address]": this.checkValue(this.gssData.po_2_address.address),
        "form[custom_addresses_attributes][po_2_address_attributes][address2]": this.checkValue(this.gssData.po_2_address.address2),
        "form[custom_addresses_attributes][po_2_address_attributes][city]": this.checkValue(this.gssData.po_2_address.city),
        "form[custom_addresses_attributes][po_2_address_attributes][state]": this.checkValue(this.gssData.po_2_address.state),
        "form[custom_addresses_attributes][po_2_address_attributes][country]": this.checkValue(this.gssData.po_2_address.country),
        "form[custom_addresses_attributes][po_2_address_attributes][postcode]": this.checkValue(this.gssData.po_2_address.postcode)
      }

      this.parsedFormData = this.appendToFormData(this.formData);
    },
    async updateGssFormHandler(){
      this.submittingForm = true;
      this.buildFormData();

       if(this.s3IconChange) {
          this.s3IconImage = await this.getS3AssetURL(this.s3FormCredentialURL,'company_icon', this.s3IconImage , this.IconName);
          this.parsedFormData.append("form[custom_images_attributes][company_icon_attributes][image]",this.s3IconImage);
          this.s3IconChange = false;
        }

       if(this.s3LogoChange) {
          this.s3LogoImage = await this.getS3AssetURL(this.s3FormCredentialURL,'company_logo', this.s3LogoImage, this.LogoName);  
          this.parsedFormData.append("form[custom_images_attributes][company_logo_attributes][image]",this.s3LogoImage);
          this.s3LogoChange = false;
        }  
        this.submitForm();     
    },
    submitForm() {
      let url = `/api/customizations${this.formMethod === 'POST' ? '':`/${this.formID}`}`;
      let method = this.formMethod === 'POST' ? 'post':'put';

      this.$http[method](url, this.parsedFormData, {headers: this.headers})
        .then(res => {
          this.submittingForm = false;
          parent.toastIt("success", "Global Content has been updated. Please refresh the page to see the changes.", InsitesUtil.ops());

          //setTimeout(() => {
          //  window.location.reload()
          // }, 1000);

        }).then(() => {

          // hotfix double refresh
          this.$store.state.GssStore.gssData['company_logo'].original = this.s3LogoImage;
          this.$store.state.GssStore.gssData['company_icon'].original = this.s3IconImage;

        }).catch(error => {
          this.submittingForm = false;
          console.error('error', error);
          parent.toastIt("error", "Failed to update settings.", InsitesUtil.ops());
        });
    },
    async getGss(){
      this.$store.commit('setFetchingGssData', true);
      await this.$http
        .get('/insites/api/get-global-content-data.json')
        .then(this.bindData)
        .catch(error => {
          console.error(error);
          let msg = 'Failed to get Global Site Settings. Please try again';
          this.$store.commit('setFetchingGssData', true);
          this.setGssLoader('', msg, 'error');
        });
    },
    bindData(res){      
      if (res.data.items.results.length) {
        this.$store.commit('setGssData', res.data.items.results[0]);
        this.checkData();
        this.checkLocNames();
      } else { 
        this.$store.commit('setGssFormMethod', 'POST');
      }

      this.$store.commit('setFetchingGssData', false);
    },
    checkData(){
       if (!this.gssData.company_logo){
        this.gssData.company_logo = {};
        this.gssData.company_logo.original = '';
       }

       if (!this.gssData.company_icon){
       this.gssData.company_icon = {};
       this.gssData.company_icon.original = '';
       }

      this.checkedData = true;
      this.setImages();
    },
    checkLocNames(){
      this.$store.commit('setFetchingGssData', true);
      for (let i = 1; i <= 3; i++){
        this.gssData[`loc_${i}_name`] = this.gssData[`loc_${i}_name`] ?
          this.gssData[`loc_${i}_name`] :
          `Location ${i}`;
      }
      this.$store.commit('setFetchingGssData', false);
    },
    setGssLoader(title, message, icon){
      this.$refs.gssLoader.stateTitle = title;
      this.$refs.gssLoader.stateMessage = message;
      this.$refs.gssLoader.stateIcon = icon;
    },
    setImages() {
      this.s3IconImage = this.$store.state.GssStore.gssData['company_icon'].original;
      this.s3LogoImage = this.$store.state.GssStore.gssData['company_logo'].original;
    }
  },
  created() {
    this.headers = this.apiHeader();
    this.authToken = this.headers["X-CSRF-Token"];     
    if (this.failedToLoadGssData){
      this.getGss();
    }  
  },
  mounted() {       
    // if (this.failedToLoadGssData){
    //   this.getGss();
    // }  
    if (this.fetchingGssData){
      this.setGssLoader('Just a moment','we\'re fetching and processing your data.','processing');
    } else {
      this.checkData();
      this.checkLocNames();
    }
  }
};
</script>

