const FileManagerStore = {
    state: {
        selected_folder: String,
        selected_folder_data: {},
        folders: [],
        selected_file: {
            tags: []
        },
        files: [],
        archived_files: [],
        search_files: [],
        toggleView: 'list',
        searchBy: 'file_name',
        searchKeyword: '',
        listArchive: false,
        details: {},
        children: [],
        assets_url: {},
        files_to_upload: []
    },
    getters: {
        getFileManagerDataStructure(state) {
            return state;
        }
    },
    mutations: {
        updateFileManagerAssetURL(state, new_state) {
            state.assets_url = {};
            state.assets_url = new_state;
        },
        updateFileManagerDataStructureSuccessMutation(state, new_state) {
            state.children = new_state.children;
            state.details = new_state.details;
            state.selected_folder = new_state.details.id;
            state.selected_folder_data.details = new_state.details;
            state.selected_folder_data.children = new_state.children;
        },
        updateFileManagerDataStructureErrorMutation(state, new_state) {
            state.children = new_state.children;
            state.details = new_state.details;
            state.selected_folder = new_state.details.id;
        },
        updateFileManagerSelectedFolder(state, selected_folder) {
            state.selected_folder = selected_folder;
        },
        updateFileManagerSelectedFolderData(state, folder_data) {
            state.selected_folder_data = {};
            state.selected_folder_data = folder_data;
        },
        updateFileManagerFolders(state, folders) {
            state.folders = folders;
        },
        updateFileManagerFiles(state, files) {
            state.listArchive = false;
            state.files = files;
        },
        updateFileManagerArchivedFiles(state, archived_files) {
            state.archived_files = archived_files;
        },
        updateFileManagerSearchResults(state, files) {
            state.search_files = files;
            for (let index in state.search_files) {
                if (state.search_files[index].tags){
                    state.search_files[index].tags = JSON.parse(state.search_files[index].tags);
                } else {
                    state.search_files[index].tags = [];
                }
            }
        },
        updateFileManagerSelectedFile(state, file) {
            state.selected_file = { };
            state.selected_file = file;
            if (state.selected_file.tags) {
                state.selected_file.tags = typeof state.selected_file.tags === "object" 
                    ? state.selected_file.tags
                    : JSON.parse(state.selected_file.tags);
            } else {
                state.selected_file.tags = [];
            }
        },
        updateToggleView(state, toggleView) {
            state.toggleView = toggleView;
        },
        updateSearchKeyword(state, searchKeyword) {
            state.listArchive = false;
            state.searchKeyword = searchKeyword;
        },
        updateSearchBy(state, searchBy) {
            state.searchBy = searchBy
        },
        updateTags(state, tags){
            state.selected_file.tags = tags;
        },
        updateFilesToUpload(state, files_to_upload) {
            state.files_to_upload = [];
            state.files_to_upload = files_to_upload;
            for (let index in state.files_to_upload) {
                state.files_to_upload[index].state = 'pending';
            }
        },
        loadingStateFilesToUpload(state, files_to_upload) {
            state.files_to_upload = files_to_upload;
            for (let index in state.files_to_upload) {
                state.files_to_upload[index].state = 'loading';
            }
        },
        successStateFilesToUpload(state, files_to_upload) {
            state.files_to_upload = {};
            state.files_to_upload = files_to_upload;
        },
        resetFileManagerDetails(state) {
            state.details = {};
        },
        resetFileManagerChildren(state) {
            state.children = [];
        },
        resetFileManagerSelectedFolder(state) {
            state.selected_folder = 'assets';
        },
        resetFileManagerFolders(state) {
            state.folders = [];
        },
        resetFileManagerSelectedFile(state) {
            state.selected_file = { tags: [] };
        },
        resetFileManagerFiles(state) {
            state.listArchive = false;
            state.files = [];
        },
        resetFileManagerArchivedFiles(state) {
            state.archived_files = [];
        },
        resetFileManagerToggleView(state) {
            state.toggleView = 'list';
        },
        resetFilesToUpload(state) {
            state.files_to_upload = [];
        },
        updateListArchiveFiles(state, listArchive) {
            state.listArchive = listArchive;
        }
    },
    actions: {
        resetFMContainer(context) {
            context.commit("resetFileManagerDetails");
            context.commit("resetFileManagerChildren");
            context.commit("resetFileManagerSelectedFolder");
            context.commit("resetFileManagerFolders");
            context.commit("resetFileManagerSelectedFile");
            context.commit("resetFileManagerFiles");
            context.commit("resetFileManagerToggleView");
        },
        resetFMToolbar(context) {
            context.commit("resetFileManagerDetails");
            context.commit("resetFileManagerChildren");
            context.commit("resetFileManagerSelectedFolder");
            context.commit("resetFileManagerFolders");
            context.commit("resetFileManagerSelectedFile");
            context.commit("resetFileManagerFiles");
        },
        resetFMNodeTree(context) {
            context.commit("resetFileManagerSelectedFolder");
            context.commit("resetFileManagerSelectedFile");
            context.commit("resetFileManagerFiles");
        },
        resetFMList(context) {
            context.commit("resetFileManagerSelectedFile");
            context.commit("resetFileManagerFiles");
        },
        updateSelectedFolder(context, data) {
            context.commit("updateFileManagerSelectedFolder", data.details.id);
            context.commit("updateFileManagerSelectedFolderData", data);
        },
        updateRefreshNodeTree(context, data) {
            context.commit('updateFileManagerDataStructureSuccessMutation', data.data_structure);
            context.commit('updateFileManagerFolders', data.folders);
            context.commit('updateFileManagerFiles', data.files);
            context.commit('updateFileManagerAssetURL', data.assets_url);
        }
    }
};

export default FileManagerStore;
